export function renderFormSection(state) {
	const {
		contextId,
		associatedTable,
		associatedSysId,
		associatedRecordModCount,
		formView
	} = state.properties;

	return (
		<div slot="form" className="form-section-container">
			<sn-playbook-form-section
				context-id={contextId}
				associated-table={associatedTable}
				associated-sys-id={associatedSysId}
				form-view={formView}
				associated-record-mod-count={associatedRecordModCount}
			></sn-playbook-form-section>
		</div>
	);
}
