import { get } from 'lodash';

export const getFirstVisibleCardIdFromLane = (
	playbooksByContextId,
	playbookContextId,
	stageName
) => {
	const lanes = get(playbooksByContextId, `${playbookContextId}.lanes`);

	if (!Array.isArray(lanes)) return null;

	const getLaneByName = lanes.find((lane) => lane.name === stageName);

	if (!getLaneByName) return null;

	const card = getLaneByName.cards.find((card) => !card.hidden);

	if (!card || !card.contextId) return null;

	return card.contextId;
};

export const setFocusNavId = (cardId, updateState) => {
	if (cardId)
		updateState({
			focusNavId: cardId
		});
};
