import { createCustomElement } from '@servicenow/ui-core';
import { RECORD_STATUS_CHANGED, RECORD_STATUS_UPDATED } from '../../constants';
import { snabbdom } from '@servicenow/ui-renderer-snabbdom';
import './sn-playbook-form-section-footer';
import {getViewProp} from './features';

const view = (state) => {
	const {
		contextId,
		associatedTable,
		associatedSysId,
		associatedRecordModCount,
		formView,
		dispatchAction
	} = state.properties;

	const mediatorId = `sn-playbook-card-mediator-${contextId}`;
	const actionSelected = `@${mediatorId}/actionSelected`;

	return (
		<now-record-form-section-connected
			table={associatedTable}
			sys-id={associatedSysId}
			{...getViewProp(formView)}
		>
			<sn-playbook-form-section-footer
				slot="footer"
				associated-record-mod-count={associatedRecordModCount}
				action-Selected={actionSelected}
				dispatch-action={dispatchAction}
			/>
		</now-record-form-section-connected>
	);
};

createCustomElement('sn-playbook-form-section', {
	renderer: {
		type: snabbdom,
		view: view
	},
	properties: {
		contextId: {
			default: ''
		},
		associatedTable: {
			default: ''
		},
		associatedSysId: {
			default: ''
		},
		formView: {
			default: ''
		},
		associatedRecordModCount: {
			default: 0
		},
		dispatchAction: {
			default: null
		}
	},
	actionHandlers: {
		[RECORD_STATUS_CHANGED]: ({ action, updateProperties }) => {
			const {
				payload: { status }
			} = action;
			//If the record has been updated, we know that the fields are validated
			//So we can update the property so the footer can dispatch
			//the declarative action with the validated fields
			if (status === RECORD_STATUS_UPDATED)
				updateProperties({ dispatchAction: Date.now() });
		}
	}
});
