import '@servicenow/now-activity-stream';
import '@servicenow/now-modal';
import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
const LIVEUPDATE_KEY = 'liveUpdateData';
const ATTACHMENTS_KEY = 'attachmentsData';

/**
 * @seismicElement now-playbook-activity-stream-modal-connected
 * @summary Component to include the list activities performed over a record displayed inside a now-modal.
 */
export default createCustomElement(
	'now-playbook-activity-stream-modal-connected',
	{
		renderer: {
			type: snabbdom,
			view(state) {
				const { table, sysId, title, modalTitle } = state.properties;

				return (
					<now-modal opened size="lg" header-label={modalTitle}>
						<now-activity-stream-connected
							table={table}
							sys-id={sysId}
							title={title}
						/>
					</now-modal>
				);
			}
		},
		properties: {
			/**
			 * Hoist the component by default.
			 * @type {boolean}
			 */
			nowHoist: { default: true },

			/**
			 * Table name of the record that we want to display the activity stream for.
			 * @type {string}
			 */
			table: {},

			/**
			 * Unique identifier for the record.
			 * @type {string}
			 */
			sysId: {},

			/**
			 * Title to provide to the now-modal wrapper of the activity stream.
			 * Defaults to "Activity"
			 * @type {string}
			 */
			modalTitle: { default: 'Activity' },

			/**
			 * View to use for the record.
			 * @type {string}
			 */
			view: {},

			/**
			 * Array of entries, when provided it will save the first request to the server
			 * Check sampleEntries for format
			 * @type {object[]}
			 */
			entries: {},

			/**
			 * Array of filter options in the shape of {isSelected: true, label: "Filter label", name: "name"}
			 * Object properties:
			 * 		isSelected: controls which filters should appear selected.
			 * 		label: text to display for the filter
			 * 		name: unique name for a filter for internal use
			 * @type {object[]}
			 */
			filterOptions: {},

			/**
			 * When provided, this function will be used to filter out the list of entries based on filterOptions param.
			 * @callback onFilter
			 * @param {object[]} filterOptions, this gives access to the current selections
			 */
			onFilter: {},

			/**
			 * Current timestamp, this is used to know when new data is coming in
			 * @type {number}
			 */
			timestamp: {},

			/**
			 * Specify if the component is active, this can be set to false while you are not presenting the component
			 * to the user, ie.: when tab is not open
			 * @type {boolean}
			 */
			active: {},

			/**
			 * Are you displaying a newly created record?
			 * @type {boolean}
			 */
			isNewRecord: {},

			/**
			 * When provided will receive all clicks from the component
			 * @callback onClickHandler
			 * @type {boolean}
			 */
			onClickHandler: {},
			/**
			 * Title for the activity stream component. Defaults to "Activty Stream" translated.
			 *
			 * @type {string}
			 */
			title: {},
			/**
			 * Object containing journal fields available. For example:
			 *
			 * ```
			 * {
			 *	 "comments": {
			 *		  name : "comments",
			 *		  label : "Additional comments (Customer Visible)",
			 *		  sys_readonly: false,
			 *		  readonly : false,
			 *	 	  visible: true,
			 *		  type: 'journal_input',
			 *		  maxLength: 1000
			 *	 },
			 *	 "work_notes": {
			 *		  name : "work_notes",
			 *		  label : "Work Notes",
			 *		  sys_readonly: false,
			 *		  readonly : false,
			 *		  visible: true,
			 *		  type: 'journal_input',
			 *		  maxLength: 2000
			 *	 }
			 * }
			 *```
			 *
			 * @type {object}
			 */
			fields: {},
			activityStream: {},
			activityStreamReading: {},
			activityStreamError: {},
			[LIVEUPDATE_KEY]: {},
			[ATTACHMENTS_KEY]: {},
			showConversationFilter: {},
			observeTile: {}
		}
	}
);
