import '@servicenow/now-icon';

const view = ({
	properties: {
		size,
		showCompleteIcon,
		isComplete: complete,
		backRingAttributes,
		progressRingAttributes,
		checkmarkAttributes
	}
}) => (
	<div className={ 'size-' + size }>
		{ complete && showCompleteIcon ? (
			<now-icon
				icon="circle-check-fill"
				size={ size }
				className={ 'complete-icon' }
			/>
		) : (
			<svg
				class={{ complete }}
				attrs={{
					xmlns: 'http://www.w3.org/2000/svg',
					viewBox: '0 0 32 32',
					preserveAspectRatio: 'xMidYMin slice'
				}}
			>
				<circle
					class={{ 'back-ring': true, complete }}
					attrs={{ ...backRingAttributes }}
				/>
				<circle
					class={{ 'progress-ring': true, complete }}
					attrs={{ ...progressRingAttributes }}
				/>
				<polyline
					class={{ checkmark: true, complete }}
					attrs={{ ...checkmarkAttributes }}
				/>
			</svg>
		)}
	</div>
);

export default view;
