import { CLASSIC_FORM_EMULATED_WORKSPACE_CONFIG_ID } from '../../../constants';
import {
	renderAttachments,
	renderChecklist,
	renderFieldList,
	renderFormFieldList,
	renderRichText
} from '../../render-utils';
import { getViewProp } from '../../sn-playbook-form-section/features';
import '../../sn-playbook-form-section/sn-playbook-form-section-scripted';

export const view = (state) => {
	const {
		workspaceConfigId,
		properties: {
			description,
			playbookCardProperties,
			contextId,
			associatedTable,
			associatedSysId,
			formView,
			formFields,
			associatedRecordModCount
		},
		componentId
	} = state;

	const mediatorComponentId = `sn-playbook-card-mediator-${contextId}-${componentId}`;

	const showFormSection =
		associatedTable !== '' &&
		associatedSysId !== '' &&
		(formView !== '' || formFields.length);

	return (
		<sn-playbook-card
			{...playbookCardProperties}
			showFormSection={showFormSection}
			mediatorComponentId={mediatorComponentId}
		>
			{renderRichText(description)}
			{renderFieldList(state, 'content', 'record_fields')}
			{renderChecklist(state, 'content')}

			{showFormSection ? (
				<div slot="form" className="form-section-container">
					<sn-playbook-form-section-scripted
						workspaceConfigId={
							workspaceConfigId ||
							CLASSIC_FORM_EMULATED_WORKSPACE_CONFIG_ID
						}
						context-id={contextId}
						table={associatedTable}
						sys-id={associatedSysId}
						{...getViewProp(formView)}
						field-whitelist={formFields}
						associated-record-mod-count={associatedRecordModCount}
						mediatorComponentId={mediatorComponentId}
					/>
				</div>
			) : null}

			{renderFormFieldList(state, 'form-field-stacked')}
			{renderAttachments(state, 'below-form')}
		</sn-playbook-card>
	);
};
