function createDebounce(fn, opts) {
	const { delay = 0, maxWait = null, aggregate = null } = opts;
	const validAggregate = typeof aggregate === 'function';
	const validMaxWait = typeof maxWait === 'number' && maxWait > delay;

	let maxTid = null;
	let tid = null;
	let finalArgs = {};

	// Saving each new 'args' as 'finalArgs' and keeping 'finishDebounce'
	// outside of the returned 'debouncedEffect' function from below so
	// that we don't have to recreate a 'finishDebounce' callback function
	// every time 'debouncedEffect' is invoked
	const finishDebounce = () => {
		clearTimeout(maxTid);
		clearTimeout(tid);
		fn(...finalArgs);
		maxTid = null;
		finalArgs = {};
	};

	return function componentBoundDebouncedEffect(args) {
		clearTimeout(tid);
		tid = setTimeout(finishDebounce, delay);

		if (validMaxWait && maxTid === null)
			maxTid = setTimeout(finishDebounce, maxWait);

		finalArgs = validAggregate ? aggregate(finalArgs, args) : args;
	};
}

/*
	This is a modified version of the Seismic debounce modifier
	https://code.devsnc.com/dev/sn-seismic/blob/master/packages/core/src/effects/modifiers/debounce.js

	TODO: Uptake Seismic debounce modifier once they've merged our PR with our enhancements
*/
export default function aggregateDebounce(fn, opts) {
	const debounceMap = new WeakMap();

	// The debouncedEffect closure is created and shared by all instances of a particular component definition
	return function debouncedEffect(...args) {
		const idx = args.length - 1 || 0;
		const coeffects = args[idx];
		const { host } = coeffects;

		if (!host) return;

		if (!debounceMap.has(host)) {
			debounceMap.set(host, createDebounce(fn, opts));
		}

		const componentBoundDebounce = debounceMap.get(host);
		componentBoundDebounce(args);
	};
}
