import '@servicenow/now-collapse';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { get } from 'lodash';
import { t } from 'sn-translate'; //'@servicenow/library-translate';
import {
	PANEL_NAVIGATION_FOCUS_ANCHOR_NAV_ID,
	PANEL_NAVIGATION_SET_FOCUS_ANCHOR,
	PANEL_NAVIGATION_UPDATE_MAP
} from '../behaviors/sn-panel-navigation/constants';
import EmptyState from '../components/empty-state';
import PlaybookHeading from '../components/playbook-heading';
import StageItem from '../components/stage-item';
import {
	EVENT_KEY,
	PLAYBOOK_STAGE_CLICKED,
	PLAYBOOK_STAGE_DEFAULT_TITLE,
	PLAYBOOK_STAGE_LIST_EXPANDED_SET,
	PLAYBOOK_STAGE_MODEL_ID,
	PLAYBOOK_STAGE_SELECTOR_SOURCE
} from '../constants';
import { NOTIFICATION_NO_PLAYBOOKS_AVAILABLE } from '../notification.constants';

const getVisibleCards = (items) => {
	const cards = items.filter((card) => !card.hidden);

	return cards.length
		? {
			cards,
			firstCardLaneName: cards[0].laneName,
			visibleCardCount: cards.length
		  }
		: {
			cards: null,
			firstCardLaneName: null,
			visibleCardCount: 0
		  };
};

const view = (
	{
		properties: {
			playbookContextIdsInOrder,
			playbooksByContextId,
			selectedStage,
			playbookExperienceId,
			compactMode,
			defaultExpandedPlaybook,
			parentRecordUpdateTime
		},
		expandedStageListsByContextId,
		playbookActionsDropdownOpened,
		openedActionsDropdownByStageName
	},
	{ dispatch }
) => {
	let selectedFilterLaneName = null;
	let previousHeadingNavKey = '';

	const activateStage = (stageName, playbookContextId) => {
		dispatch(PLAYBOOK_STAGE_CLICKED, {
			stageName,
			playbookContextId
		});
	};

	const disableForCompactModeStageSelected =
		compactMode &&
		selectedStage.playbookContextId &&
		selectedStage.stageName;

	if (playbookContextIdsInOrder.length < 1)
		return (
			<EmptyState
				title={NOTIFICATION_NO_PLAYBOOKS_AVAILABLE.header}
				text={NOTIFICATION_NO_PLAYBOOKS_AVAILABLE.body}
				illustration={NOTIFICATION_NO_PLAYBOOKS_AVAILABLE.illustration}
			/>
		);

	return (
		<div
			class={{
				'now-playbook-stage-selector': true,
				'disable-stage-selector': disableForCompactModeStageSelected
			}}
			role="list"
		>
			<div
				contenteditable={true}
				className="focus-anchor"
				tabindex={-1}
				hook-insert={({ elm }) =>
					dispatch(PANEL_NAVIGATION_SET_FOCUS_ANCHOR, {
						focusAnchorRef: elm,
						source: PLAYBOOK_STAGE_SELECTOR_SOURCE
					})
				}
				data-nav={PANEL_NAVIGATION_FOCUS_ANCHOR_NAV_ID}
			/>

			{playbookContextIdsInOrder.map((contextId) => {
				const playbook = playbooksByContextId[contextId];
				const headingNavKey = `pb-heading-${contextId}`;
				const {
					title = '',
					parentTable,
					parentRecord,
					state,
					filterOpened = false,
					filterData = [],
					selectedFilters,
					selectedFilterCount = 0,
					sysId,
					lanes,
					cancellationReason,
					canceledBy
				} = playbook;
				let previousNavKey = '';

				const expandStageList = get(
					expandedStageListsByContextId,
					contextId,
					contextId === defaultExpandedPlaybook
				);
				const playbookJSX = (
					<Fragment>
						<span className={'now-m-block-start--xl'} />
						<PlaybookHeading
							title={title}
							parentTable={parentTable}
							parentRecord={parentRecord}
							playbookExperienceId={playbookExperienceId}
							playbookContextId={sysId}
							state={state}
							filterOpened={filterOpened}
							filterData={filterData}
							selectedFilterCount={selectedFilterCount}
							selectedFilters={selectedFilters}
							expandStageList={expandStageList}
							cancellationReason={cancellationReason}
							canceledBy={canceledBy}
							actionsDropdownOpened={
								playbookActionsDropdownOpened
							}
							navKey={headingNavKey}
							previousNavKey={previousHeadingNavKey}
							onExpandOrCollapseClicked={() => {
								dispatch(PANEL_NAVIGATION_UPDATE_MAP, {
									mapKey: headingNavKey,
									mapEntry: {
										right: expandStageList
											? `pb-actions-${contextId}`
											: `pb-filter-${contextId}`
									}
								});
								dispatch(PLAYBOOK_STAGE_LIST_EXPANDED_SET, {
									playbookContextId: contextId,
									updatedExpandState: !expandStageList
								});
							}}
							dispatch={dispatch}
							parentRecordUpdateTime={parentRecordUpdateTime}
						/>

						<span className={'now-m-block-start--xs'} />
						<now-collapse
							expanded={expandStageList}
							id={`collapse-playbook-stage-list-${contextId}`}
							key={`playbook-stage-list-${contextId}`}
							config-aria={{
								role: 'group',
								'aria-label': t('{0} Playbook Stages', title),
								'aria-expanded': expandStageList
							}}
						>
							{lanes.map((stage) => {
								const {
									title: stageTitle,
									name,
									stats: {
										totalItems,
										completedItems,
										readyItems
									},
									state: stageState,
									cards
								} = stage;

								if (!(cards && cards.length)) return null;

								const navKey = `stage-item-${contextId}.${name}`;

								let filteredCardsCount = null;
								let isDisabled = false;

								if (selectedFilterCount) {
									const {
										firstCardLaneName,
										visibleCardCount
									} = getVisibleCards(cards);

									filteredCardsCount = visibleCardCount;

									if (visibleCardCount) {
										if (selectedFilterLaneName === null) {
											selectedFilterLaneName = firstCardLaneName;

											if (
												filterOpened &&
												!compactMode &&
												selectedStage.stageName !==
													firstCardLaneName
											)
												activateStage(
													firstCardLaneName,
													sysId
												);
										}
									} else isDisabled = true;
								}

								if (totalItems === 0) isDisabled = true;

								const sanitizedName = name.replace(/\s+/g, '-');
								const declarativeActionsDataProviderId = `playbook-stage-actions-data-provider-${contextId}-${sanitizedName}`;
								const onStageKeyDown = (e) => {
									switch (e.key) {
										case EVENT_KEY.ENTER:
										case EVENT_KEY.SPACE:
											e.stopPropagation();
											activateStage(name, contextId);
											//dispatch(PANEL_NAVIGATION_SWITCH_FOCUS, { source: PLAYBOOK_STAGE_SELECTOR_SOURCE}); -> needs some work
											break;
										default:
											return;
									}
								};

								const stageJSX = (
									<sn-playbook-declarative-actions-data-provider
										component-id={
											declarativeActionsDataProviderId
										}
										associatedTable={parentTable}
										associatedSysId={parentRecord}
										playbookExperienceId={
											playbookExperienceId
										}
										modelId={PLAYBOOK_STAGE_MODEL_ID}
										parentRecordUpdateTime={
											parentRecordUpdateTime
										}
									>
										<sn-declarative-mediator>
											<StageItem
												parentTable={parentTable}
												parentRecord={parentRecord}
												playbookExperienceId={
													playbookExperienceId
												}
												playbookContextId={contextId}
												playbookState={state}
												stageState={stageState}
												progress={
													totalItems
														? completedItems /
														  totalItems
														: 0
												}
												title={
													stageTitle
														? stageTitle
														: PLAYBOOK_STAGE_DEFAULT_TITLE
												}
												name={name}
												completeActivities={
													completedItems
												}
												totalActivities={totalItems}
												readyActivities={readyItems}
												selected={
													selectedStage.playbookContextId ==
														sysId &&
													selectedStage.stageName ==
														name
												}
												disabled={isDisabled}
												actions={`@${declarativeActionsDataProviderId}/declarativeActions`}
												filteredCardCount={
													filteredCardsCount
												}
												previousNavKey={previousNavKey}
												navKey={navKey}
												onStageClicked={() =>
													activateStage(
														name,
														contextId
													)
												}
												onStageKeyDown={onStageKeyDown}
												actionsDropdownOpened={
													openedActionsDropdownByStageName ===
													name
												}
												dispatch={dispatch}
											/>
										</sn-declarative-mediator>
									</sn-playbook-declarative-actions-data-provider>
								);

								if (!isDisabled) previousNavKey = navKey;

								return stageJSX;
							})}
						</now-collapse>
					</Fragment>
				);

				previousHeadingNavKey = headingNavKey;

				return playbookJSX;
			})}
		</div>
	);
};

export default view;
