import { get } from 'lodash';
import { createGraphQLEffect } from '@seismic/effect-graphql';
import { actionTypes } from '@servicenow/ui-core';
import {
	PLAYBOOK_UPDATE_CREATE_RECORD_FLOW,
	PLAYBOOK_UPDATE_CREATE_RECORD_FLOW_SUCCEEDED,
	PLAYBOOK_UPDATE_CREATE_RECORD_FLOW_FAILED,
	PLAYBOOK_FETCH_TABLE_FROM_FLOW,
	PLAYBOOK_FETCH_TABLE_FROM_FLOW_SUCCEEDED,
	PLAYBOOK_FETCH_TABLE_FROM_FLOW_FAILED,
	RECORD_STATUS_CHANGED,
	RECORD_STATUS_INSERTED,
	PLAYBOOK_SUFFICIENT_DATA
} from '../../../constants';
import { NOTIFICATION_NETWORK_ERROR } from '../../../notification.constants';

const {
	COMPONENT_PROPERTY_CHANGED,
	COMPONENT_BOOTSTRAPPED,
	COMPONENT_CONNECTED
} = actionTypes;

const initiateGetTableEffect = ({ action, dispatch, properties }) => {
	const {
		activityProperties: { associated_record = {} }
	} = properties;
	if (associated_record.value)
		dispatch(PLAYBOOK_FETCH_TABLE_FROM_FLOW, {
			sysId: associated_record.value
		});
};

const getTableFromFlowQuery = `
	query($sysId: String!) {
		GlideRecord_Query {
			sys_flow_data (sys_id: $sysId) {
				_results {
					vars {
	        			variables {
	          				variableName
	          				variableValue
	        			}
	      			}
				}
			}
		}
	}
`;

const getTableFromFlowEffect = createGraphQLEffect(getTableFromFlowQuery, {
	variableList: ['sysId'],
	successActionType: PLAYBOOK_FETCH_TABLE_FROM_FLOW_SUCCEEDED,
	errorActionType: PLAYBOOK_FETCH_TABLE_FROM_FLOW_FAILED
});

const getTableFromFlowSucceededEffect = ({ action, updateState }) => {
	const sysFlowData = get(
		action,
		'payload.data.GlideRecord_Query.sys_flow_data._results[0]',
		{}
	);

	const {
		vars: { variables }
	} = sysFlowData;
	variables.forEach((element) => {
		if (element.variableName === 'table')
			updateState({ table: element.variableValue });
	});
};

const getTableFromFlowFailedEffect = ({ updateState }) => {
	updateState({ notification: NOTIFICATION_NETWORK_ERROR });
};

const updateCreateRecordFlowQuery = `
	mutation($sysId: String!) {
		GlideRecord_Mutation {
			update_sys_flow_data(sys_id: $sysId, state: "COMPLETE", vars: [
				{variableName: "record_created", variableValue: "{{recordCreated}}" },
				{variableName: "table", variableValue: "{{table}}" }
			]) {
      			state {
      				value
      				displayValue
      			}
      			vars {
        			variables {
          				variableName
          				variableValue
        			}
      			}
      		}
		}
	}
`;

const updateCreateRecordFlowEffect = createGraphQLEffect(
	updateCreateRecordFlowQuery,
	{
		templateVarList: ['recordCreated', 'table'],
		variableList: ['sysId'],
		successActionType: PLAYBOOK_UPDATE_CREATE_RECORD_FLOW_SUCCEEDED,
		errorActionType: PLAYBOOK_UPDATE_CREATE_RECORD_FLOW_FAILED
	}
);

const updateCreateRecordFlowSucceededEffect = () => {
	//DO NOTHING, Playbook will update card with new data from activity context
};

const updateCreateRecordFlowFailedEffect = ({ updateState }) => {
	updateState({ notification: NOTIFICATION_NETWORK_ERROR });
};

const recordCreatedEffect = ({ action, state, dispatch }) => {
	const {
		properties: {
			activityProperties: { associated_record }
		}
	} = state;
	const {
		payload: { status, table, sys_id }
	} = action;

	if (status === RECORD_STATUS_INSERTED) {
		dispatch(PLAYBOOK_UPDATE_CREATE_RECORD_FLOW, {
			sysId: associated_record && associated_record.value,
			table: table,
			recordCreated: sys_id
		});
	}
};

const hasSufficientData = (properties) => {
	const { activityProperties } = properties;

	const {
		associated_table = {},
		associated_record = {}
	} = activityProperties;

	// A card must have an associated record and table
	if (associated_table.value && associated_record.value) return true;

	// No data to show
	return false;
};

export const actionHandlers = {
	[PLAYBOOK_FETCH_TABLE_FROM_FLOW]: getTableFromFlowEffect,
	[PLAYBOOK_FETCH_TABLE_FROM_FLOW_SUCCEEDED]: getTableFromFlowSucceededEffect,
	[PLAYBOOK_FETCH_TABLE_FROM_FLOW_FAILED]: getTableFromFlowFailedEffect,
	[PLAYBOOK_UPDATE_CREATE_RECORD_FLOW]: updateCreateRecordFlowEffect,
	[PLAYBOOK_UPDATE_CREATE_RECORD_FLOW_SUCCEEDED]: updateCreateRecordFlowSucceededEffect,
	[PLAYBOOK_UPDATE_CREATE_RECORD_FLOW_FAILED]: updateCreateRecordFlowFailedEffect,
	[RECORD_STATUS_CHANGED]: recordCreatedEffect,
	[COMPONENT_BOOTSTRAPPED]: initiateGetTableEffect,
	[COMPONENT_CONNECTED]: ({ properties, dispatch }) => {
		dispatch(PLAYBOOK_SUFFICIENT_DATA, {
			hasSufficientData: hasSufficientData(properties)
		});
	},
	[COMPONENT_PROPERTY_CHANGED]: ({ properties, action, dispatch }) => {
		const {
			payload: { name }
		} = action;
		if (name === 'activityProperties' || name === 'activityState') {
			dispatch(PLAYBOOK_SUFFICIENT_DATA, {
				hasSufficientData: hasSufficientData(properties)
			});
			initiateGetTableEffect({ action, dispatch, properties });
		}
	}
};
