import {
	NOW_DROPDOWN_OPENED_SET,
	PLAYBOOK_CARD_VIEW_MODE_CHANGE
} from '../../../../constants';

export const cardActionHandlers = {
	[PLAYBOOK_CARD_VIEW_MODE_CHANGE]: ({
		action: {
			payload: { expanded }
		},
		updateState
	}) => {
		updateState({
			expanded
		});
	},
	[NOW_DROPDOWN_OPENED_SET]: {
		effect: ({ updateState, action: { payload } }) => {
			updateState({
				headerActionsDropdownOpened: payload.value
			});
		},
		stopPropagation: true
	}
};
