import { createAmbSubscriptionEffect } from '@servicenow/ui-effect-amb';
import { get } from 'lodash';
import {
	AMB_CHANNEL_ID,
	CARD_STATE_VALUES,
	LANE_STATE_VALUES,
	PLAYBOOK_CHANNEL_UPDATE_RECEIVED,
	PLAYBOOK_REQUEST_FETCH_ACTIVITY_CONTEXTS,
	PLAYBOOK_REQUEST_FETCH_PLAYBOOKS,
	PLAYBOOK_STATE_VALUES,
	PLAYBOOK_SUBSCRIBE
} from '../../constants';

export const ambPlaybookHandlers = {
	[PLAYBOOK_SUBSCRIBE]: createAmbSubscriptionEffect(AMB_CHANNEL_ID, {
		messageReceivedActionType: PLAYBOOK_CHANNEL_UPDATE_RECEIVED,
		encodeURIComponentForChannelId: false
	}),
	[PLAYBOOK_CHANNEL_UPDATE_RECEIVED]: ({
		action,
		dispatch,
		updateProperties
	}) => {
		updateProperties(({ properties }) => {
			const playbookContext = get(action, 'payload.data.PDContext', {});

			const {
				id: playbookContextId,
				LaneContexts: laneContexts
			} = playbookContext;

			const playbook = properties.playbooksByContextId[playbookContextId];

			const { playbookExperienceId } = properties;

			if (!playbook) {
				dispatch(PLAYBOOK_REQUEST_FETCH_PLAYBOOKS, {
					sysIds: [playbookContextId],
					playbookExperienceId
				});

				return {
					shouldUpdate: false
				};
			}

			const currentPlaybookState = playbook.state || {};
			const nextPlaybookState = playbookContext.state || {};

			let nextPlaybook = null;

			if (
				nextPlaybookState.value &&
				nextPlaybookState.value !== currentPlaybookState.value
			) {
				nextPlaybook = {
					...playbook,
					state: nextPlaybookState
				};
			}

			let activityContexts = [];

			if (
				isPlaybookBeingCancelled(
					currentPlaybookState,
					nextPlaybookState
				)
			)
				activityContexts = getPendingActivityContextIds(playbook);

			if (laneContexts && laneContexts.length) {
				laneContexts.forEach((newLane) => {
					const laneIdx = playbook.lanes.findIndex(
						({ name }) => name === newLane.name
					);

					if (laneIdx === -1) return { shouldUpdate: false };

					const currentLaneState = get(
						playbook,
						`lanes[${laneIdx}].state`,
						{}
					);

					const nextLaneState = newLane.state || {};

					if (
						nextLaneState.value &&
						nextLaneState.value !== currentLaneState.value
					) {
						if (nextPlaybook === null)
							nextPlaybook = { ...playbook };

						const newLanes = [...nextPlaybook.lanes];

						newLanes[laneIdx] = {
							...nextPlaybook.lanes[laneIdx],
							state: nextLaneState
						};

						if (
							nextLaneState.value ===
							LANE_STATE_VALUES.IN_PROGRESS.value
						)
							newLanes[laneIdx].expanded = true;

						nextPlaybook.lanes = newLanes;
					}

					if (Array.isArray(newLane.ActivityContexts))
						activityContexts = [
							...activityContexts,
							...newLane.ActivityContexts
						];
				});
			}

			if (activityContexts.length)
				dispatch(PLAYBOOK_REQUEST_FETCH_ACTIVITY_CONTEXTS, {
					contextIds: activityContexts.map((ac) => ac.id)
				});

			return {
				path: `playbooksByContextId.${playbookContextId}`,
				operation: 'set',
				value: nextPlaybook,
				shouldUpdate: nextPlaybook !== null
			};
		});
	}
};

const getPendingActivityContextIds = (playbook) => {
	let activityContextIds = [];
	playbook.lanes.forEach((lane) => {
		lane.cards.forEach((card) => {
			if (card.state.value === CARD_STATE_VALUES.PENDING.value)
				activityContextIds.push({
					id: card.contextId
				});
		});
	});

	return activityContextIds;
};

export const isPlaybookBeingCancelled = (
	currentPlaybookState,
	nextPlaybookState
) => {
	if (currentPlaybookState.value === PLAYBOOK_STATE_VALUES.CANCELLED)
		return false;

	if (
		nextPlaybookState.value === PLAYBOOK_STATE_VALUES.CANCELLED ||
		nextPlaybookState.value === PLAYBOOK_STATE_VALUES.PENDING_CANCEL
	)
		return true;

	return false;
};
