import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { get } from 'lodash';
import 'sn-component-checklist';
import { CARD_PROPS_TO_QUERY_MAP } from '../../constants';
import { customCreateElement } from './customCreateElement';

const normalizeCardProps = (item) => {
	return Object.entries(CARD_PROPS_TO_QUERY_MAP).reduce(
		(properties, [cardProp, queryProp]) => {
			if (item.hasOwnProperty(queryProp))
				properties[cardProp] = item[queryProp];
			return properties;
		},
		{}
	);
};

const transformAuthorizedActions = (authorizedActions = []) => {
	return authorizedActions.reduce((accu, action) => {
		if (action && typeof action === 'string') accu[`can_${action}`] = true;
		return accu;
	}, {});
};

// mapModelFieldValues - Produces are key value pair, where value is always a string
// if an activityType of fdbeceb95b61001070e4492c11f91a28(list) is found with the key
// of table then rename the key list_table
const mapModelFieldValues = (item) => {
	const listActivityTypeValue = 'fdbeceb95b61001070e4492c11f91a28';
	return Object.keys(item.activityProperties).reduce((result, currentKey) => {
		let value = get(item, `activityProperties[${currentKey}].value`);

		if (value === null) value = '';

		if (
			item.activityType.value === listActivityTypeValue &&
			currentKey === 'table'
		)
			return { ...result, list_table: value };

		return { ...result, [currentKey]: value };
	}, {});
};

const playbookCard = ({
	item,
	userId,
	loadingActivities,
	expandOnMountOrChange,
	nowMode,
	playbookState,
	playbookContextId,
	dataProviderComponentId,
	parentRecordUpdateTime
}) => {
	// TODO: include declarative actions and declarative model in GraphQL Schema instead of client side
	const declarativeActions = `@${dataProviderComponentId}/declarativeActions`;

	const declarativeModelFields = mapModelFieldValues(item);

	const authorizedActions = transformAuthorizedActions(
		item.authorizedActions
	);

	const declarativeModel =
		(item &&
			item.state &&
			item.activityType &&
			item.laneContext &&
			item.laneContext.state && {
			...declarativeModelFields,
			...authorizedActions,
			state: item.state.value,
			type: item.activityType.value,
			sysId: item.associatedRecord,
			table: item.associatedTable,
			parentTable: item.parentTable,
			parentSysId: item.parentRecord,
			contextId: item.contextId,
			stage_name: item.laneContext.name,
			stage_title: item.laneContext.title,
			stage_status: item.laneContext.state.value,
			stage_order: item.laneContext.order,
			playbook_status: playbookState,
			playbook_context_id: playbookContextId
		}) ||
		{};

	const normalizedProps = normalizeCardProps({
		...item,
		declarativeActions,
		declarativeModel
	});

	const props = {
		...normalizedProps,
		loading: loadingActivities.hasOwnProperty(item.contextId),
		appendToPayload: { contextId: item.contextId },
		userId,
		expandOnMountOrChange,
		nowMode,
		activityLabel: item.title
	};

	return (
		<Fragment>
			<sn-playbook-declarative-actions-data-provider
				associatedTable={item.associatedTable}
				associatedSysId={item.associatedRecord}
				parentTable={item.parentTable}
				parentSysId={item.parentRecord}
				view={item.view}
				component-id={dataProviderComponentId}
				associatedRecordModCount={item.associatedRecordModCount}
				activityProperties={item.activityProperties}
				playbookExperienceId={item.playbookExperienceId}
				modelId={item.modelId}
				activityDefinitionId={item.activityDefinitionId}
				parentRecordUpdateTime={parentRecordUpdateTime}
			/>
			{customCreateElement(item.cardRendererTag, { ...props })}
		</Fragment>
	);
};

export default playbookCard;
