import '@devsnc/sn-declarative-actions';
import '@servicenow/now-avatar';
import '@servicenow/now-collapse';
import '@servicenow/now-dropdown';
import '@servicenow/now-highlighted-value';
import '@servicenow/now-icon';
import '@servicenow/now-rich-text';
import '@servicenow/now-tooltip';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate'; //'@servicenow/library-translate';
import { PANEL_NAVIGATION_UPDATE_MAP } from '../../../behaviors/sn-panel-navigation/constants';
import EmptyState from '../../../components/empty-state';
import '../../../components/sn-playbook-card-header-actions';
import '../../../components/sn-playbook-card-loader';
import {
	CARD_STATE_VALUES,
	EVENT_KEY,
	NOW_MODAL_OPENED_SET,
	PLAYBOOK_CARD_COLLAPSE_TOOLTIP,
	PLAYBOOK_CARD_EXPAND_TOOLTIP,
	PLAYBOOK_CARD_PRIORITIZED_COLLAPSE_TOOLTIP,
	PLAYBOOK_CARD_PRIORITIZED_EXPAND_TOOLTIP,
	PLAYBOOK_CARD_PENDING_TOOLTIP,
	PLAYBOOK_CARD_ERROR_TOOLTIP,
	PLAYBOOK_CARD_VIEW_MODE_CHANGE,
	PLAYBOOK_STANDARD_TOOLTIP_DELAY
} from '../../../constants';
import { NOTIFICATION_PLAYBOOK_CARD_ERROR } from '../../../notification.constants';
import { renderPlaybookFooter, showFormSection } from '../../render-utils';
import { HeaderActivityState } from './subcomponents';

const getEmptyStateData = (state) => {
	const {
		properties: {
			activityState,
			pendingTitle,
			pendingDescription, //cancelledTitle, cancelledDescription,
			notification,
			hasSufficientData
		}
	} = state;

	switch (activityState.value) {
		case CARD_STATE_VALUES.PENDING.value:
			return { title: pendingTitle, text: pendingDescription };
		default:
			if (
				activityState.value !== CARD_STATE_VALUES.COMPLETE.value &&
				!hasSufficientData
			) {
				return { title: pendingTitle, text: pendingDescription };
			} else {
				return notification.header || notification.body
					? { title: notification.header, text: notification.body }
					: null;
			}
	}
};

const renderModal = (state) => {
	const {
		properties: { declarativeModel, formFieldItems = [] },
		modalProperties: { modalOpen, modalAction = {}, modalLinkText = {} }
	} = state;

	let modalFooterActions = [
		{
			variant: 'secondary',
			label: t('Cancel'),
			clickActionType: NOW_MODAL_OPENED_SET,
			id: `${NOW_MODAL_OPENED_SET}_FALSE`
		}
	];

	if (modalAction) modalFooterActions.push(modalAction);

	return (
		<div
			className="playbook-card-modal"
			on-click={(e) => e.stopPropagation()}
		>
			<now-modal
				opened={modalOpen}
				size="lg"
				header-label={modalAction.label || ''}
				footer-actions={modalFooterActions}
				append-to-payload={{
					model: { ...declarativeModel, fields: formFieldItems },
					modalSubmit: true
				}}
			>
				{modalLinkText.label && (
					<header>
						<now-text-link
							label={modalLinkText.label}
							variant="secondary"
							href="javascript:void(0)"
							append-to-payload={{
								table: declarativeModel.table,
								sys_id: declarativeModel.sysId,
								recordLink: true
							}}
						/>
					</header>
				)}
				<slot name="form" />
			</now-modal>
		</div>
	);
};

const renderForm = (state) => {
	const {
		formFieldsInModal,
		formFieldsRequired,
		properties: { formFieldItems = [] }
	} = state;
	// Note if formFieldsRequired is falsey, no elements with attribute
	// slot='form' will render, meaning the 'renderForms' method will not be
	// able to slot anything onto the page
	if (formFieldsRequired === true) {
		return formFieldsInModal ? renderModal(state) : <slot name="form" />;
	} else {
		// We will still show form fields if no Declarative Actions require them.
		// We show them as 'now-label-value-stacked' in a custom slot
		// 'form-field-stacked' to differentiate them from editable form fields.
		// This behavior is needed so not to throw users off when they see
		// 'form_fields' in the Activity Context record but don't see them
		// in the Playbook Card.
		return formFieldItems.length ? (
			<slot name="form-field-stacked" />
		) : null;
	}
};

export const view = (state, { dispatch }) => {
	const {
		properties: {
			contextId,
			tagline,
			heading,
			caption,
			buttonActions,
			headerIconActions,
			headerDropdownActions,
			associatedSysId,
			declarativeModel,
			activityState,
			configProperties: {
				maxFormFieldsOnCard,
				showActivityState,
				slaConfiguration
			},
			showSla,
			loading,
			prioritized,
			formFieldItems = [],
			cardFooter,
			assignedTo,
			userId,
			showFormSection: showFormSectionProperty,
			mediatorComponentId,
			pendingTitle
		},
		expanded,
		headerActionsDropdownOpened,
		componentId
	} = state;

	const emptyState = getEmptyStateData(state);
	const statePendingOrError =
		activityState.value === CARD_STATE_VALUES.PENDING.value ||
		activityState.value === CARD_STATE_VALUES.ERROR.value;
	const stateCancelled =
		activityState.value === CARD_STATE_VALUES.CANCELLED.value;
	const collapseSectionId = `collapse-section-${contextId}-${componentId}`;

	if (statePendingOrError && expanded)
		dispatch(PLAYBOOK_CARD_VIEW_MODE_CHANGE, { expanded: false });

	const showFormSectionValue =
		showFormSectionProperty !== undefined
			? showFormSectionProperty
			: showFormSection(declarativeModel);

	const isPrioritized =
		prioritized &&
		!statePendingOrError &&
		!stateCancelled &&
		activityState.value !== CARD_STATE_VALUES.SKIPPED.value;

	return (
		<sn-declarative-mediator key={`playbook-card-${contextId}`}>
			<sn-playbook-card-mediator
				component-id={mediatorComponentId}
				show-form-section={showFormSectionValue}
			>
				<div
					class={{
						'playbook-card': true,
						'is-complete':
							activityState.value ===
							CARD_STATE_VALUES.COMPLETE.value,
						'is-skipped':
							activityState.value ===
							CARD_STATE_VALUES.SKIPPED.value,
						'is-cancelled': stateCancelled,
						'is-pending-or-error': statePendingOrError,
						'is-prioritized': isPrioritized,
						'is-expanded': expanded
					}}
					role="listitem"
					aria-label={t(
						'{0} activity card is {1} in {2} state',
						heading.label,
						prioritized ? t('prioritized') : t('not prioritized'),
						expanded ? t('expanded') : t('collapsed')
					)}
					aria-expanded={expanded}
				>
					<now-collapse-trigger
						controls={
							statePendingOrError ? null : collapseSectionId
						}
						class={{
							'card-header': true
						}}
						on-click={(e) => {
							e.stopPropagation();
							if (!statePendingOrError)
								dispatch(PLAYBOOK_CARD_VIEW_MODE_CHANGE, {
									expanded: !expanded
								});
						}}
						on-keydown={(e) => {
							if (
								statePendingOrError &&
								(e.key === EVENT_KEY.ENTER ||
									e.key === EVENT_KEY.SPACE)
							)
								e.stopPropagation();
						}}
						tabindex={0}
						data-nav={`card-header-${contextId}`}
						hook-insert={({ elm }) =>
							elm
								? dispatch(PANEL_NAVIGATION_UPDATE_MAP, {
									mapKey: `card-header-${contextId}`,
									mapEntry: { domRef: elm }
								  })
								: null
						}
						config-aria={{
							'aria-controls': collapseSectionId
						}}
					>
						<HeaderActivityState
							activityState={activityState}
							showActivityState={showActivityState}
						/>

						<span className={'now-m-inline-start--xl'} />
						<label className={'stage-type'}>{tagline.label}</label>

						<sn-playbook-card-header-actions
							class={{
								'header-dropdown': true,
								show: expanded || headerActionsDropdownOpened
							}}
							associatedSysId={associatedSysId}
							headerIconActions={headerIconActions}
							headerDropdownActions={headerDropdownActions}
							declarativeModel={declarativeModel}
							formFieldItems={formFieldItems}
							showSla={showSla}
							slaConfiguration={slaConfiguration}
							on-click={(e) => e.stopPropagation()}
							on-keydown={(e) => {
								if (
									e.key === EVENT_KEY.ENTER ||
									e.key === EVENT_KEY.SPACE
								)
									e.stopPropagation();
							}}
						/>

						{statePendingOrError ? null : (
							<now-icon
								icon="chevron-down-outline"
								size="sm"
								class={{
									'chevron-icon': true,
									'is-expanded': expanded
								}}
							/>
						)}

						<span className={'now-m-block-start--xs'} />
						<div className={'card-title-container'}>
							<now-icon
								icon="presence-fill"
								size="sm"
								class={{
									'prioritized-icon': true,
									'show-icon':
										isPrioritized &&
										!expanded &&
										heading.label
								}}
								on-mouseover={(e) => e.stopPropagation()}
							/>

							<h4 className={'card-title'}>
								{activityState.value ===
								CARD_STATE_VALUES.ERROR.value
									? NOTIFICATION_PLAYBOOK_CARD_ERROR.header
									: activityState.value ===
											CARD_STATE_VALUES.PENDING.value &&
									  pendingTitle
										? pendingTitle
										: heading.label}
							</h4>
						</div>

						{statePendingOrError ? (
							<now-rich-text
								class={{
									'empty-state-text': true,
									'now-m-block-start--md':
										activityState.value !==
											CARD_STATE_VALUES.ERROR.value &&
										!(
											emptyState &&
											emptyState.text &&
											emptyState.text.startsWith('<p>')
										)
								}}
								html={
									activityState.value ===
									CARD_STATE_VALUES.ERROR.value
										? NOTIFICATION_PLAYBOOK_CARD_ERROR.body
										: emptyState.text
								}
							/>
						) : assignedTo ? (
							<Fragment>
								<span className={'now-m-block-start--sm'} />
								<div className={'assigned-to-block'}>
									<now-avatar
										size="sm"
										userName={
											assignedTo.value === userId
												? 'Me'
												: assignedTo.displayValue
										}
										imageSrc={assignedTo.avatar}
									/>

									<label className={'assigned-to-label'}>
										{t(
											'Assigned to {0}',
											assignedTo.value === userId
												? t('Me')
												: assignedTo.displayValue
										)}
									</label>
								</div>
							</Fragment>
						) : null}
					</now-collapse-trigger>

					<now-tooltip
						content={
							expanded
								? prioritized
									? PLAYBOOK_CARD_PRIORITIZED_COLLAPSE_TOOLTIP
									: PLAYBOOK_CARD_COLLAPSE_TOOLTIP
								: activityState.value ===
								  CARD_STATE_VALUES.PENDING.value
									? PLAYBOOK_CARD_PENDING_TOOLTIP
									: activityState.value ===
								  CARD_STATE_VALUES.ERROR.value
										? PLAYBOOK_CARD_ERROR_TOOLTIP
										: prioritized
											? PLAYBOOK_CARD_PRIORITIZED_EXPAND_TOOLTIP
											: PLAYBOOK_CARD_EXPAND_TOOLTIP
						}
						delay={PLAYBOOK_STANDARD_TOOLTIP_DELAY}
						hook-insert={({ elm }) =>
							elm
								? (elm.targetRef = elm.previousElementSibling)
								: null
						}
					/>

					<now-collapse
						expanded={expanded}
						id={collapseSectionId}
						on-keydown={(e) => {
							switch (e.key) {
								case EVENT_KEY.ENTER:
								case EVENT_KEY.SPACE:
									e.stopPropagation();
									break;
								default:
									return;
							}
						}}
						config-aria={{
							'aria-expanded': expanded
						}}
					>
						{emptyState ? (
							<Fragment>
								<span className={'now-m-block-start--lg'} />
								<EmptyState
									title={emptyState.title}
									text={emptyState.text}
									alignment={'horizontal'}
									illustration={
										activityState.value ===
										CARD_STATE_VALUES.ERROR.value
											? 'error'
											: 'no-data'
									}
								/>
								<span className={'now-m-block-start--xs'} />
							</Fragment>
						) : (
							<Fragment>
								<div className={'card-content'}>
									<slot name="content" />

									{renderForm(state)}

									<slot name="below-form" />
									<slot name="footer" />

									<div className="card-actions-container">
										{buttonActions.length &&
										expanded &&
										!stateCancelled ? (
												<sn-playbook-card-actions
													className="card-actions"
													items={buttonActions}
													append-to-payload={{
														model: {
															...declarativeModel,
															fields: formFieldItems
														},
														maxFormFieldsOnCard
													}}
												/>
											) : null}
									</div>
								</div>
								<slot name="playbook-card-footer">
									{renderPlaybookFooter(cardFooter, '')}
								</slot>
							</Fragment>
						)}
					</now-collapse>

					<sn-playbook-card-loader
						loading={loading}
						activityState={activityState}
					/>
				</div>
			</sn-playbook-card-mediator>
		</sn-declarative-mediator>
	);
};
