import { renderFormFieldControls } from './renderFormFieldControls';
import { renderFormSection } from './renderFormSection';
import '../sn-playbook-form-section/sn-playbook-form-section';

export function renderForm(state, dispatch) {
	const {
		properties: {
			formFields = [],
			associatedTable,
			associatedSysId,
			formView
		}
	} = state;
	// 'formFields' contains list of fields specified to render by the
	// Activity Context, it is used to generate 'formFieldItems' which contains
	// element data needed to render 'formFields'.
	//
	// If 'formFields' has content it means that we should either render
	// Form Field Controls (<Control />)
	// or Form Field List (<now-label-value-stacked />)
	// and NOT Form Section (<now-record-form-section-connected />)
	if (formFields.length) return renderFormFieldControls(state, dispatch);

	if (formView && associatedTable && associatedSysId)
		return renderFormSection(state);

	return null;
}

export function showFormSection(declarativeModel) {
	const {
		table = null,
		sysId = null,
		form_fields = null,
		form_view = null
	} = declarativeModel;

	// show if form view but no form fields
	if (table && sysId && form_view && !form_fields) return true;

	// Not showing the form section
	return false;
}
