import {
	CANCEL_PLAYBOOK,
	CANCEL_PLAYBOOK_CONFIRMED,
	NOW_MODAL_OPENED_SET,
	PLAYBOOK_CANCEL_PLAYBOOK_CONTEXT_MUTATION_GQL
} from '../../constants';

/**
 * NOTE: Whatever component this behavior is attached to must have a 'playbook' property.
 */
export const cancelPlaybookBehavior = {
	name: 'cancelPlaybookBehavior',
	properties: {
		/**
		 * the title and sysId of the playbook context to cancel
		 * @type {Object}
		 */
		cancelPlaybookContext: {
			default: {
				title: '',
				sysId: ''
			},
			selectable: true
		}
	},
	actionHandlers: {
		[CANCEL_PLAYBOOK]: {
			effect: ({ updateProperties, action, properties }) => {
				const { playbooksByContextId = {} } = properties;
				const { playbookContextId = '' } = action.payload;
				const { title = '' } =
					playbooksByContextId[playbookContextId] || {};

				if (title && playbookContextId) {
					setTimeout(() => {
						updateProperties(() => ({
							path: 'cancelPlaybookContext',
							operation: 'assign',
							value: { title, sysId: playbookContextId }
						}));
					}, 0);
				}
			},
			stopPropagation: true
		},
		[CANCEL_PLAYBOOK_CONFIRMED]: {
			effect: ({ dispatch, action: { payload } }) => {
				const { action: payloadAction = {} } = payload;
				const {
					playbookContextId,
					cancellationReason,
					playbookExperienceId
				} = payloadAction;

				if (playbookContextId && cancellationReason) {
					dispatch(PLAYBOOK_CANCEL_PLAYBOOK_CONTEXT_MUTATION_GQL, {
						playbookContextId,
						playbookExperienceId,
						cancellationReason
					});

					dispatch(NOW_MODAL_OPENED_SET, {
						playbookCloseCancelModal: true
					});
				}
			},
			stopPropagation: true
		},
		[NOW_MODAL_OPENED_SET]: {
			effect: ({ updateProperties, updateState, action }) => {
				if (action.payload.playbookCloseCancelModal) {
					updateProperties(() => ({
						path: 'cancelPlaybookContext',
						operation: 'assign',
						value: {
							title: '',
							sysId: ''
						}
					}));
					updateState({
						cancellationReason: ''
					});
				}
			},
			stopPropagation: true
		}
	}
};
