import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import styles from './styles.scss';

const strokeWidthFromSize = { sm: 5.333, md: 4.8, lg: 4.267, xl: 4 };
const getColorStyle = (color, properties = ['stroke']) =>
	color
		? properties.map((property) => `${property}: ${color}`).join(';')
		: '';

createCustomElement('now-playbook-radial-progress', {
	renderer: { type: snabbdom, view },
	properties: {
		progress: {
			default: 0,
			schema: { type: 'number', minimum: 0, maximum: 1 }
		},
		size: {
			default: 'xl',
			schema: {
				type: 'string',
				enum: ['sm', 'md', 'lg', 'xl']
			}
		},
		color: { schema: { type: 'string' } },
		backRingColor: { schema: { type: 'string' } },
		checkmarkColor: { schema: { type: 'string' } },
		checkmarkCompleteColor: { schema: { type: 'string' } },
		showCompleteIcon: {
			default: false,
			schema: {
				type: 'boolean'
			}
		},
		isComplete: {
			computed({ properties: { progress } }) {
				return progress >= 1;
			}
		},
		ring: {
			computed({ properties: { size } }) {
				const radius = 16 - 0.5 * strokeWidthFromSize[size];
				return {
					radius,
					strokeWidth: strokeWidthFromSize[size],
					circumference: 2 * Math.PI * radius
				};
			}
		},
		backRingAttributes: {
			computed({ properties: { backRingColor, ring } }) {
				return {
					cx: 16,
					cy: 16,
					r: ring.radius,
					'stroke-width': ring.strokeWidth,
					style: getColorStyle(backRingColor)
				};
			}
		},
		progressRingAttributes: {
			computed({ properties: { progress, isComplete, color, ring } }) {
				return {
					cx: 16,
					cy: 16,
					r: ring.radius,
					'stroke-width': ring.strokeWidth,
					'stroke-dasharray': ring.circumference,
					'stroke-dashoffset': (
						ring.circumference *
						(1 - progress)
					).toFixed(3),
					style: isComplete
						? getColorStyle(color, ['stroke', 'fill'])
						: getColorStyle(color)
				};
			}
		},
		checkmarkAttributes: {
			computed({
				properties: {
					size,
					isComplete,
					checkmarkColor,
					checkmarkCompleteColor
				}
			}) {
				return {
					points: [7.5, 16.8, 12.5, 22, 24.4, 10].join(','),
					'stroke-width': 0.57 * strokeWidthFromSize[size],
					style: getColorStyle(
						isComplete ? checkmarkCompleteColor : checkmarkColor
					)
				};
			}
		}
	},
	styles
});
