import '@servicenow/now-icon';
import '@servicenow/now-popover';
import '@servicenow/now-text-link';
import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import './now-playbook-cancelled-popover-content';
import styles from './styles.scss';

/**
 * ## Playbook Cancelled Popover
 *
 * @seismicElement now-playbook-cancelled-popover
 * @summary now-popover component that displays the cancellation reason for a playbook
 */
createCustomElement('now-playbook-cancelled-popover', {
	renderer: {
		type: snabbdom,
		view: ({ properties: { canceledBy, cancellationReason } }) => (
			<div className="popover-trigger-container">
				<div className="popover-trigger-display-area">
					<now-icon icon="triangle-exclamation-outline" />
					<span>{t('Canceled')}</span>
					<now-popover
						positions={[
							{ target: 'center-end', content: 'center-start' }
						]}
					>
						<now-text-link
							label={t('View Reason')}
							variant="secondary"
							href="javascript:void(0)"
							underlined={true}
							slot="trigger"
						/>
						<now-playbook-cancelled-popover-content
							slot="content"
							cancellationReason={cancellationReason}
							canceledBy={canceledBy}
						/>
					</now-popover>
				</div>
			</div>
		)
	},
	styles,
	properties: {
		/**
		 * the text for the reason the playbook was canceled - displayed in popover
		 * @type {String}
		 */
		cancellationReason: {
			default: ''
		},

		/**
		 * the text for who canceled by the playbook - displayed in popover
		 * @type {String}
		 */
		canceledBy: {
			default: ''
		}
	}
});
