import { actionTypes } from '@servicenow/ui-core';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { get } from 'lodash';
import {
	PLAYBOOK_CARD_KNOWLEDGE_FAILED,
	PLAYBOOK_CARD_KNOWLEDGE_FETCH,
	PLAYBOOK_CARD_KNOWLEDGE_STARTED,
	PLAYBOOK_CARD_KNOWLEDGE_SUCCEEDED,
	PLAYBOOK_SET_ACTIVITY_LOADING,
	PLAYBOOK_SUFFICIENT_DATA
} from '../../../constants';
import { NOTIFICATION_NETWORK_ERROR } from '../../../notification.constants';
import { stripHTML } from '../../../utils';
import { formatFields } from '../../utils';

const {
	COMPONENT_BOOTSTRAPPED,
	COMPONENT_PROPERTY_CHANGED,
	COMPONENT_CONNECTED
} = actionTypes;

const knowledgeQuery = `
	query($sysId: String!) {
	   GlideRecord_Query {
		   {{table}}(sys_id: $sysId) {
				_results{
					{{fields}}
				}
			}
		}
	}
`;

const queryFields = [
	'number',
	'short_description',
	'rating',
	'sys_view_count',
	'sys_updated_on',
	'sys_id',
	'text'
];

const innerFields = `{
	value
	displayValue
}`;

const fetchEffect = createGraphQLEffect(knowledgeQuery, {
	templateVarList: ['table', 'fields'],
	variableList: ['sysId'],
	startActionType: PLAYBOOK_CARD_KNOWLEDGE_STARTED,
	successActionType: PLAYBOOK_CARD_KNOWLEDGE_SUCCEEDED,
	errorActionType: PLAYBOOK_CARD_KNOWLEDGE_FAILED
});

const initiateFetchEffect = ({ dispatch, properties }) => {
	const { knowledgeTable, knowledgeRecord } = properties;

	if (knowledgeTable && knowledgeRecord)
		dispatch(PLAYBOOK_CARD_KNOWLEDGE_FETCH, {
			fields: formatFields(queryFields, innerFields),
			table: knowledgeTable,
			sysId: knowledgeRecord
		});
};

const fetchStartedEffect = ({ action, dispatch }) => {
	dispatch(PLAYBOOK_SET_ACTIVITY_LOADING, {
		contextId: action.payload.contextId,
		loading: true
	});
};

const receiveResultsEffect = ({
	action,
	dispatch,
	properties,
	updateState
}) => {
	const { knowledgeTable } = properties;
	const knowledge = get(
		action,
		`payload.data.GlideRecord_Query.${knowledgeTable}._results[0]`,
		{}
	);

	const heading = get(knowledge, 'short_description.displayValue', '');
	const snippet = stripHTML(get(knowledge, 'text.value', ''));
	const updated = get(knowledge, 'sys_updated_on.displayValue', '');
	const views = get(knowledge, 'sys_view_count.displayValue', '');

	updateState({
		knowledge,
		heading,
		snippet,
		updated,
		views
	});

	dispatch(PLAYBOOK_SET_ACTIVITY_LOADING, {
		contextId: action.payload.contextId,
		loading: false
	});
};

const fetchFailedEffect = ({ action, dispatch, updateState }) => {
	updateState({ notification: NOTIFICATION_NETWORK_ERROR });

	dispatch(PLAYBOOK_SET_ACTIVITY_LOADING, {
		contextId: action.payload.context,
		loading: false
	});
};

const hasSufficientData = (properties) => {
	const { activityProperties } = properties;
	const {
		title = {},
		knowledge_table = {},
		knowledge_record = {}
	} = activityProperties;

	// a card is sufficient with a title
	if (title.value) return true;

	// a card is sufficient with a knowledge table and knowledge record
	if (knowledge_table.value && knowledge_record.value) return true;

	// No data to show
	return false;
};

export default {
	[COMPONENT_BOOTSTRAPPED]: initiateFetchEffect,
	[PLAYBOOK_CARD_KNOWLEDGE_FETCH]: fetchEffect,
	[PLAYBOOK_CARD_KNOWLEDGE_STARTED]: fetchStartedEffect,
	[PLAYBOOK_CARD_KNOWLEDGE_SUCCEEDED]: receiveResultsEffect,
	[PLAYBOOK_CARD_KNOWLEDGE_FAILED]: fetchFailedEffect,
	[COMPONENT_CONNECTED]: ({ properties, dispatch }) => {
		dispatch(PLAYBOOK_SUFFICIENT_DATA, {
			hasSufficientData: hasSufficientData(properties)
		});
	},
	[COMPONENT_PROPERTY_CHANGED]: ({ action, dispatch, properties }) => {
		const { name, previousValue, value } = action.payload;

		if (name === 'activityProperties') {
			if (
				previousValue.knowledge_record.value !==
					value.knowledge_record.value ||
				previousValue.knowledge_table.value !==
					value.knowledge_table.value
			)
				initiateFetchEffect({ action, dispatch, properties });

			dispatch(PLAYBOOK_SUFFICIENT_DATA, {
				hasSufficientData: hasSufficientData(properties)
			});
		}
	}
};
