//TODO: Add fields back in when formLayout supports it
export const recordQuery = `
	query($sysId: String, $table: String!, $view: String) {
		GlideRecord_Query {
			{{associatedTable}} (sys_id: $sysId) {
				_results {
					sys_class_name {
						displayValue
					}
					number {
						displayValue
					}
					sys_updated_by {
						value
					}
					sys_id {
						value
					}
					_query {
						notes: GlideRecord_Query {
							sys_journal_field(queryConditions: "element_id=$$parent.sys_id$$^ORDERBYDESCsys_created_on", pagination: {limit: 1, offset: 0}) {
                     			_results {
                       				value {
                         				value
                         				displayValue
									}
								}
							}
						}
					}
				}
			}
		},
		GlideLayout_Query {
			formLayout(table: $table, view: $view, sysId: $sysId){
				baseTable
				recordValues {
					name
					value
					displayValue
					valuesList {
						value
						displayValue
					}
				}
				elementsData {
					name: elementName
					dictionary: dictionaryData {
						type: dictionaryType
						dependentField
						dependentTable
						name
						label
						canWrite
						canRead
						canCreate
						internalType
						isMandatory
						isReadonly: sysReadonly
						attributes {
							name
							value
						}
					}
					... on GlideLayout_ChoiceElementType {
						choices {
							displayValue
							value
						}
					}
					... on GlideLayout_ReferenceElementType {
						reference
						referenceQualifier
						referenceKey
						useReferenceQualifier
						dependent
						dependentOnField
						refAutoCompleter
						refAcOrderBy
						refAcColumns
						refAcColumnsSearch
						refAcDisplayValue
						refQualElements
						refContributions
						isDynamicCreate
						isReferenceScriptableTable
						referenceError
					}
					... on GlideLayout_GlideVarElementType {
						modelTable
						glidevars: _elements {
							name: elementName
							dictionary: dictionaryData {
								type: dictionaryType
								dependentField
								dependentTable
								name
								label
								canWrite
								canRead
								canCreate
								internalType
								isMandatory
								isReadonly: sysReadonly
								attributes {
									name
									value
								}
							}
							... on GlideLayout_ChoiceElementType {
								choices {
									displayValue
									value
								}
							}
							... on GlideLayout_ReferenceElementType {
								reference
								referenceQualifier
								referenceKey
								useReferenceQualifier
								dependent
								dependentOnField
								refAutoCompleter
								refAcOrderBy
								refAcColumns
								refAcColumnsSearch
								refAcDisplayValue
								refQualElements
								refContributions
								isDynamicCreate
								isReferenceScriptableTable
								referenceError
							}
							... on GlideLayout_ValueElementType {
								maxLength
								defaultRows
							}
						}
					}
					... on GlideLayout_ValueElementType {
						maxLength
						defaultRows
					}
				}
			}
		}
	}
`;
