import '@servicenow/now-record-form-connected';
import { GFORM_SAVE } from '../../../constants';
import { t } from 'sn-translate';

export const view = (state) => {
	const {
		properties: { playbookCardProperties, formView = '' },
		table = ''
	} = state;

	const items = [
		{
			label: t('Save'),
			variant: 'primary',
			clickActionType: GFORM_SAVE
		}
	];

	if (table === '') return null;

	return (
		<sn-playbook-card {...playbookCardProperties}>
			<now-record-form-section-connected
				slot="content"
				table={table}
				sys-id="-1"
				view={formView}
				inUiBuilder={true}
			>
				<sn-playbook-card-actions
					slot="footer"
					className="footer"
					items={items}
				/>
			</now-record-form-section-connected>
		</sn-playbook-card>
	);
};
