import {
	NOW_DROPDOWN_OPENED_SET,
	PLAYBOOK_STAGE_ACTIONS_DROPDOWN_OPENED_SET
} from '../../constants';

const actionHandlers = {
	[NOW_DROPDOWN_OPENED_SET]: {
		effect: ({ dispatch, action: { payload }, properties }) => {
			const { playbookContextId = '', stageName = '' } = payload;
			const { opened = false } = properties;

			dispatch(PLAYBOOK_STAGE_ACTIONS_DROPDOWN_OPENED_SET, {
				openedState: !opened,
				playbookContextId,
				stageName
			});
		},
		stopPropagation: true
	}
};

export default actionHandlers;
