import { actionTypes } from '@servicenow/ui-core';
import {
	LINK_INTERCEPTOR_SAVE_URLS,
	NOW_LABEL_VALUE_STACKED_TEXT_LINK_CLICKED,
	PLAYBOOK_FIELD_LIST_UPDATED,
	PREVIEW_RECORD,
	PLAYBOOK_FIELD_LIST_EMPTY
} from '../../constants';
const {
	COMPONENT_PROPERTY_CHANGED,
	COMPONENT_BOOTSTRAPPED,
	COMPONENT_CONNECTED
} = actionTypes;
import { convertElementInfo } from '../utils';

export const FIELD_LIST_BEHAVIOR_NAME = 'playbookFieldListBehavior';

function parseFieldList({
	dispatch,
	updateState,
	state: { fieldListProperties = [] },
	properties: { activityProperties }
}) {
	const htmlValues = [];

	const fieldListItems = fieldListProperties.reduce((result, prop) => {
		if (!activityProperties.hasOwnProperty(prop)) return result;

		const {
			[prop]: { relatedProperties = [] }
		} = activityProperties;

		if (!Array.isArray(relatedProperties) || !relatedProperties.length)
			return result;

		result[prop] = relatedProperties.map((prop) => {
			const { elementData } = prop;

			const info = convertElementInfo({
				label: prop.label || '',
				value: prop.value || PLAYBOOK_FIELD_LIST_EMPTY,
				displayValue: prop.displayValue || PLAYBOOK_FIELD_LIST_EMPTY,
				type: elementData.type || 'string',
				reference: elementData.reference
			});

			if (info.isHTMLType) htmlValues.push(prop.value);

			return info;
		});

		return result;
	}, {});

	if (htmlValues.length) dispatch(LINK_INTERCEPTOR_SAVE_URLS, htmlValues);

	updateState({ fieldListItems });
}

export const playbookFieldListBehavior = {
	name: FIELD_LIST_BEHAVIOR_NAME,
	initialState: { refreshRequested: null },
	actionHandlers: {
		[COMPONENT_CONNECTED]({
			updateState,
			action: {
				payload: { options }
			}
		}) {
			const { fieldListProperties = [] } = options;
			updateState({ fieldListProperties });
		},
		[COMPONENT_BOOTSTRAPPED]({ dispatch }) {
			dispatch(PLAYBOOK_FIELD_LIST_UPDATED);
		},
		[COMPONENT_PROPERTY_CHANGED]({ action, dispatch }) {
			const { payload: { name } = {} } = action || {};

			if (name === 'activityProperties')
				dispatch(PLAYBOOK_FIELD_LIST_UPDATED);
		},
		[NOW_LABEL_VALUE_STACKED_TEXT_LINK_CLICKED]: {
			effect: ({ action, dispatch }) => {
				const {
					payload: { item }
				} = action;
				if (item && item.table && item.sysId) {
					dispatch(PREVIEW_RECORD, {
						table: item.table,
						sys_id: item.sysId
					});
				}
			},
			stopPropagation: false
		},
		[PLAYBOOK_FIELD_LIST_UPDATED]: parseFieldList
	}
};
