import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { get } from 'lodash';
import {
	PLAYBOOK_ADD_OR_UPDATE_ACTIVITIES,
	PLAYBOOK_FETCH_ACTIVITY_CONTEXTS,
	PLAYBOOK_FETCH_ACTIVITY_CONTEXTS_DEBOUNCED,
	PLAYBOOK_FETCH_ACTIVITY_CONTEXTS_FAILED,
	PLAYBOOK_FETCH_ACTIVITY_CONTEXTS_SUCCEEDED,
	PLAYBOOK_REQUEST_FETCH_ACTIVITY_CONTEXTS,
	PLAYBOOK_SET_ACTIVITY_LOADING
} from '../../constants';
import { debounce } from '../../effects';
import { CARD_QUERY } from '../../gql.constants';
import { setCoeffectsPayload } from './utils';

const fetchActivityContextsQuery = `
	query($provider: String, $contextIds: String!, $playbookExperienceId: String) {
		GlidePlaybookContext_Query {
			getActivityContexts(provider: $provider, contextIds: $contextIds, playbookExperienceId: $playbookExperienceId) {
				${CARD_QUERY}
			}
		}
	}
`;

const requestFetchPlaybookActivityContextsEffect = ({
	action,
	state,
	dispatch
}) => {
	const contextIds = get(action, 'payload.contextIds', []);
	const provider = state.provider;

	if (!contextIds || !provider) return;

	if (!Array.isArray(contextIds) && typeof contextIds !== 'string') return;

	const ids = Array.isArray(contextIds) ? contextIds : [contextIds];
	// We want the card to immediate show a loading before the debouncing happens,
	// this requires us not to check for duplicates here. We also cannot check for
	// duplicate 'contextIds' in the aggregate callback of 'PLAYBOOK_FETCH_ACTIVITY_CONTEXTS_DEBOUNCED'
	// because more fetch requests can coming in after this fetch request with
	// same 'contextIds'. So the solution is we check for duplicates in
	// 'PLAYBOOK_FETCH_ACTIVITY_CONTEXTS_DEBOUNCED' after the debounce
	// and before fetching Activity Contexts, while at the same time setting
	// 'loading=false' for any duplicate 'contextIds'
	dispatch(PLAYBOOK_FETCH_ACTIVITY_CONTEXTS_DEBOUNCED, { contextIds: ids });
	dispatch(PLAYBOOK_SET_ACTIVITY_LOADING, {
		requests: contextIds.map((contextId) => ({ contextId, loading: true }))
	});
};

const fetchActivityContextsDebouncedEffect = ({
	action,
	dispatch,
	state,
	properties
}) => {
	const contextIds = get(action, 'payload.contextIds', []);
	const provider = state.provider;
	const playbookExperienceId = properties.playbookExperienceId;
	const uniqIds = new Set();
	const duplicateIds = [];

	contextIds.forEach((id) => {
		uniqIds.has(id) ? duplicateIds.push(id) : uniqIds.add(id);
	});

	dispatch(PLAYBOOK_FETCH_ACTIVITY_CONTEXTS, {
		provider,
		playbookExperienceId,
		contextIds: [...uniqIds].join(',')
	});
	// If there is a duplicate contextId in this fetch action handler it means
	// that we have already dispatched a '{ loading: true }' to the loading
	// action handler, so here we dispatch another '{ loading: false }' to
	// cancel out the earlier one
	dispatch(PLAYBOOK_SET_ACTIVITY_LOADING, {
		requests: duplicateIds.map((contextId) => ({
			contextId,
			loading: false
		}))
	});
};

const fetchActivityContextsEffect = createGraphQLEffect(
	fetchActivityContextsQuery,
	{
		variableList: ['provider', 'contextIds', 'playbookExperienceId'],
		successActionType: PLAYBOOK_FETCH_ACTIVITY_CONTEXTS_SUCCEEDED,
		errorActionType: PLAYBOOK_FETCH_ACTIVITY_CONTEXTS_FAILED
	}
);

const fetchActivityContextsSucceededEffect = ({ action, dispatch }) => {
	const activities = get(
		action,
		'payload.data.GlidePlaybookContext_Query.getActivityContexts',
		[]
	);
	dispatch(PLAYBOOK_ADD_OR_UPDATE_ACTIVITIES, { activities });
	dispatch(PLAYBOOK_SET_ACTIVITY_LOADING, {
		requests: activities.map(({ contextId }) => ({
			contextId,
			loading: false
		}))
	});
};

const fetchActivityContextsFailedEffect = ({ action, dispatch }) => {
	const contextIds = get(action, 'meta.options.variables.contextIds', []);
	dispatch(PLAYBOOK_SET_ACTIVITY_LOADING, {
		requests: contextIds
			.split(',')
			.map((contextId) => ({ contextId, loading: false }))
	});
	// TODO: dispatching a error message banner; ask DJ/Meredith
};

export default {
	[PLAYBOOK_REQUEST_FETCH_ACTIVITY_CONTEXTS]: requestFetchPlaybookActivityContextsEffect,
	[PLAYBOOK_FETCH_ACTIVITY_CONTEXTS_DEBOUNCED]: debounce(
		fetchActivityContextsDebouncedEffect,
		{
			delay: 1000,
			maxWait: 2000,
			aggregate: (args = [], nextArgs = []) => {
				const idx = nextArgs.length - 1 || 0; // 'coeffects' is the last argument passed to Action Handler
				const oldContextIds = get(
					args[idx],
					'action.payload.contextIds',
					[]
				);
				const newContextIds = get(
					nextArgs[idx],
					'action.payload.contextIds',
					[]
				);
				const newArgs = [...nextArgs];

				newArgs[idx] = setCoeffectsPayload(nextArgs[idx], {
					contextIds: [...oldContextIds, ...newContextIds]
				});

				return newArgs;
			}
		}
	),
	[PLAYBOOK_FETCH_ACTIVITY_CONTEXTS]: fetchActivityContextsEffect,
	[PLAYBOOK_FETCH_ACTIVITY_CONTEXTS_SUCCEEDED]: fetchActivityContextsSucceededEffect,
	[PLAYBOOK_FETCH_ACTIVITY_CONTEXTS_FAILED]: fetchActivityContextsFailedEffect
};
