import { t } from 'sn-translate';
import { PANEL_NAVIGATION_UPDATE_MAP } from '../../behaviors/sn-panel-navigation/constants';

const view = (
	{
		properties: {
			transformedDeclarativeActions,
			declarativeModel,
			opened,
			playbookContextId,
			stageName
		}
	},
	{ dispatch }
) => {
	if (transformedDeclarativeActions.length === 0) return null;

	const dropdownActions = transformedDeclarativeActions.map((action) => {
		return {
			...action,
			id: action.assignmentId,
			icon: action.icon || ''
		};
	});

	return (
		<now-dropdown
			manage-opened={opened}
			opened={opened}
			items={dropdownActions}
			selectedItems={[]}
			icon="ellipsis-v-outline"
			tooltipContent={t('Playbook Stage Actions')}
			size="sm"
			variant="secondary"
			select="none"
			bare={true}
			hideCaret={true}
			hook-insert={({ elm }) =>
				dispatch && dropdownActions && dropdownActions.length && elm
					? dispatch(PANEL_NAVIGATION_UPDATE_MAP, {
						mapKey: `stage-actions-${playbookContextId}.${stageName}`,
						mapEntry: {
							domRef: elm,
							left: `stage-item-${playbookContextId}.${stageName}`
						}
					  })
					: null
			}
			data-nav={`stage-actions-${playbookContextId}.${stageName}`}
			append-to-payload={{
				model: declarativeModel,
				playbookContextId,
				stageName
			}}
		/>
	);
};

export default view;
