import { actionTypes } from '@servicenow/ui-core';
import {
	NOW_DROPDOWN_OPENED_SET,
	PLAYBOOK_ACTIONS_DROPDOWN_OPENED_SET
} from '../../constants';
import { PANEL_NAVIGATION_UPDATE_MAP } from '../../behaviors/sn-panel-navigation/constants';
const { COMPONENT_PROPERTY_CHANGED } = actionTypes;

const actionHandlers = {
	[COMPONENT_PROPERTY_CHANGED]: ({
		action: {
			payload: { name, value }
		},
		properties: { playbookContextId },
		dispatch
	}) =>
		name === 'leftNavKey'
			? dispatch(PANEL_NAVIGATION_UPDATE_MAP, {
				mapKey: `pb-actions-${playbookContextId}`,
				mapEntry: {
					left: value
				}
			  })
			: null,
	[NOW_DROPDOWN_OPENED_SET]: {
		effect: ({ dispatch, action: { payload }, properties }) => {
			const { playbookContextId = '' } = payload;
			const { opened = false } = properties;

			dispatch(PLAYBOOK_ACTIONS_DROPDOWN_OPENED_SET, {
				openedState: !opened,
				playbookContextId
			});
		},
		stopPropagation: true
	}
};

export default actionHandlers;
