import { actionTypes } from '@servicenow/ui-core';
import get from 'lodash/get';
import {
	PLAYBOOK_CARD_AUTOMATED_ICON,
	PLAYBOOK_CARD_AUTOMATED_TAGLINE,
	PLAYBOOK_SUFFICIENT_DATA
} from '../../constants';
import { defaultRendererProperties } from '../utils';
import { NOTIFICATION_PLAYBOOK_CARD_PENDING } from '../../notification.constants';

const { COMPONENT_PROPERTY_CHANGED } = actionTypes;

export const playbookCardBehavior = {
	name: 'playbookCardBehavior',
	properties: {
		...defaultRendererProperties,
		declarativeActions: {
			default: []
		},
		declarativeModel: {
			default: {}
		},
		parentTable: {
			default: ''
		},
		parentSysId: {
			default: ''
		},
		filterData: {
			default: []
		},
		assignedTo: {
			default: {}
		},
		playbookCardProperties: {
			computed({
				formFieldItems,
				title: stateTitle,
				notification,
				hasSufficientData = true,
				formFieldsInModal,
				properties: {
					associatedSysId,
					associatedTable,
					declarativeActions,
					declarativeModel,
					activityState,
					activityProperties: {
						pending_title = {},
						pending_description = {},
						title = {},
						footer,
						is_automated = {},
						show_sla = {},
						tagline = {},
						icon = {}
					} = {},
					configProperties,
					filterData,
					assignedTo,
					formFieldsRequired,
					loading,
					contextId,
					prioritized,
					expandOnMountOrChange,
					userId,
					activityLabel
				} = {}
			}) {
				// Override icon / tagline if automated
				if (is_automated.value === '1') {
					icon.value = PLAYBOOK_CARD_AUTOMATED_ICON;
					icon.displayValue = PLAYBOOK_CARD_AUTOMATED_ICON;
					tagline.value = PLAYBOOK_CARD_AUTOMATED_TAGLINE;
					tagline.displayValue = PLAYBOOK_CARD_AUTOMATED_TAGLINE;
				}

				return {
					declarativeActions,
					declarativeModel,
					activityState,
					...(associatedTable && { associatedTable }),
					...(associatedSysId && { associatedSysId }),
					...(activityLabel && {
						heading: {
							label: activityLabel
						}
					}),
					...(title.value && {
						heading: {
							label: title.value
						}
					}),
					...(formFieldItems && { formFieldItems }),
					...((tagline.value || icon.value) && {
						tagline: {
							label: tagline.value || '',
							icon: icon.value || ''
						}
					}),
					pendingTitle:
						get(pending_title, 'value') ||
						get(title, 'value') ||
						activityLabel ||
						NOTIFICATION_PLAYBOOK_CARD_PENDING.header,
					...(pending_description.value && {
						pendingDescription: pending_description.value
					}),
					loading,
					contextId,
					configProperties,
					cardFooter: footer,
					filterData,
					assignedTo,
					formFieldsRequired,
					...(notification && { notification }),
					formFieldsInModal,
					showSla: show_sla.value === '1',
					hasSufficientData,
					prioritized,
					expandOnMountOrChange,
					userId
				};
			}
		}
	},
	actionHandlers: {
		[PLAYBOOK_SUFFICIENT_DATA]: ({ action: { payload }, updateState }) => {
			const { hasSufficientData = null } = payload;
			if (hasSufficientData === null) return;

			updateState({ hasSufficientData });
		},
		[COMPONENT_PROPERTY_CHANGED]: ({ action, updateState }) => {
			const { payload: { name, value, previousValue } = {} } =
				action || {};

			if (name === 'associatedRecordModCount' && value > previousValue)
				updateState({ associatedRecordUpdated: Date.now() });
		}
	}
};
