import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { preWsConfigSysId } from 'sn-uxpage-presource';
import { PLAYBOOK_SUFFICIENT_DATA } from '../../../constants';
import {
	playbookCardBehavior,
	playbookFieldListBehavior,
	playbookRecordBehavior
} from '../../behaviors';
import styles from './styles.scss';
import { view } from './view';

const {
	COMPONENT_BOOTSTRAPPED,
	COMPONENT_PROPERTY_CHANGED,
	COMPONENT_CONNECTED
} = actionTypes;

const hasSufficientData = (properties) => {
	const { activityProperties, showChecklist, description } = properties;
	const {
		title = {},
		associated_table = {},
		associated_record = {},
		form_fields = {},
		form_view = {},
		record_fields = {},
		attachment_source = {}
	} = activityProperties;

	// a card is sufficient with a description or title
	if (description || title.value) return true;

	// without a description or title, a card must have an associated record and table
	if (!associated_table.value || !associated_record.value) return false;

	// If we have data to show for this associated record, we're all set
	if (
		form_fields.value ||
		form_view.value ||
		record_fields.value ||
		attachment_source.value ||
		showChecklist
	)
		return true;

	// No data to show
	return false;
};

/**
 * ## Default Playbook Card
 *
 * Default playbook card that accepts these activityProperties:
 * - title
 * - description
 * - recordFields
 * - formFields
 * - showChecklist
 * - formView
 *
 * @summary The default card rendering
 * @seismicElement sn-playbook-card-default
 */
createCustomElement('sn-playbook-card-default', {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {
		formFieldItems: []
	},
	behaviors: [
		{ behavior: playbookCardBehavior },
		{ behavior: playbookRecordBehavior },
		{
			behavior: playbookFieldListBehavior,
			options: { fieldListProperties: ['record_fields'] }
		},
		preWsConfigSysId
	],
	properties: {
		/**
		 * Displayed at the top of the card if present.
		 * @type {String}
		 */
		description: {
			computed({
				properties: {
					activityProperties: { description }
				}
			}) {
				return description ? description.value : '';
			}
		},
		/**
		 * If true is set and there's an existing checklist for the associated record
		 * that checklist should show up in the activity card.
		 * @type {Boolean}
		 */
		showChecklist: {
			computed({
				properties: {
					activityProperties: { show_checklist }
				}
			}) {
				// Convert true/false value to actual true/false
				return !!(show_checklist && show_checklist.value === '1');
			}
		}
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({ state, updateState }) => {
			updateState({
				workspaceConfigId: preWsConfigSysId.getPreSource(state)
			});
		},
		[COMPONENT_CONNECTED]: ({ properties, dispatch }) => {
			dispatch(PLAYBOOK_SUFFICIENT_DATA, {
				hasSufficientData: hasSufficientData(properties)
			});
		},
		[COMPONENT_PROPERTY_CHANGED]: ({
			properties,
			action: {
				payload: { name }
			},
			dispatch
		}) => {
			if (name === 'activityProperties')
				dispatch(PLAYBOOK_SUFFICIENT_DATA, {
					hasSufficientData: hasSufficientData(properties)
				});
		}
	},
	styles
});
