import rtlBehavior from '@servicenow/behavior-rtl';
import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { preUserData } from 'sn-uxpage-presource';
import {
	DEFAULT_MAX_FORM_FIELDS_RENDERED_IN_CARD,
	EVENT_KEY,
	PLAYBOOK_CARD_VIEW_MODE_CHANGE
} from '../../../constants';
import {
	NOTIFICATION_PLAYBOOK_CARD_CANCELLED,
	NOTIFICATION_PLAYBOOK_CARD_PENDING
} from '../../../notification.constants';
import actionHandlers from './actions';
import styles from './styles.scss';
import { view } from './view';

/**
 * ## Playbook Card
 *
 * Wraps now-card, adding support for Declarative Actions
 * - Card Button
 * - Header Dropdown
 * - Card Click
 * - Form Button
 *
 * @summary Wrapper for now-card
 * @seismicElement sn-playbook-card
 */
createCustomElement('sn-playbook-card', {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {
		expanded: false,
		modalProperties: {},
		/*
		 * Defaults to 'null' to signify no caculations have been done because
		 * no Declarative Actions are present.
		 */
		formFieldsRequired: null,
		headerActionsDropdownOpened: false
	},
	properties: {
		/** @type {String} **/
		contextId: { default: '' },
		// associated record table name
		/** @type {String} **/
		associatedTable: { default: {} },
		// associated record sys id
		/** @type {String} **/
		associatedSysId: { default: {} },
		/** @type {} **/
		formFieldItems: [],
		/** @type {} **/
		declarativeModel: {
			default: {
				table: '',
				sysId: ''
			}
		},
		/** @type {{showActivityState: Boolean, showAutomatedManualIndicator: Boolean}} **/
		configProperties: {
			default: {
				showActivityState: true,
				showAutomatedManualIndicator: true,
				maxFormFieldsOnCard: DEFAULT_MAX_FORM_FIELDS_RENDERED_IN_CARD
			}
		},
		/** @type {{value: String, displayValue: String}} **/
		activityState: {
			default: {
				value: '',
				displayValue: ''
			}
		},
		/** @type {Boolean} **/
		loading: { default: false },
		/** @type {Boolean} **/
		prioritized: { default: false },
		/** @type {Array} **/
		declarativeActions: {
			schema: {
				type: 'array'
			},
			default: []
		},
		/** @type {Array} **/
		buttonActions: {
			default: [],
			selectable: true
		},
		/** @type {Array} **/
		cardClickActions: {
			default: [],
			selectable: true
		},
		/** @type {Array} **/
		filterData: { default: [] },
		// header properties
		/** @type {String} **/
		heading: {
			default: { label: '' }
		},
		/** @type {String} **/
		tagline: {
			default: { label: '' }
		},
		/** @type {} **/
		assignedTo: {},
		/** @type {String} **/
		caption: {},
		/** @type {Array} **/
		headerIconActions: {
			default: [],
			selectable: true
		},
		/** @type {Array} **/
		headerDropdownActions: {
			default: [],
			selectable: true
		},
		/** @type {String} **/
		cardFooter: {},
		/** @type {Boolean} **/
		formFieldsInModal: { default: false },
		/** @type {Boolean} **/
		isAutomated: { default: false },
		/** @type {Boolean} **/
		showSla: { default: false },
		// error notification
		/** @type {} **/
		notification: { default: {} },
		/** @type {String} **/
		pendingTitle: { default: '' },
		/** @type {String} **/
		pendingDescription: {
			default: NOTIFICATION_PLAYBOOK_CARD_PENDING.body
		},
		/** @type {String} **/
		cancelledTitle: {
			default: NOTIFICATION_PLAYBOOK_CARD_CANCELLED.header
		},
		/** @type {String} **/
		cancelledDescription: {
			default: NOTIFICATION_PLAYBOOK_CARD_CANCELLED.body
		},
		/** @type {Boolean} **/
		hasSufficientData: { default: true },
		showFormSection: { default: undefined },
		/** @type {Boolean} **/
		expandOnMountOrChange: { default: false },
		/** @type {String} **/
		userId: { default: '' },
		/** @type {String} **/
		mediatorComponentId: { default: '' }
	},
	eventHandlers: [
		{
			events: ['keydown'],
			effect({
				action: {
					payload: { event }
				},
				state: { expanded },
				dispatch
			}) {
				switch (event.key) {
					case EVENT_KEY.ENTER:
					case EVENT_KEY.SPACE:
						dispatch(PLAYBOOK_CARD_VIEW_MODE_CHANGE, {
							expanded: !expanded
						});
						break;
					default:
						return;
				}
			}
		}
	],
	actionHandlers,
	behaviors: [preUserData, rtlBehavior],
	styles
});
