import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { view } from './view';
import styles from './styles.scss';

/**
 * ## Playbook Modal
 *
 * @summary Create an now-playbook-experience-modal-default component and renders it within a now-modal
 * @seismicElement now-playbook-experience-modal-default
 */
createCustomElement('now-playbook-experience-modal-default', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		/** @type {String} */
		title: {
			default: ''
		},
		/** @type {String} */
		description: {
			default: ''
		},
		/** @type {String} */
		associatedRecord: {
			default: ''
		},
		/** @type {String} */
		associatedTable: {
			default: ''
		},
		/** @type {String} */
		state: {
			default: ''
		},
		/** @type {String} */
		type: {
			default: ''
		},
		/** @type {String} */
		formFields: {
			default: ''
		}
	},
	styles
});
