export function renderChecklist(state, slotName) {
	const {
		properties: { showChecklist, associatedSysId }
	} = state;

	if (!showChecklist || !associatedSysId) return null;

	return (
		<div slot={slotName} className="checklist-container">
			<sn-checklist document-id={associatedSysId} />
		</div>
	);
}
