import { getFetchActions } from '@devsnc/library-form';
import {
	CLEAR_NOTIFICATIONS,
	createClientScriptBehavior,
	getConfig,
	getGFormAttributes,
	REMOVE_NOTIFICATION
} from '@devsnc/sn-scripting-environment';
import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import { snabbdom } from '@servicenow/ui-renderer-snabbdom';
import { get } from 'lodash';
import {
	DECLARATIVE_ACTION,
	DECLARATIVE_ACTION_TYPE_COMPONENT,
	DECLARATIVE_ACTION_UPDATE_RENDER,
	FORM_DATA_PROVIDER_FETCH_START_REQUESTED,
	GFORM_VALIDATE_SUCCESS,
	NOW_ALERT_LIST_ITEMS_SET
} from '../../constants';
import './sn-playbook-form-section-footer';

const getStatus = (type) => {
	switch (type) {
		case 'info':
			return 'info';
		case 'error':
			return 'critical';
		case 'warning':
			return 'warning';
		case 'success':
			return 'positive';
		default:
			return 'info';
	}
};

const getIcon = (type) => {
	switch (type) {
		case 'error':
			return 'circle-close-outline';
		case 'warning':
			return 'exclamation-circle-outline';
		case 'success':
			return 'check-circle-outline';
		default:
			return 'info-circle-outline';
	}
};

const { COMPONENT_RENDER_REQUESTED } = actionTypes;

const clientScriptBehavior = createClientScriptBehavior(null, {
	classicForm: true,
	fetchFormDataActions: getFetchActions('PLAYBOOK')
});

const view = (state) => {
	const {
		formData,
		formModal,
		notifications,
		properties: {
			contextId,
			table,
			sysId,
			associatedRecordModCount,
			view,
			dispatchAction,
			filteredFields,
			filteredSections,
			mediatorComponentId
		}
	} = state;

	if (!filteredFields || !formData || !formData.record) return null;

	const actionSelected = `@${mediatorComponentId}/actionSelected`;

	const formSectionProperties = {
		table,
		sysId,
		view,
		fields: filteredFields,
		sections: filteredSections,
		config: {
			...getConfig(formData),
			showActivityStream: false,
			isSectionMenuHidden: true,
			noSectionHeaderPadding: true
		},
		inUiBuilder: true,
		formModal,
		...getGFormAttributes(state, state.properties)
	};
	const alerts = notifications.map(({ type, message }, idx) => {
		return {
			id: idx,
			status: getStatus(type),
			icon: getIcon(type),
			content: {
				type: 'html',
				value: message
			},
			action: { type: 'dismiss' }
		};
	});
	return (
		<div>
			<sn-form-internal-modal-container />
			<now-alert-list items={alerts} manage-items={true} />
			<now-record-form-section-column-layout {...formSectionProperties} />
			<sn-playbook-form-section-footer
				associated-record-mod-count={associatedRecordModCount}
				action-Selected={actionSelected}
				dispatch-action={dispatchAction}
				fields={filteredFields}
			/>
		</div>
	);
};

const computeFilteredFields = (state) => {
	const {
		fields = {},
		properties: { fieldWhitelist = [], view }
	} = state;

	if (fieldWhitelist.length) {
		let filteredFields = {};
		for (let fieldName of Object.keys(fields))
			for (let whitelistedFieldName of fieldWhitelist)
				if (
					fieldName === whitelistedFieldName ||
					fieldName.indexOf(whitelistedFieldName + '.') === 0
				)
					filteredFields[fieldName] = fields[fieldName];
		return filteredFields;
	} else if (view && view !== '') return fields;
	else return null;
};

const computeFilteredSections = (state) => {
	const {
		sections = [],
		properties: { fieldWhitelist = [] }
	} = state;
	if (fieldWhitelist.length)
		return sections
			.map(({ rows, ...section }) => {
				const newRows = rows.map((row) => {
					return row.filter((column) => {
						const { fields } = column;
						for (let fieldName of fields)
							for (let whitelistedFieldName of fieldWhitelist)
								if (
									fieldName === whitelistedFieldName ||
									fieldName.indexOf(
										whitelistedFieldName + '.'
									) === 0
								)
									return true;
						return false;
					});
				});

				return {
					...section,
					rows: newRows
				};
			})
			.filter(({ rows }) => {
				return rows.some((row) =>
					row.some(({ fields }) => fields.length)
				);
			});
	else return sections;
};

createCustomElement('sn-playbook-form-section-scripted', {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: { classicForm: true },
	behaviors: [{ behavior: clientScriptBehavior }],
	properties: {
		contextId: {
			default: ''
		},
		table: {
			required: true
		},
		sysId: {
			required: true
		},
		view: {
			default: ''
		},
		forcedViewName: {
			default: ''
		},
		readOnlyForm: {
			default: false
		},
		associatedRecordModCount: {
			default: 0
		},
		dispatchAction: {
			default: null
		},
		fieldWhitelist: {
			default: []
		},
		filteredFields: {
			computed: computeFilteredFields,
			default: null
		},
		filteredSections: {
			computed: computeFilteredSections
		},
		mediatorComponentId: {
			default: ''
		}
	},
	actionHandlers: {
		['SN_FORM_DATA_CONNECTED#FETCH_START_REQUESTED']: {
			stopPropagation: true
		},
		[FORM_DATA_PROVIDER_FETCH_START_REQUESTED]: {
			stopPropagation: true
		},
		[DECLARATIVE_ACTION_UPDATE_RENDER]: {
			stopPropagation: true
		},
		[DECLARATIVE_ACTION]: {
			effect: ({ action }) => {
				const da = get(action, 'payload.action', {});

				if (da.actionType === DECLARATIVE_ACTION_TYPE_COMPONENT)
					action.stopPropagation();
			}
		},
		[NOW_ALERT_LIST_ITEMS_SET]: ({
			state: { notifications },
			action,
			dispatch
		}) => {
			if (action.payload.value.length === 0) {
				dispatch(CLEAR_NOTIFICATIONS, {
					notificationAction: {
						type: 'clear'
					}
				});
				return;
			}

			notifications.forEach((notification, index) => {
				if (!action.payload.value.find(({ id }) => id === index))
					dispatch(REMOVE_NOTIFICATION, { index });
			});
		},
		[GFORM_VALIDATE_SUCCESS]: ({ updateProperties }) => {
			updateProperties({ dispatchAction: Date.now() });
		},
		[COMPONENT_RENDER_REQUESTED]: ({ action, state, properties }) => {
			const currentWhitelist = properties.fieldWhitelist || [];
			const previousWhitelist =
				action.payload.previousRenderProperties.fieldWhitelist || [];

			if (
				state.fields &&
				action.payload.previousRenderState.notifications ===
					state.notifications &&
				action.payload.previousRenderState.fields === state.fields &&
				action.payload.previousRenderProperties.dispatchAction ===
					properties.dispatchAction &&
				currentWhitelist.join(',') === previousWhitelist.join(',')
			)
				action.preventDefault();
		}
	}
});

export { computeFilteredFields, computeFilteredSections };
