import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import get from 'lodash/get';
import {
	ADD_NOTIFICATIONS,
	CARD_STATE_VALUES,
	PLAYBOOK_ADD_OR_UPDATE_ACTIVITIES,
	PLAYBOOK_CANCEL_ACTIVITY_CONTEXT,
	PLAYBOOK_COMPLETE_ACTIVITY_CONTEXT,
	PLAYBOOK_ERROR,
	PLAYBOOK_SET_ACTIVITY_CONTEXT_STATE_MUTATION_GQL,
	PLAYBOOK_SET_ACTIVITY_CONTEXT_STATE_SUCCEEDED,
	PLAYBOOK_SET_ACTIVITY_LOADING,
	PLAYBOOK_SKIP_ACTIVITY_CONTEXT,
	PLAYBOOK_START_ACTIVITY_CONTEXT
} from '../../constants';
import { CARD_QUERY } from '../../gql.constants';

export const setActivityContextMutation = `
	mutation ($contextId: String!, $playbookExperienceId: String!, $targetState: String!) {
		GlidePlaybookContext_Mutation {
			setActivityContextStateMutation(contextId: $contextId, playbookExperienceId: $playbookExperienceId, targetState: $targetState) {
				${CARD_QUERY}
			}
		}
	}  
`;

export default {
	[PLAYBOOK_START_ACTIVITY_CONTEXT]: ({ action, dispatch, properties }) => {
		const { playbookExperienceId = '' } = properties;

		dispatch(PLAYBOOK_SET_ACTIVITY_LOADING, {
			contextId: action.payload.contextId,
			loading: true
		});

		dispatch(PLAYBOOK_SET_ACTIVITY_CONTEXT_STATE_MUTATION_GQL, {
			...action.payload,
			playbookExperienceId,
			targetState: CARD_STATE_VALUES.IN_PROGRESS.value
		});
	},
	[PLAYBOOK_SKIP_ACTIVITY_CONTEXT]: ({ action, dispatch, properties }) => {
		const { playbookExperienceId = '' } = properties;

		dispatch(PLAYBOOK_SET_ACTIVITY_LOADING, {
			contextId: action.payload.contextId,
			loading: true
		});

		dispatch(PLAYBOOK_SET_ACTIVITY_CONTEXT_STATE_MUTATION_GQL, {
			...action.payload,
			playbookExperienceId,
			targetState: CARD_STATE_VALUES.SKIPPED.value
		});
	},
	[PLAYBOOK_COMPLETE_ACTIVITY_CONTEXT]: ({
		action,
		dispatch,
		properties
	}) => {
		const { playbookExperienceId = '' } = properties;

		dispatch(PLAYBOOK_SET_ACTIVITY_LOADING, {
			contextId: action.payload.contextId,
			loading: true
		});

		dispatch(PLAYBOOK_SET_ACTIVITY_CONTEXT_STATE_MUTATION_GQL, {
			...action.payload,
			playbookExperienceId,
			targetState: CARD_STATE_VALUES.COMPLETE.value
		});
	},
	[PLAYBOOK_CANCEL_ACTIVITY_CONTEXT]: ({ action, dispatch, properties }) => {
		const { playbookExperienceId = '' } = properties;

		dispatch(PLAYBOOK_SET_ACTIVITY_LOADING, {
			contextId: action.payload.contextId,
			loading: true
		});

		dispatch(PLAYBOOK_SET_ACTIVITY_CONTEXT_STATE_MUTATION_GQL, {
			...action.payload,
			playbookExperienceId,
			targetState: CARD_STATE_VALUES.CANCELLED.value
		});
	},
	[PLAYBOOK_SET_ACTIVITY_CONTEXT_STATE_MUTATION_GQL]: createGraphQLEffect(
		setActivityContextMutation,
		{
			variableList: ['contextId', 'playbookExperienceId', 'targetState'],
			successActionType: PLAYBOOK_SET_ACTIVITY_CONTEXT_STATE_SUCCEEDED,
			errorActionType: PLAYBOOK_ERROR
		}
	),
	[PLAYBOOK_SET_ACTIVITY_CONTEXT_STATE_SUCCEEDED]: ({ action, dispatch }) => {
		const activity = get(
			action,
			'payload.data.GlidePlaybookContext_Mutation.setActivityContextStateMutation',
			null
		);

		if (!activity) {
			return;
		}

		dispatch(PLAYBOOK_SET_ACTIVITY_LOADING, {
			contextId: activity.contextId,
			loading: false
		});

		if (activity.errors.length) {
			const notifications = activity.errors.map(({ message }) => {
				return {
					type: 'error',
					message
				};
			});

			dispatch(ADD_NOTIFICATIONS, { notifications });
			return;
		}

		dispatch(PLAYBOOK_ADD_OR_UPDATE_ACTIVITIES, {
			activities: [activity]
		});
	}
};
