import '@servicenow/now-textarea';
import { t } from 'sn-translate';
import { CANCEL_PLAYBOOK_CONFIRMED, NOW_MODAL_OPENED_SET } from '../constants';

const view = ({
	properties: {
		cancelPlaybookContext: { title = '', sysId = '' },
		playbookExperienceId = ''
	},
	cancellationReason = ''
}) => {
	return (
		<now-modal
			opened={title !== '' && sysId !== ''}
			size="md"
			header-label={t('Canceling {0}', title)}
			footer-actions={[
				{
					variant: 'primary-negative',
					label: t('Cancel Playbook'),
					clickActionType: CANCEL_PLAYBOOK_CONFIRMED,
					disabled: cancellationReason.length === 0,
					cancellationReason,
					playbookContextId: sysId,
					playbookExperienceId
				},
				{
					variant: 'secondary',
					label: t('Close'),
					clickActionType: NOW_MODAL_OPENED_SET
				}
			]}
			append-to-payload={{ playbookCloseCancelModal: true }}
		>
			<p className="now-text--md">
				{t(
					'Some activities cant be canceled once started. These activities will need to finish.'
				)}
				<br />
				{t('Provide a reason for canceling this Playbook.')}
			</p>
			<now-textarea
				label={t('Reason for cancellation')}
				required={true}
				resize="none"
				showBorders={true}
				placeholder={t('Enter reason here...')}
				value={cancellationReason}
				append-to-payload={{ textAreaField: 'cancellationReason' }}
				rows={5}
			></now-textarea>
		</now-modal>
	);
};

export default view;
