import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import {
	createTransformDeclarativeActionsBehaviorFromModel,
	declarativeActionsHeaderClickedBehavior
} from '../../behaviors';
import { EVENT_KEY } from '../../constants';
import actionHandlers from './actions';
import view from './view';

/**
 * ## Playbook Stage Actions
 *
 * Dropdown component that enables the user to take action. Renders Declarative
 * Actions that implement Playbook Stage declarative model inside now-dropdown
 * when provided with running playbook sysId and status, stage order, stage status,
 * stage title, stage name, and activity contexts belonging to the stage.
 *
 * ```jsx
 * const { sysId, state, parentTable, parentRecord } = playbook;
 * const { order, state: stageState, title, name, cards } = stage;
 * <sn-declarative-mediator>
 * 	<sn-playbook-actions
 * 		declarative-actions={`@playbook-actions-data-provider-${sysId}/declarativeActions}
 * 		table={parentTable}
 * 		sys-id={parentRecord}
 * 		playbook-context-id={sysId}
 * 		playbook-status={state.value}
 * 		stage-order={order}
 * 		stage-status={stageState.value}
 * 		stage-title={title}
 * 		stage-name={name}
 * 	/>
 * </sn-declarative-mediator>
 * ```
 *
 * @seismicElement sn-playbook-stage-actions
 * @summary now-dropdown component that enables the user to take action on a
 * playbook from its stage header.
 */
createCustomElement('sn-playbook-stage-actions', {
	renderer: { type: snabbdom, view },
	properties: {
		/**
		 * the table of playbook parent record
		 * @type {String}
		 */
		table: {
			default: ''
		},
		/**
		 * the sysId of the playbook parent record
		 * @type {String}
		 */
		sysId: {
			default: ''
		},
		/**
		 * the sysId of the running playbook context
		 * @type {String}
		 */
		playbookContextId: {
			default: ''
		},
		/**
		 * the status of the running playbook context
		 * @type {String}
		 */
		playbookStatus: {
			default: ''
		},
		/**
		 * the display order of the stage in the running playbook context
		 * @type {Number}
		 */
		stageOrder: {
			default: 0
		},
		/**
		 * the status of the stage within the running playbook context
		 * @type {String}
		 */
		stageStatus: {
			default: ''
		},
		/**
		 * the title of the stage in the running playbook context
		 * @type {String}
		 */
		stageTitle: {
			default: ''
		},
		/**
		 * the name of the stage in the running playbook context
		 * @type {String}
		 */
		stageName: {
			default: ''
		},
		/**
		 * the opened state of the action dropdown
		 * @type {Boolean}
		 */
		opened: {
			default: false
		},
		dropdownRef: {
			default: null
		}
	},
	eventHandlers: [
		{
			events: ['mousedown', 'click', 'keydown'],
			effect: ({
				action: {
					payload: { event: e }
				}
			}) =>
				e.type !== 'keydown' ||
				e.key === EVENT_KEY.ENTER ||
				e.key === EVENT_KEY.SPACE
					? e.stopPropagation()
					: null
		}
	],
	actionHandlers,
	behaviors: [
		createTransformDeclarativeActionsBehaviorFromModel(
			({
				properties: {
					table,
					sysId,
					playbookContextId,
					playbookStatus,
					stageOrder,
					stageStatus,
					stageTitle,
					stageName
				}
			}) => ({
				table,
				sysId,
				playbook_context_id: playbookContextId,
				playbook_status: playbookStatus,
				stage_order: stageOrder,
				stage_status: stageStatus,
				stage_title: stageTitle,
				stage_name: stageName
			})
		),
		declarativeActionsHeaderClickedBehavior
	]
});
