import '@servicenow/now-button';
import '@servicenow/now-dropdown';
import { t } from 'sn-translate';

const view = ({
	properties: {
		associatedSysId,
		headerIconActions,
		headerDropdownActions,
		declarativeModel,
		formFieldItems,
		showSla,
		slaConfiguration
	}
}) => (
	<div className="sn-playbook-card-header-actions">
		{showSla && associatedSysId ? (
			<now-sla-timer
				className="playbook-sla"
				task={associatedSysId}
				configuration={slaConfiguration}
			/>
		) : null}

		{headerIconActions
			? headerIconActions.map((action) => (
				<now-button-iconic
					className={'action-icon'}
					icon={action.icon}
					disabled={action.disabled}
					tooltipContent={action.tooltip}
					size="sm"
					variant="tertiary"
					bare={true}
					hidePadding={true}
					configAria={{
						button: {
							'aria-label': action.tooltip
						}
					}}
					append-to-payload={{
						action,
						model: {
							...declarativeModel,
							fields: formFieldItems
						}
					}}
				/>
			  ))
			: null}

		{headerDropdownActions.length ? (
			<now-dropdown
				items={headerDropdownActions}
				icon="ellipsis-v-fill"
				size="sm"
				variant="tertiary"
				select="none"
				bare={true}
				hideCaret={true}
				tooltipContent={t('More actions')}
				configAria={{
					'aria-label': t('More actions')
				}}
				className={'card-header-dropdown'}
				append-to-payload={{
					model: {
						...declarativeModel,
						fields: formFieldItems
					}
				}}
			/>
		) : null}
	</div>
);

export default view;
