import cuid from 'cuid';
import get from 'lodash/get';
import getAMBClient from './amb-client';
import {
	createActionTypes,
	createResolvedChannel,
	createSubscribeMessage,
	createSubscriptionCallback,
	createUnsubscribeMessage
} from './effectUtils';

function createRegistryId(containerId, subscriptionId) {
	return `${containerId}-${subscriptionId}`;
}
function createSubscriptionId() {
	return cuid();
}

/*
	ambRegistry = {
		<registryId>: {
			<channelId>: ChannelListener
		}
	}

	TODO: Uptake Seismic createBatchSubscriptionEffect once they've merged our PR with our enhancements
*/
const ambRegistry = new Map();

const cleanAMBRegistry = (ambRegistry, registryId) => {
	if (!ambRegistry || !ambRegistry.has(registryId)) return;

	if (!ambRegistry.get(registryId).size)
		// channelListenerRegistry is empty
		ambRegistry.delete(registryId);
};

const batchMessages = (ambClient, messages = {}) => {
	if (!ambClient || !messages.size) return;

	ambClient.batch(() => {
		messages.forEach((msg) => typeof msg === 'function' && msg());
	});
};

export function ambSubscriptionEffect(
	ambClient,
	subscriptionId,
	ambRegistry,
	options = {},
	coeffects
) {
	const { action, dispatch } = coeffects;
	const actionTypes = createActionTypes(options);
	const containerId = get(action, 'meta.id');
	const unsubscribeAll = get(action, 'payload.unsubscribeAll', false);
	const registryId = createRegistryId(containerId, subscriptionId);

	if (!ambRegistry.has(registryId)) ambRegistry.set(registryId, new Map());

	const channelListenerRegistry = ambRegistry.get(registryId);
	/*
		Using a Map for cases where the collection of 'channelIds' has duplicating
		ids, for example:

			[{
				channelId: "/rw/default/incident/c3lzX2lkPWVkOTJlOGQxNzNkMDIzMDA
							yNzI4NjYwYzRjZjZhN2Jj",
				subscribe: false
			},
			{
				channelId: "/rw/default/incident/c3lzX2lkPWVkOTJlOGQxNzNkMDIzMDA
							yNzI4NjYwYzRjZjZhN2Jj",
				subscribe: true
			}]

		In which case we will take which ever appears last in the collection
	*/
	const messages = new Map();

	if (unsubscribeAll) {
		channelListenerRegistry.forEach((channelListener) => {
			const metaData = {
				...action.meta,
				channelId: channelListener.getName()
			};
			const msg = () =>
				createUnsubscribeMessage(
					channelListener,
					actionTypes,
					metaData,
					dispatch
				);
			messages.set(channelListener, msg);
		});

		channelListenerRegistry.clear();
		batchMessages(ambClient, messages);
		cleanAMBRegistry(ambRegistry, registryId);
		return;
	}

	const encodeURIComponentForChannelId = !(
		options.encodeURIComponentForChannelId === false
	);
	const channelIds = get(action, 'payload.channelIds', []);

	channelIds.forEach(({ channelId, subscribe }) => {
		const shouldSubscribe = subscribe === true;
		const hasChannelListener = channelListenerRegistry.has(channelId);

		if (hasChannelListener) {
			const channelListener = channelListenerRegistry.get(channelId);
			// If ChannelListener exists and the action payload is asking us to
			// subscribe, we don't need to subscribe again
			if (shouldSubscribe) return;
			// If ChannelListener exists and the action payload is asking us to
			// UN-subscribe, we will unsubscribe
			const metaData = {
				...action.meta,
				channelId: channelListener.getName()
			};
			const msg = () =>
				createUnsubscribeMessage(
					channelListener,
					actionTypes,
					metaData,
					dispatch
				);
			messages.set(channelId, msg);
			// Currently 'amb.Channel.unsubscribeFromCometD' does not
			// provide a callback (though 'amb.Channel.subscribeToCometD' does)
			// so that we can be certain that unsubscribing is successful before
			// we remove the existing channelListener
			// https://code.devsnc.com/dev/amb-client-js/blob/de6859671358297004e9099890140b186962bba6/src/amb.Channel.js#L130
			//
			// Although this is possible according to cometd's documentation for
			// version 3.1.12., amb-client-js uses 3.1.4
			// https://docs.cometd.org/current3/reference/#_javascript_subscribe
			// `cometd.unsubscribe(subscription1, additional, function(unsubscribeReply)`
			channelListenerRegistry.delete(channelId);
		} else if (shouldSubscribe) {
			// If no ChannelListener exists for the 'channelId' provided AND
			// should subscribe, create ChannelListener and subscribe
			const cache = {};
			// Only add channelListener to the channelListenerRegistry when the
			// Promise returned from the server is successful
			const resolvedChannel = createResolvedChannel(
				channelId,
				action.payload,
				encodeURIComponentForChannelId
			);
			const metaData = { ...action.meta, channelId: resolvedChannel };
			const registerChannelListener = () => {
				channelListenerRegistry.set(channelId, cache.channelListener);
				ambRegistry.set(registryId, channelListenerRegistry);
			};
			const subscriptionCallback = createSubscriptionCallback(
				dispatch,
				options,
				metaData,
				registerChannelListener
			);
			const channelListener = ambClient.getChannel(resolvedChannel, {
				subscriptionCallback
			});
			const msg = () =>
				createSubscribeMessage(
					channelListener,
					actionTypes,
					metaData,
					dispatch
				);
			messages.set(channelId, msg);
			cache.channelListener = channelListener;
		}
	});

	batchMessages(ambClient, messages);
	cleanAMBRegistry(ambRegistry, registryId);
}

export default function createAmbBatchSubscriptionEffect(options) {
	return {
		effect: ambSubscriptionEffect,
		args: [getAMBClient(), createSubscriptionId(), ambRegistry, options]
	};
}
