import { get } from 'lodash';
import {
	PLAYBOOK_ACTIONS_DROPDOWN_OPENED_SET,
	PLAYBOOK_STAGE_ACTIONS_DROPDOWN_OPENED_SET
} from '../../constants';

export const stageSelectorActionsDropdownBehavior = {
	name: 'stageSelectorActionsDropdownBehavior',
	actionHandlers: {
		[PLAYBOOK_ACTIONS_DROPDOWN_OPENED_SET]: {
			effect: ({ action, updateState }) => {
				const { openedState = false } = get(action, 'payload', {});

				updateState({
					playbookActionsDropdownOpened: openedState
				});
			},
			stopPropagation: true
		},
		[PLAYBOOK_STAGE_ACTIONS_DROPDOWN_OPENED_SET]: {
			effect: ({ action, updateState }) => {
				const { openedState = false, stageName } = get(
					action,
					'payload',
					{}
				);

				updateState({
					openedActionsDropdownByStageName: openedState
						? stageName
						: null
				});
			},
			stopPropagation: true
		}
	}
};
