import { get } from 'lodash';
import {
	PLAYBOOK_REQUEST_FETCH_ACTIVITY_CONTEXTS,
	PLAYBOOK_RW_ADD_AND_REMOVE_WATCHING_RECORDS,
	PLAYBOOK_RW_BATCH_SUBSCRIBE,
	PLAYBOOK_RW_CHANNEL_UPDATE_RECEIVED
} from '../../constants';
import { processWatchingRecords } from '../../effects/amb-batch-subscription';
import createAmbBatchSubscriptionEffect from '../../effects/amb-batch-subscription/ambBatchSubscriptionEffect';
import { constructRWQueryBatchFromRecordsMap, getChannelId } from '../ambUtils';

/*
	'PLAYBOOK_RW_ADD_AND_REMOVE_WATCHING_RECORDS' is the only action that should
	be dispatched from other components. The action payload should include
	properties 'toBeAdded' and/or 'toBeRemoved' in this format:

	{
		'toBeAdded': {
			'incident': [
				'3ca8265bc7670010bfbaf89f51c26033',
				'bca8265bc7670010bfbaf89f51c26033'
			]
		},
		'toBeRemoved': {
			'problem': [
				'51b8265bc7670010bfbaf89f51c260dd'
			]
		}
	}

	Read comments for method 'processWatchingRecords' to see how 'channelId' is
	built and how it works when subscribing and unsubscribing multiple channels
	at the same time
*/
const handlers = {
	[PLAYBOOK_RW_ADD_AND_REMOVE_WATCHING_RECORDS]: ({
		action,
		dispatch,
		state,
		updateState
	}) => {
		const unsubscribeAll = get(action, 'payload.unsubscribeAll', false);
		// If unsubscribeAll, skip all processing
		if (unsubscribeAll) {
			dispatch(PLAYBOOK_RW_BATCH_SUBSCRIBE, { unsubscribeAll });
			return;
		}

		const toBeAddedRecords = get(action, 'payload.toBeAdded', {});
		const toBeRemovedRecords = get(action, 'payload.toBeRemoved', {});
		const watchingRecords = state.watchingRecords || {};

		const {
			nextWatchingRecords,
			toBeSubscribedRecords,
			toBeUnsubscribedRecords
		} = processWatchingRecords(
			watchingRecords,
			toBeAddedRecords,
			toBeRemovedRecords
		);

		if (
			!Object.keys(toBeSubscribedRecords).length &&
			!Object.keys(toBeUnsubscribedRecords).length
		)
			return;

		const toBeSubscribedBatch = constructRWQueryBatchFromRecordsMap(
			toBeSubscribedRecords
		);
		const toBeUnsubscribedBatch = constructRWQueryBatchFromRecordsMap(
			toBeUnsubscribedRecords
		);
		const channelIds = [];
		// Note it's important that 'toBeUnsubscribedBatch' is pushed in BEFORE
		// 'toBeSubscribedBatch', so that if the same 'channelId' exists in both
		// collections, the subscribe wins over the unsubscribe.
		Object.entries(toBeUnsubscribedBatch).forEach(([table, filter]) => {
			channelIds.push({
				channelId: getChannelId(table, filter),
				subscribe: false
			});
		});

		Object.entries(toBeSubscribedBatch).forEach(([table, filter]) => {
			channelIds.push({
				channelId: getChannelId(table, filter),
				subscribe: true
			});
		});

		dispatch(PLAYBOOK_RW_BATCH_SUBSCRIBE, { channelIds });
		updateState({ watchingRecords: nextWatchingRecords });
	},
	[PLAYBOOK_RW_BATCH_SUBSCRIBE]: createAmbBatchSubscriptionEffect({
		messageReceivedActionType: PLAYBOOK_RW_CHANNEL_UPDATE_RECEIVED,
		encodeURIComponentForChannelId: false
		// TODO: on 'subscribeFailedActionType', we can try again to subscribe
		// to the same records. After a maximum number of tries is exceeded,
		// we also need to remove the failed records from 'watchingRecords'.
	}),
	[PLAYBOOK_RW_CHANNEL_UPDATE_RECEIVED]: ({
		action,
		dispatch,
		state,
		updateProperties
	}) => {
		const {
			table_name: tableName,
			sys_id: sysId,
			changes = [],
			operation
		} = get(action, 'payload.data', {});

		const watchingRecords = state.watchingRecords || {};

		if (
			!watchingRecords.hasOwnProperty(tableName) ||
			!watchingRecords[tableName].includes(sysId)
		)
			return;

		const playbooks = get(
			state.properties,
			'playbookContextIdsInOrder',
			[]
		).map((id) => get(state.properties, `playbooksByContextId.${id}`));

		const commentsOnlyUpdate =
			operation === 'update' &&
			changes.length === 1 &&
			changes.includes('comments');
		const workNotesOnlyUpdate =
			operation === 'update' &&
			changes.length === 1 &&
			changes.includes('work_notes');
		const checkBoxOnlyUpdate =
			operation === 'update' &&
			changes.length === 2 &&
			changes.includes('activity_due') &&
			changes.includes('work_notes');

		const contextIdsToUpdate = new Set();
		const { parentTable, parentSysId } = state.properties;

		if (tableName === parentTable && sysId === parentSysId) {
			updateProperties({ parentRecordUpdateTime: Date.now() });
		}

		playbooks.forEach(({ lanes = [] }) => {
			lanes.forEach(({ cards = [] }) => {
				cards.forEach(
					({
						associatedTable,
						associatedRecord,
						recordFieldHasComments,
						contextId,
						activityProperties: {
							experience_status_table: {
								value: statusTable
							} = {},
							experience_status_record: {
								value: statusRecord
							} = {}
						} = {}
					}) => {
						if (
							(associatedTable === tableName &&
								associatedRecord === sysId) ||
							(statusTable === tableName &&
								statusRecord === sysId)
						) {
							const shouldIgnoreUpdate =
								!recordFieldHasComments &&
								(commentsOnlyUpdate ||
									workNotesOnlyUpdate ||
									checkBoxOnlyUpdate);
							if (shouldIgnoreUpdate) return;
							contextIdsToUpdate.add(contextId);
						}
					}
				);
			});
		});

		if (contextIdsToUpdate.size)
			dispatch(PLAYBOOK_REQUEST_FETCH_ACTIVITY_CONTEXTS, {
				contextIds: Array.from(contextIdsToUpdate.values())
			});
	}
};

export default handlers;
