import '@servicenow/now-badge';
import '@servicenow/now-dropdown';
import '@servicenow/now-highlighted-value';
import '@servicenow/now-tooltip';
import { t } from 'sn-translate'; //'@servicenow/library-translate';
import { PANEL_NAVIGATION_UPDATE_MAP } from '../../behaviors/sn-panel-navigation/constants';
import { PLAYBOOK_STANDARD_TOOLTIP_DELAY } from '../../constants';
import '../now-playbook-radial-progress';
import '../sn-playbook-stage-actions';

const stageItem = ({
	progress,
	title,
	name,
	completeActivities,
	totalActivities,
	readyActivities,
	selected,
	disabled,
	actions,
	onStageClicked,
	parentTable,
	parentRecord,
	playbookExperienceId,
	playbookContextId,
	playbookState,
	stageState,
	navKey,
	previousNavKey,
	filteredCardCount,
	actionsDropdownOpened,
	onStageKeyDown,
	dispatch
}) => {
	const stageId = `${playbookContextId}.${name}`;

	return (
		<div
			class={{
				'stage-item': true,
				'is-selected': selected,
				'is-disabled': disabled
			}}
			on-click={
				onStageClicked && typeof onStageClicked === 'function'
					? onStageClicked
					: () => {}
			}
			on-keydown={(e) =>
				onStageKeyDown && typeof onStageKeyDown === 'function'
					? onStageKeyDown(e)
					: () => {}
			}
			tabindex={disabled ? -1 : 0}
			hook-update={(oldNode, { elm }) => {
				if (dispatch && typeof dispatch === 'function') {
					if (elm)
						dispatch(PANEL_NAVIGATION_UPDATE_MAP, {
							mapKey: navKey,
							mapEntry: {
								domRef: disabled ? null : elm,
								up: previousNavKey ? previousNavKey : '',
								right: `stage-actions-${stageId}`
							}
						});

					if (previousNavKey)
						dispatch(PANEL_NAVIGATION_UPDATE_MAP, {
							mapKey: previousNavKey,
							mapEntry: { down: disabled ? '' : navKey } //, operation: 'merge'
						});
				}
			}}
			data-nav={`stage-item-${stageId}`}
			role="listitem"
			aria-selected={selected}
			aria-current={selected}
			aria-label={
				readyActivities
					? t(
						'{0} stage with {1} out of {2} activities complete and {3} activities prioritized',
						title,
						completeActivities,
						totalActivities,
						readyActivities
					  )
					: t(
						'{0} stage with {1} out of {2} activities complete',
						title,
						completeActivities,
						totalActivities
					  )
			}
		>
			<now-playbook-radial-progress progress={progress} />
			<div className={'stage-item-content'}>
				<span className={'now-m-block-start--sm'} />
				<h3
					class={{
						'stage-title': true,
						'now-m-inline-start--sm': true
					}}
					title={title}
				>
					{title}
				</h3>

				<span className={'now-m-block-start--xs'} />
				<label className={'stage-item-complete'}>
					{totalActivities === 0
						? t('No activities available')
						: filteredCardCount === null
							? t(
								'{0} of {1} complete',
								completeActivities,
								totalActivities
						  )
							: t(
								'{0} filtered results',
								filteredCardCount ? filteredCardCount : t('No')
						  )}
				</label>
			</div>
			<now-tooltip
				content={title}
				delay={PLAYBOOK_STANDARD_TOOLTIP_DELAY}
				hook-insert={({ elm }) =>
					(elm.targetRef = elm.previousElementSibling)
				}
			/>

			<now-badge
				value={readyActivities}
				status="info"
				variant="secondary"
				size="sm"
				class={{
					'stage-item-prioritized': true,
					hide: !readyActivities
				}}
				description={t('Prioritized Activities')}
			/>
			<now-tooltip
				content={t('Prioritized Activities')}
				delay={PLAYBOOK_STANDARD_TOOLTIP_DELAY}
				hook-insert={({ elm }) =>
					(elm.targetRef = elm.previousElementSibling)
				}
			/>

			{parentTable &&
			parentRecord &&
			playbookExperienceId &&
			!disabled ? (
					<sn-playbook-stage-actions
						class={{
							'stage-actions': true,
							opened: actionsDropdownOpened,
							hide: !(actions && actions.length)
						}}
						declarative-actions={actions}
						table={parentTable}
						sys-id={parentRecord}
						playbook-context-id={playbookContextId}
						playbook-status={playbookState.value}
						stage-status={stageState.value}
						stage-title={title}
						stage-name={name}
						opened={actionsDropdownOpened}
						config-aria={{
							'aria-label': t('{0} Stage Actions', title)
						}}
					/>
				) : null}
		</div>
	);
};

export default stageItem;
