import { createCustomElement } from '@seismic/core';
import snabbdom from '@seismic/snabbdom-renderer';
import { view } from './view';
import { playbookCardBehavior } from '../../behaviors/playbookCardBehavior';
import { playbookRecordBehavior } from '../../behaviors/playbookRecordBehavior';
import { actionHandlers } from './actions';
import styles from './styles.scss';

/**
 * ## Create Record Card
 *
 * Uses form section component to create a card that allows user to create
 * record
 *
 * @summary Creates a new form within a card in playbook
 * @seismicElement now-playbook-experience-card-create-record
 */
createCustomElement('now-playbook-experience-card-create-record', {
	renderer: {
		type: snabbdom,
		view
	},
	behaviors: [
		{ behavior: playbookCardBehavior },
		{ behavior: playbookRecordBehavior }
	],
	actionHandlers: actionHandlers,
	styles
});
