import { withStopPropagation } from '../../utils';
import { processPlaybook } from '../processingUtils';
import activityAddOrUpdateHandler from './activityAddOrUpdate';
import activityFetchHandler from './activityFetch';
import createActivityLoadingActionHandlers from './activityLoading';
import activitySkipActionHandlers from './activitySkip';
import { ambPlaybookHandlers } from './ambPlaybook';
import ambRecordWatcherHandler from './ambRecordWatcher';
import filterActionHandlers from './filter';
import {
	cleanUpFilter,
	combineSelectedFilters,
	evaluateFilterOnCard,
	reduceSelectedFilters
} from './filterUtils';
import { createGraphQLActionHandlers } from './graphql';
import createLifecycleActionHandlers from './lifecycle';
import playbookActionsHandlers from './playbookActions';
import updateActivityContextActionHandlers from './updateActivityContext';
import { getAMBChannel, setCoeffectsPayload } from './utils';

export default withStopPropagation({
	...activityAddOrUpdateHandler,
	...activityFetchHandler,
	...createActivityLoadingActionHandlers(setCoeffectsPayload),
	...activitySkipActionHandlers,
	...ambPlaybookHandlers,
	...ambRecordWatcherHandler,
	...filterActionHandlers(
		cleanUpFilter,
		combineSelectedFilters,
		evaluateFilterOnCard,
		reduceSelectedFilters
	),
	...createGraphQLActionHandlers(getAMBChannel, processPlaybook),
	...playbookActionsHandlers,
	...createLifecycleActionHandlers(getAMBChannel),
	...updateActivityContextActionHandlers
});
