import { get } from 'lodash';
import { incrementSingleActivityStats } from './';

const getUpdatedLaneStats = (
	activity = {},
	existingActivity = {},
	stats = {}
) => {
	const newStats = { ...stats };
	const activityState = get(activity, 'state.value');

	if (existingActivity) {
		const existingActivityState = get(existingActivity, 'state.value');
		// No state or visibility change
		if (
			existingActivityState === activityState &&
			existingActivity.show === activity.show
		)
			return stats;

		// decrement existing activity's stats if it was visible
		if (existingActivity.show) {
			incrementSingleActivityStats(newStats, existingActivityState, '-');
			if (!activity.show) newStats.totalItems--;
		}

		// Increment new activity's stats if it's visible
		if (activity.show) {
			incrementSingleActivityStats(newStats, activityState, '+');
			if (!existingActivity.show) newStats.totalItems++;
		}

		if (existingActivity.prioritized !== activity.prioritized) {
			if (activity.prioritized) newStats.readyItems++;
			else newStats.readyItems--;
		}

		return newStats;
	}

	// New hidden activity. Nothing to update.
	if (!activity.show) return stats;

	// Increment new activity stats
	incrementSingleActivityStats(newStats, activityState, '+');
	newStats.totalItems++;
	if (activity.prioritized) newStats.readyItems++;

	return newStats;
};

export default getUpdatedLaneStats;
