import {
	controlElementFactory,
	FieldType
} from '@devsnc/sn-glide-form-controls';
import { PLAYBOOK_RECORD_FIELD_UPDATE } from '../../constants';

const onValueChange = (field, dispatch) => {
	return (e, name, value, displayValue) => {
		if (field.value !== value || field.displayValue !== displayValue) {
			let updatedField = {};

			switch (field.dictionary.type) {
				case FieldType.GLIDE_LIST:
					updatedField = {
						...field,
						value,
						displayValue: displayValue.join(', '),
						display_value_list: displayValue
					};
					break;
				default:
					updatedField = {
						...field,
						value,
						displayValue
					};
			}

			if (updatedField.mandatory) {
				updatedField = {
					...updatedField,
					invalid: !updatedField.value
				};
			}

			dispatch(PLAYBOOK_RECORD_FIELD_UPDATE, updatedField);
		}
	};
};

export function renderFormFieldControls(state, dispatch) {
	const { componentId, formFieldItems = [] } = state;

	return (
		<div slot="form" className="field-items-container">
			{formFieldItems.map((field) => {
				const fac = controlElementFactory.getFormControl(
					componentId,
					field.dictionary.type,
					field,
					onValueChange(field, dispatch),
					null
				);
				const { Control, props } = fac;

				if (field.messages) {
					props.messages = field.messages;
				}
				return <Control {...props} />;
			})}
		</div>
	);
}
