import { get } from 'lodash';
import {
	PLAYBOOK_CLEAR_ACTIVITY_LOADING,
	PLAYBOOK_SET_ACTIVITY_LOADING
} from '../../constants';
import { debounce } from '../../effects';

/*
    	loadingActivities = {
        	<contextId>: <indicator>
    	}

	loadingActivities[<contextId>] is a loading indicator, a number where
	indicator > 0 means it warrants a loading status on a Card with <contextId>.
	indicator <= 0 means card is not loading. Depending on card author's
	intentions, a 'loading=true' request is usually followed by a 'loading=false'
	request.
*/

const createActivityLoadingActionHandlers = (setCoeffectsPayload) => {
	const setActivityLoadingEffect = ({ action, updateProperties }) => {
		const requests = get(action, 'payload.requests', []);

		updateProperties(({ properties }) => {
			const loadingActivities = {
				...(properties.loadingActivities || {})
			};

			requests.forEach(({ contextId, loading }) => {
				if (!contextId || typeof loading !== 'boolean')
					return { shouldUpdate: false };

				let indicator = get(loadingActivities, contextId, 0);

				loading ? indicator++ : indicator--;

				indicator > 0
					? (loadingActivities[contextId] = indicator)
					: delete loadingActivities[contextId];
			});

			return {
				path: 'loadingActivities',
				operation: 'set',
				value: loadingActivities
			};
		});
	};

	const clearActivityLoading = ({
		action: { payload },
		updateProperties
	}) => {
		updateProperties(({ properties }) => {
			const contextId = payload.contextId;

			if (!contextId) return { shouldUpdate: false };

			const loadingActivities = {
				...(properties.loadingActivities || {})
			};
			delete loadingActivities[contextId];

			return {
				path: 'loadingActivities',
				operation: 'set',
				value: loadingActivities
			};
		});
	};

	return {
		// Prevent card from flickering due to too many loading requets
		[PLAYBOOK_SET_ACTIVITY_LOADING]: debounce(setActivityLoadingEffect, {
			delay: 200,
			aggregate: (args = [], nextArgs = []) => {
				const idx = nextArgs.length - 1 || 0; // 'coeffects' is the last argument passed to Action Handler
				const oldRequests = get(
					args[idx],
					'action.payload.requests',
					[]
				); // args[0] is 'coeffects'

				const { contextId, loading, requests: newRequests = [] } = get(
					nextArgs[idx],
					'action.payload',
					{}
				); // nextArgs[0] is 'coeffects'

				const newArgs = [...nextArgs];

				newArgs[idx] = setCoeffectsPayload(nextArgs[idx], {
					requests: [
						...oldRequests,
						...(newRequests.length
							? newRequests
							: [{ contextId, loading }])
					]
				});

				return newArgs;
			}
		}),
		[PLAYBOOK_CLEAR_ACTIVITY_LOADING]: clearActivityLoading
	};
};

export default createActivityLoadingActionHandlers;
