import { transformer } from '@devsnc/sn-declarative-actions';

export const createTransformDeclarativeActionsBehavior = (transformer) => (
	computedModel
) => ({
	name: 'transformDeclarativeActionsBehavior',
	properties: {
		declarativeModel: { computed: computedModel },
		declarativeActions: {
			default: []
		},
		transformedDeclarativeActions: {
			computed({
				properties: {
					declarativeActions = [],
					declarativeModel = {}
				} = {}
			}) {
				if (declarativeActions.length === 0) return [];

				const transformedDeclarativeActions = transformer(
					declarativeActions,
					declarativeModel
				);
				return transformedDeclarativeActions;
			}
		}
	}
});

export const createTransformDeclarativeActionsBehaviorFromModel = createTransformDeclarativeActionsBehavior(
	transformer
);
