import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-icon';
import '../../../../components/now-playbook-radial-progress';
import { t } from 'sn-translate';

export const HeaderActivityState = ({ activityState, showActivityState }) => (
	<Fragment>
		{activityState.value === 'CANCELLED' ? (
			<now-icon icon="triangle-exclamation-outline" size="sm" />
		) : activityState.value === 'SKIPPED' ? (
			<now-icon icon="circle-next-outline" size="sm" />
		) : activityState.value === 'PENDING' ? (
			<now-icon icon="change-fill" size="sm" />
		) : activityState.value === 'ERROR' ? (
			<now-icon icon="ban-fill" size="sm" />
		) : (
			<now-playbook-radial-progress
				size={'sm'}
				progress={
					activityState.value === 'IN_PROGRESS'
						? 0.5
						: activityState.value === 'COMPLETE'
							? 1
							: 0
				}
				className={'radial-progress'}
			/>
		)}
		<label
			class={{
				'card-state': true,
				'is-in-progress': activityState.value === 'IN_PROGRESS'
			}}
			title={activityState.displayValue}
			aria-label={t('Activity State: {0}', activityState.displayValue)}
		>
			{showActivityState ? activityState.displayValue : null}
		</label>
	</Fragment>
);
