// Called on filter selection if there's a stale filter option
const cleanUpFilter = (
	filterData = {},
	selectedFilters = {},
	toBeCleanedSelectedFilterOption = {}
) => {
	const newFilterData = { ...filterData };
	const newSelectedFilters = { ...selectedFilters };

	for (const field in toBeCleanedSelectedFilterOption) {
		const toBeCleanedOptions = toBeCleanedSelectedFilterOption[field];

		for (const option in toBeCleanedOptions) {
			const selectedOption = (selectedFilters[field] || {})[option];
			// Remove the option from selectedFilters
			if (selectedOption) {
				let newSelectedFilter = { ...selectedFilters[field] };

				for (const newSelectedOption in newSelectedFilter) {
					if (newSelectedOption === option)
						newSelectedFilter[newSelectedOption] = false;
				}

				newSelectedFilters[field] = newSelectedFilter;
			}
			// If this is the only option, remove category
			if (toBeCleanedOptions[option].removeCategory) {
				delete newFilterData[field];
				continue;
			}
			// If not the only option, remove option from the category
			let newChildren = [...newFilterData[field].children];
			const index = newChildren.findIndex(
				({ label }) => label === option
			);
			newChildren.splice(index, 1);
			newFilterData[field] = {
				...newFilterData[field],
				children: newChildren
			};
		}
	}

	return {
		filterData: newFilterData,
		selectedFilters: newSelectedFilters
	};
};

export default cleanUpFilter;
