import { t } from 'sn-translate';
import { renderSnippet } from '../../render-utils';

export const view = (state) => {
	const {
		properties: { playbookCardProperties },
		heading,
		snippet,
		updated,
		views
	} = state;

	const label = `${t('Updated')} ${updated}  ${views} ${t('views')}`;

	return (
		<sn-playbook-card
			heading={{ label: heading }}
			{...playbookCardProperties}
		>
			{renderSnippet(snippet, 'content')}
			<now-card-footer
				slot="footer"
				className="knowledge-footer"
				label={label}
			/>
		</sn-playbook-card>
	);
};
