import pathToRegexp from 'path-to-regexp';
import {
	AMB_CHANNEL_SUBSCRIBE_FAILED,
	AMB_CHANNEL_SUBSCRIBE_STARTED,
	AMB_CHANNEL_SUBSCRIBE_SUCCEEDED,
	AMB_CHANNEL_UNSUBSCRIBE_SUCCEEDED,
	AMB_MESSAGE_RECEIVED
} from './constants';

function replaceChannelParameters(
	channelId,
	channelParams = {},
	encodeURIComponentForChannelId = true
) {
	const toPath = pathToRegexp.compile(channelId);
	const toPathOptions =
		encodeURIComponentForChannelId === false
			? { encode: (str) => str }
			: {};
	return toPath(channelParams, toPathOptions);
}

export const createResolvedChannel = (
	channelId,
	actionPayload,
	encodeURIComponentForChannelId
) => {
	return replaceChannelParameters(
		channelId,
		actionPayload,
		encodeURIComponentForChannelId
	);
};

export const createActionTypes = (options = {}) => ({
	subscribeStartedActionType:
		options.subscribeStartedActionType || AMB_CHANNEL_SUBSCRIBE_STARTED,
	subscribeSucceededActionType:
		options.subscribeSucceededActionType || AMB_CHANNEL_SUBSCRIBE_SUCCEEDED,
	subscribeFailedActionType:
		options.subscribeFailedActionType || AMB_CHANNEL_SUBSCRIBE_FAILED,
	unsubscribeSucceededActionType:
		options.unsubscribeSucceededActionType ||
		AMB_CHANNEL_UNSUBSCRIBE_SUCCEEDED,
	messageReceivedActionType:
		options.messageReceivedActionType || AMB_MESSAGE_RECEIVED
});

export const createSubscriptionCallback = (
	dispatch,
	actionOptions,
	metaData,
	successCallback
) => {
	// Use setTimeout because of an AMB client subscription timing issue
	return (response) =>
		setTimeout(() => {
			if (response.successful) {
				typeof successCallback === 'function' && successCallback();
				dispatch(
					actionOptions.subscribeSucceededActionType,
					{ response },
					metaData
				);
			} else {
				const { error } = response;
				dispatch(
					actionOptions.subscribeFailedActionType,
					error,
					metaData,
					true
				);
			}
		});
};

export const isSubscribed = (channelListener) =>
	channelListener && channelListener.getCallback() !== undefined;

export const createSubscribeMessage = (
	channelListener,
	actionTypes,
	metaData,
	dispatch
) => {
	if (isSubscribed(channelListener)) return;

	channelListener.subscribe((message) =>
		dispatch(actionTypes.messageReceivedActionType, message, metaData)
	);

	dispatch(actionTypes.subscribeStartedActionType, {}, metaData);
};

export const createUnsubscribeMessage = (
	channelListener,
	actionTypes,
	metaData,
	dispatch
) => {
	if (!isSubscribed(channelListener)) return;

	channelListener.unsubscribe();
	dispatch(actionTypes.unsubscribeSucceededActionType, {}, metaData);
};
