import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { createCustomElement } from '@servicenow/ui-core';
import {
	DECLARATIVE_ACTION,
	NOW_CARD_CLICKED,
	PLAYBOOK_DECLARATIVE_ACTION,
	PLAYBOOK_SHOW_FORM,
	ADD_NOTIFICATIONS,
	PLAYBOOK_CLOSE_FORM_FIELDS_MODAL
} from '../../../constants';
import {
	getMissingMandatoryFields,
	validateMandatoryFields
} from '../../utils';
import { t } from 'sn-translate';

const cardClicked = ({ action: { payload }, dispatch }) => {
	const { action = {}, model } = payload;
	dispatch(action.clickActionType, { action, model });
};

export const view = () => {
	return <slot />;
};

/**
 * ## Playbook Card Mediator
 *
 * Translate PLAYBOOK_DECLARATIVE_ACTION into either
 * PLAYBOOK_SHOW_FORM or DECLARATIVE_ACTION
 * depending on how many form fields are on the card
 *
 * @summary Optimizing form fields rendering
 * @seismicElement sn-playbook-card-mediator
 */
createCustomElement('sn-playbook-card-mediator', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		/**
		 * Declarative Actions selector
		 * @type {Object}
		 */
		actionSelected: {
			selectable: true,
			default: null
		},
		/**
		 * If displaying a form section
		 * @type {boolean}
		 */
		showFormSection: {
			default: false
		}
	},
	actionHandlers: {
		[NOW_CARD_CLICKED]: {
			effect: cardClicked,
			stopPropagation: true
		},
		[PLAYBOOK_DECLARATIVE_ACTION]: ({
			action: {
				payload: { action, model, modalSubmit, maxFormFieldsOnCard }
			},
			dispatch,
			state,
			updateProperties
		}) => {
			const validatedMandatoryFields = validateMandatoryFields(
				model.fields
			);
			const {
				properties: { showFormSection }
			} = state;

			if (action.formFieldsRequired && !modalSubmit) {
				//Stop declarative action and show model
				if (validatedMandatoryFields.length > maxFormFieldsOnCard) {
					dispatch(PLAYBOOK_SHOW_FORM, {
						action,
						model: {
							...model,
							fields: [...validatedMandatoryFields]
						}
					});
					return;
				}
			}

			const missingFieldLabels = getMissingMandatoryFields(
				validatedMandatoryFields
			);
			if (missingFieldLabels.length) {
				if (modalSubmit) dispatch(PLAYBOOK_CLOSE_FORM_FIELDS_MODAL);
				dispatch(ADD_NOTIFICATIONS, {
					notifications: [
						{
							type: 'info',
							message: t(
								'The following mandatory fields are not filled in: {0}',
								missingFieldLabels.join(', ')
							)
						}
					]
				});
				return;
			}

			const actionSelected = {
				action,
				model: {
					...model,
					fields: [...validatedMandatoryFields]
				}
			};

			//Only use the actionSelected property if we need to validate on the form Section
			//Make sure action requires form fields and that the form section is being rendered
			if (action.formFieldsRequired && showFormSection) {
				updateProperties({ actionSelected });
				//let the form section dispatch the declarative action with validated fields
				return;
			}

			dispatch(DECLARATIVE_ACTION, actionSelected);
		}
	}
});
