import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { view } from './view';
import { playbookCardBehavior } from '../../behaviors/playbookCardBehavior';
import { playbookFieldListBehavior } from '../../behaviors/playbookFieldListBehavior';
import {
	LIST_DATA_FETCH_COMPLETE,
	PLAYBOOK_SUFFICIENT_DATA
} from '../../../constants';
import styles from './styles.scss';

const { COMPONENT_PROPERTY_CHANGED, COMPONENT_CONNECTED } = actionTypes;

const hasSufficientData = ({ activityProperties }) => {
	const { list_title = {}, table = {} } = activityProperties;

	// list card requires list table and list title
	return list_title.value && table.value;
};

/**
 * ## List Card
 *
 * Uses simple list component to create a list card
 *
 * @summary Creates a list within a card in playbook
 * @seismicElement now-playbook-experience-card-list
 */
createCustomElement('now-playbook-experience-card-list', {
	renderer: {
		type: snabbdom,
		view,
		transformState(state) {
			const {
				table = { value: '' },
				list_title = { value: '' },
				columns = { value: '' },
				list_query = { value: '' },
				row_count = { value: 0 },
				max_columns = { value: 0 },
				ui_view = { value: '' }
			} = state.properties.activityProperties;

			return {
				...state,
				table: table.value,
				list_title: list_title.value,
				columns: columns.value,
				list_query: list_query.value,
				row_count: parseInt(row_count.value || 0),
				max_columns: parseInt(max_columns.value || 0),
				ui_view: ui_view.value
			};
		}
	},
	behaviors: [
		{ behavior: playbookCardBehavior },
		{
			behavior: playbookFieldListBehavior,
			options: { fieldListProperties: ['record_fields'] }
		}
	],
	actionHandlers: {
		[LIST_DATA_FETCH_COMPLETE]: ({
			action: {
				payload: { count }
			},
			updateState
		}) => {
			updateState({ listResultsCount: count });
		},
		[COMPONENT_CONNECTED]: ({ properties, dispatch }) => {
			dispatch(PLAYBOOK_SUFFICIENT_DATA, {
				hasSufficientData: hasSufficientData(properties)
			});
		},
		[COMPONENT_PROPERTY_CHANGED]: ({
			properties,
			action: {
				payload: { name }
			},
			dispatch
		}) => {
			if (name === 'activityProperties' || name === 'activityState')
				dispatch(PLAYBOOK_SUFFICIENT_DATA, {
					hasSufficientData: hasSufficientData(properties)
				});
		}
	},
	styles
});
