import { t } from 'sn-translate';
import { PANEL_NAVIGATION_UPDATE_MAP } from '../../behaviors/sn-panel-navigation/constants';

const view = (
	{
		properties: {
			transformedDeclarativeActions,
			declarativeModel,
			opened,
			playbookContextId,
			leftNavKey
		}
	},
	{ dispatch }
) => {
	if (transformedDeclarativeActions.length === 0) return null;

	const dropdownActions = transformedDeclarativeActions.map((action) => {
		return {
			...action,
			id: action.assignmentId,
			icon: action.icon || ''
		};
	});

	return (
		<now-dropdown
			items={dropdownActions}
			selectedItems={[]}
			icon="ellipsis-v-outline"
			tooltipContent={t('Playbook Actions')}
			size="md"
			variant="secondary"
			select="none"
			bare={true}
			hideCaret={true}
			append-to-payload={{ model: declarativeModel, playbookContextId }}
			data-nav={`pb-actions-${playbookContextId}`}
			hook-insert={({ elm }) =>
				dispatch && typeof dispatch === 'function' && elm
					? dispatch(PANEL_NAVIGATION_UPDATE_MAP, {
						mapKey: `pb-actions-${playbookContextId}`,
						mapEntry: {
							left: leftNavKey,
							domRef: elm
						}
					  })
					: null
			}
		/>
	);
};

export default view;
