import '@servicenow/now-loader';
import { get, isNil } from 'lodash';
import {
	LOADING_TEXT_DEFAULT,
	PLAYBOOK_ACTIVITY_ANIMATION_END
} from '../../constants';

const view = ({ properties: { loading }, animation }, { dispatch }) => {
	const animationIcon = get(animation, 'icon', '');
	const animationText = get(animation, 'text', '');

	return (
		<div
			class={{
				['loader-layer']: true,
				['sn-playbook-card-loader']: true,
				show: loading || !isNil(animation)
			}}
		>
			<div
				class={{
					['loader']: true,
					['sn-playbook-card-loader-content']: true,
					show: loading || !isNil(animation),
					['is-completed']: !isNil(animation)
				}}
				on-animationend={(e) => {
					if (e.animationName === 'fade-out') {
						dispatch(PLAYBOOK_ACTIVITY_ANIMATION_END);
					}
				}}
			>
				<now-icon
					class={{
						['sn-playbook-card-loader-icon']: true,
						['is-completed']: !isNil(animation),
						['is-hidden']: isNil(animation)
					}}
					icon={animationIcon}
					size="lg"
				/>
				<now-loader
					class={{
						['sn-playbook-card-loader-icon']: true,
						['is-hidden']: !loading
					}}
					size="lg"
				/>

				<span
					class={{
						['is-completed']: !isNil(animation),
						['is-hidden']: isNil(animation)
					}}
				>
					{animationText}
				</span>
				<span
					class={{
						['is-hidden']: !loading
					}}
				>
					{LOADING_TEXT_DEFAULT}
				</span>
			</div>
		</div>
	);
};

export default view;
