import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { preWsConfigSysId } from 'sn-uxpage-presource';
import { daActionHandlers } from './actions';

/**
 * ## Declarative Actions Data Provider
 *
 * Loads declarative actions for a given card by:
 * - Table (associated-table)
 * - SysId (associated-sys-id)
 * - Parent Record Table (input-table)
 * - Parent Record SysId (input-sys-id)
 *
 * Activity Properties:
 * associated-table, associated sys-id, input-table, input-sys-id, declarative-actions(selectable)
 *
 * There is one declarative action data provider for each card.
 *
 * @summary Declarative Action Data Provider for cards.
 * @seismicElement sn-playbook-declarative-actions-data-provider
 */
export default createCustomElement(
	'sn-playbook-declarative-actions-data-provider',
	{
		renderer: {
			type: snabbdom,
			view: () => <slot />
		},
		properties: {
			/**
			 * the table name of the record
			 * @type {String}
			 */
			associatedTable: { default: '' },
			/**
			 * the sys_id of the record
			 * @type {String}
			 */
			associatedSysId: { default: '' },
			/**
			 * the table of the parent record
			 * @type {String}
			 */
			parentTable: { default: '' },
			/**
			 * the sys_id of the parent record
			 * @type {String}
			 */
			parentSysId: { default: '' },
			/**
			 * the view to display record in
			 * @type {String}
			 */
			view: { default: '' },
			/**
			 * Declarative Actions selector
			 * @type {Array}
			 */
			declarativeActions: {
				selectable: true,
				default: []
			},
			/**
			 * We need to reload Declarative Actions when we re-fetch an Activity Context
			 * but only if its Activity Properties or Associated Record has changed, these
			 * are used to evaluate the conditions on a Declarative Action
			 * @type {Object}
			 */
			activityProperties: { default: {} },
			/**
			 * @type {Number}
			 */
			associatedRecordModCount: { default: 0 },
			/**
			 * @type {String}
			 */
			playbookExperienceId: { default: '' },
			/**
			 * @type {String}
			 */
			modelId: { default: '' },

			/**
			 * @type {String}
			 */
			activityDefinitionId: { default: '' },

			/**
			 * @type {Number}
			 */
			parentRecordUpdateTime: { default: 0 }
		},
		initialState: { workspaceConfigId: '' },
		actionHandlers: daActionHandlers,
		behaviors: [preWsConfigSysId]
	}
);
