import { transformer } from '@devsnc/sn-declarative-actions';
import { actionTypes } from '@servicenow/ui-core';
import { get } from 'lodash';
import {
	ADD_NOTIFICATIONS,
	DECLARATIVE_ACTION,
	DECLARATIVE_ACTION_CANCEL_ACTION,
	DECLARATIVE_ACTION_TYPE_SERVER_SCRIPT,
	ELLIPSES,
	MAX_NUM_DAS_BOTTOM_OF_CARD,
	NOW_MODAL_OPENED_SET,
	PLAYBOOK_STATE_VALUES,
	PLAYBOOK_CARD_FORM_FIELDS_REQUIRED,
	PLAYBOOK_CLEAR_ACTIVITY_LOADING,
	PLAYBOOK_CLOSE_FORM_FIELDS_MODAL,
	PLAYBOOK_DECLARATIVE_ACTION,
	PLAYBOOK_REQUEST_FETCH_ACTIVITY_CONTEXTS,
	PLAYBOOK_SET_ACTIVITY_LOADING,
	PLAYBOOK_SHOW_FORM,
	REFRESH_REQUESTED,
	RENDER_MODE_BUTTON,
	RENDER_MODE_CARD_CLICKED,
	RENDER_MODE_HEADER_DROPDOWN,
	RENDER_MODE_HEADER_ICON
} from '../../../../constants';

const {
	COMPONENT_BOOTSTRAPPED,
	COMPONENT_DOM_READY,
	COMPONENT_PROPERTY_CHANGED
} = actionTypes;

const allowedTransformActions = [
	'declarativeActions',
	'declarativeModel',
	'formFieldItems'
];

//perform variable replacement for actionPayload & actionAttributes from model
const transformDeclarativeActions = ({
	action,
	dispatch,
	state,
	updateProperties,
	updateState
}) => {
	const {
		properties: {
			declarativeActions,
			declarativeModel,
			formFieldItems = [],
			configProperties: { maxFormFieldsOnCard }
		}
	} = state;

	if (
		action.type == COMPONENT_PROPERTY_CHANGED &&
		!allowedTransformActions.includes(get(action, 'payload.name'))
	)
		return;

	const evaluatedActions = transformer(declarativeActions, declarativeModel);

	let formFieldsRequired = false,
		formFieldsInModal = false,
		renderBottomButtonsCount = 0;

	const transformedActions = evaluatedActions.reduce(
		(results, action) => {
			//check glide var condition to determine if action is a button, dropdown, or card click action
			const clickActionType = PLAYBOOK_DECLARATIVE_ACTION;
			let label = action.label;

			if (action.formFieldsRequired) {
				formFieldsRequired = true;
				if (formFieldItems.length <= 0) return results;
				if (formFieldItems.length > maxFormFieldsOnCard) {
					formFieldsInModal = true;
					label += ELLIPSES;
				}
			}

			const tempAction = {
				...action,
				label,
				id: action.assignmentId, // id needed for aria
				clickActionType,
				icon: action.icon || ''
			};

			switch (action.renderMode) {
				case RENDER_MODE_BUTTON:
					if (
						++renderBottomButtonsCount > MAX_NUM_DAS_BOTTOM_OF_CARD
					) {
						results.headerDropdownActions.push({
							...tempAction,
							variant: action.buttonType
						});
					} else {
						results.buttonActions.push({
							...tempAction,
							variant: action.buttonType
						});
					}
					break;
				case RENDER_MODE_HEADER_DROPDOWN:
					results.headerDropdownActions.push({
						...tempAction,
						variant: action.buttonType
					});
					break;
				case RENDER_MODE_CARD_CLICKED:
					results.headerIconActions.push({
						...tempAction,
						icon: 'circle-info-outline'
					});
					break;
				case RENDER_MODE_HEADER_ICON:
					results.headerIconActions.push({ ...tempAction });
					break;
				default:
					break;
			}

			return results;
		},
		{
			buttonActions: [],
			headerDropdownActions: [],
			cardClickActions: [],
			headerIconActions: []
		}
	);
	// STRY50525392: Temporarily resorts the card type actions with highest specificity to display last in list.  Will be removed later.
	for (let i = transformedActions.headerIconActions.length - 1; i >= 0; i--) {
		const headerIconAction = transformedActions.headerIconActions[i];
		if (
			headerIconAction.renderMode == RENDER_MODE_CARD_CLICKED &&
			headerIconAction.icon == 'circle-info-outline'
		) {
			transformedActions.headerIconActions.push(
				transformedActions.headerIconActions.splice(i, 1)[0]
			);
		}
	}

	updateProperties({ ...transformedActions });
	// Exit early to prevent extra checks and property updates for
	// 'formFieldsRequired' if there are no Declarative Actions to be evaluated
	if (!evaluatedActions.length) return;

	if (state.formFieldsInModal !== formFieldsInModal)
		updateState({ formFieldsInModal });

	if (state.formFieldsRequired !== formFieldsRequired) {
		updateState({ formFieldsRequired });
		// Exposes 'formFieldsRequired' property to parent components through
		// parent component's Action Handlers
		dispatch(PLAYBOOK_CARD_FORM_FIELDS_REQUIRED, { formFieldsRequired });
	}
};

export const generalActionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: transformDeclarativeActions,
	[COMPONENT_DOM_READY]: ({
		state: {
			properties: { expandOnMountOrChange }
		},
		updateState
	}) => updateState({ expanded: expandOnMountOrChange }),
	[COMPONENT_PROPERTY_CHANGED]: {
		effect: transformDeclarativeActions,
		interceptors: [
			{
				after: ({
					coeffects: {
						action: {
							payload: { name, value }
						},
						state: { expanded },
						updateState
					}
				}) => {
					if (name === 'expandOnMountOrChange' && value && !expanded)
						updateState({ expanded: true });
				}
			}
		]
	},
	[REFRESH_REQUESTED]: {
		effect: ({ dispatch, properties }) => {
			dispatch(PLAYBOOK_CLOSE_FORM_FIELDS_MODAL);
			dispatch(PLAYBOOK_REQUEST_FETCH_ACTIVITY_CONTEXTS, {
				contextIds: [properties.contextId]
			});
			dispatch(PLAYBOOK_SET_ACTIVITY_LOADING, {
				contextId: properties.contextId,
				loading: false
			});
		},
		stopPropagation: true
	},
	[ADD_NOTIFICATIONS]: ({ dispatch, properties }) => {
		dispatch(PLAYBOOK_CLEAR_ACTIVITY_LOADING, {
			contextId: properties.contextId
		});
	},
	[DECLARATIVE_ACTION]: ({ action, dispatch, properties }) => {
		const da = get(action, 'payload.action', {});

		if (da.actionType !== DECLARATIVE_ACTION_TYPE_SERVER_SCRIPT) return;

		dispatch(PLAYBOOK_SET_ACTIVITY_LOADING, {
			contextId: properties.contextId,
			loading: true
		});
	},
	[DECLARATIVE_ACTION_CANCEL_ACTION]: ({ dispatch, properties }) => {
		dispatch(PLAYBOOK_SET_ACTIVITY_LOADING, {
			contextId: properties.contextId,
			loading: false
		});
	},
	[PLAYBOOK_SHOW_FORM]: ({
		action: {
			payload = {},
			payload: { action = {} }
		},
		state: { modalProperties },
		properties: { heading },
		updateState
	}) => {
		updateState({
			modalProperties: {
				...modalProperties,
				modalOpen: !!(payload.value || action),
				modalLinkText: heading,
				modalAction: action
					? {
						...action,
						label:
								action.label && action.label.endsWith(ELLIPSES)
									? action.label.slice(0, -ELLIPSES.length)
									: action.label
					  }
					: {}
			}
		});
	},
	[PLAYBOOK_CLOSE_FORM_FIELDS_MODAL]: ({ updateState }) => {
		updateState({
			path: 'modalProperties.modalOpen',
			value: false,
			operation: 'set'
		});
	},
	[NOW_MODAL_OPENED_SET]: ({ action, dispatch }) => {
		const id = get(action, 'payload.action.id', '');
		const value = get(action, 'payload.value', '');

		if (value == false || id == `${NOW_MODAL_OPENED_SET}_FALSE`)
			dispatch(PLAYBOOK_CLOSE_FORM_FIELDS_MODAL);
	}
};
