import { DEFAULT_MAX_FORM_FIELDS_RENDERED_IN_CARD } from '../../../constants';

const getDefaultConfigProperties = (playbook) => {
	const defaultConfig = {
		showActivityState: true,
		showAutomatedManualIndicator: true,
		maxFormFieldsOnCard: DEFAULT_MAX_FORM_FIELDS_RENDERED_IN_CARD
	};

	const { configProperties = {} } = playbook;

	return { ...defaultConfig, ...configProperties };
};

export default getDefaultConfigProperties;
