import { get, isEmpty } from 'lodash';
import { t } from 'sn-translate';
import { CARD_STATE_VALUES, CARD_STATUS_FILTER_SECTION } from '../constants';

/*
card.filterData = [{name, label, value, displayValue}]
playbook.filterData = {
	${field_name}: {
		{
			name: '',
			label: '',
			id: '',
			children: [
				{
					id: '',
					label: '',
					itemCount: '', //number of matching activity contexts
					filterSection: '' //field_name for this option, required when selection is returned by now-dropdown-panel
				}
			] //Array of filter options
			selected: []
		}
	}

}
playbook.selectedFilter = {
        ${field_name}: {
                ${label}: isSelected
        }
}
playbook.toBeCleanedSelectedFilterOption = {
        ${field_name}: {
                ${label}: { removeCategory: boolean //should the category be removed from filter}
        }
}
*/
export const pushToFilter = (
	state = {},
	card = {},
	initialFilter = {},
	currentUserId = ''
) => {
	const playbookFilterData = get(
		state,
		'playbook.filterData.playbookFilterData',
		{}
	);
	let newFilterData = !isEmpty(playbookFilterData)
		? { ...playbookFilterData }
		: { ...initialFilter };
	const { filterData: cardFilterData = {} } = card;

	// Sometimes when we get AMB updates to refetch an Activity Context, the
	// Activity Context's Associated Table/Record may not have been set, thus
	// resulting in a 'null' 'filterData'
	if (!cardFilterData) return newFilterData;

	for (const cardFilter of cardFilterData) {
		const {
			name: fieldName,
			label: fieldLabel,
			value,
			displayValue: dv
		} = cardFilter;

		let displayValue = '';

		//Apply backend state mapping
		if (fieldName == CARD_STATUS_FILTER_SECTION) {
			if (value == CARD_STATE_VALUES.ERROR.value) continue;
			displayValue = CARD_STATE_VALUES[value].displayValue || dv;
		} else displayValue = value && value === currentUserId ? t('Me') : dv;

		if (!displayValue.trim()) continue;

		const newFilter = newFilterData[fieldName] || {};

		if (newFilter.children) {
			const filterIndex = newFilter.children.findIndex(
				({ label }) => label == displayValue
			);

			if (filterIndex > -1) {
				const children = [...newFilter.children];

				children[filterIndex] = {
					...children[filterIndex],
					itemCount: children[filterIndex].itemCount + 1
				};

				newFilterData[fieldName] = {
					...newFilter,
					children
				};

				continue;
			}
		}

		const filterObject = {
			name: fieldName,
			label: fieldLabel,
			id: fieldName,
			selected: [],
			children: []
		};

		newFilterData[fieldName] = newFilterData[fieldName] || filterObject;

		const currentOption = {
			id: `${fieldName}|${displayValue}`,
			label: displayValue,
			itemCount: 1,
			filterSection: fieldName
		};

		newFilterData[fieldName].children.push(currentOption);
	}

	return newFilterData;
};

// Called on insert/update of activities
export const removeFromFilter = (
	filter = {},
	selectedFilters = {},
	toBeCleanedSelectedFilterOption = {},
	filterData = {}
) => {
	const newFilter = { ...filter };
	const newToBeCleanedSelectedFilterOption = {
		...toBeCleanedSelectedFilterOption
	};

	for (const [fieldName, displayValue] of Object.entries(filterData)) {
		const children = (filter[fieldName] || {}).children;

		if (!children) continue;

		const filterIndex = children.findIndex(
			({ label }) => label == displayValue
		);

		if (filterIndex === -1) continue;

		const isSelectedFilterOption = !!(selectedFilters[fieldName] || {})[
			displayValue
		];
		const oldChildren = filter[fieldName].children;
		const oldChild = oldChildren[filterIndex];
		const { itemCount, label } = oldChild;
		const newOptions = { ...newToBeCleanedSelectedFilterOption[fieldName] };

		if (itemCount !== 1) {
			const newChildren = [...filter[fieldName].children];
			newChildren[filterIndex].itemCount = itemCount - 1;
			newFilter[fieldName].children = newChildren;
			continue;
		}

		// If only option in this category, remove category
		const shouldRemoveCategory = oldChildren.length == 1;
		// If category has more options and this option has only one related record, remove option
		const shouldRemoveOption = oldChildren.length > 1;

		if (!shouldRemoveCategory && !shouldRemoveOption) continue;

		// If the filter is already selected save it to be cleaned up later
		if (isSelectedFilterOption) {
			if (!newToBeCleanedSelectedFilterOption[fieldName])
				newToBeCleanedSelectedFilterOption[fieldName] = {};

			newOptions[label] = {
				removeCategory: shouldRemoveCategory
			};

			newToBeCleanedSelectedFilterOption[fieldName] = newOptions;
			continue;
		}

		if (shouldRemoveCategory) {
			// Handle category removal
			delete newFilter[fieldName];
		} else if (shouldRemoveOption) {
			let newChildren = [...filter[fieldName].children];
			newChildren.splice(filterIndex, 1);
			newFilter[fieldName].children = newChildren;
		}
	}

	return {
		filter: newFilter,
		selectedFilters,
		toBeCleanedSelectedFilterOption: newToBeCleanedSelectedFilterOption
	};
};

/*
 * Depending on the value of 'shouldBeSelected', this method either adds the
 * newly selected filter option to the collection of 'selectedFilters' or
 * removes from it.
 */
export const combineSelectedFilters = (
	selectedFilters = {},
	{ filterSection, label, shouldBeSelected }
) => {
	const combined = { ...selectedFilters };

	if (shouldBeSelected) {
		if (!combined[filterSection]) combined[filterSection] = {};

		combined[filterSection] = {
			...combined[filterSection],
			[label]: shouldBeSelected
		};
	} else if ((combined[filterSection] || {})[label]) {
		delete combined[filterSection][label];
	}

	return combined;
};

export const reduceSelectedFilters = (selectedFilters = {}) => {
	let selectedFilterCount = 0;
	const reducedSelectedFilter = {};

	Object.keys(selectedFilters).forEach((filter) => {
		const filterSection = selectedFilters[filter];
		const newFilter = [];

		Object.keys(filterSection).forEach((label) => {
			if (filterSection[label] !== true) return;

			selectedFilterCount++;
			newFilter.push(label);
		});

		if (newFilter.length)
			reducedSelectedFilter[filter] = newFilter.join('|');
	});

	return {
		reducedSelectedFilter,
		selectedFilterCount
	};
};

export const evaluateFilterOnCard = (reducedSelectedFilter = {}, card = {}) => {
	let match = null,
		shouldExpand = null,
		hidden = false;
	const cardFilterData = card.filterData;

	for (const [fieldName, filterValues] of Object.entries(
		reducedSelectedFilter
	)) {
		if (!filterValues) {
			shouldExpand = true;
			hidden = false;
			match = null;
			continue;
		}

		if (isEmpty(cardFilterData)) {
			hidden = true;
			match = null;
			break;
		}

		if (!cardFilterData[fieldName]) {
			hidden = true;
			match = null;
			break;
		}

		const exp = new RegExp(filterValues);
		match = cardFilterData[fieldName].match(exp);
		if (match == null) {
			hidden = true;
			break;
		}
	}

	if (match != null) {
		shouldExpand = true;
		hidden = false;
	}

	return {
		...card,
		...(shouldExpand !== null && { shouldExpand }),
		hidden,
		defer: !shouldExpand && !card.prioritized
	};
};

// Called on filter selection if there's a stale filter option
export const cleanUpFilter = (
	filterData = {},
	selectedFilters = {},
	toBeCleanedSelectedFilterOption = {}
) => {
	const newFilterData = { ...filterData };
	const newSelectedFilters = { ...selectedFilters };

	for (const field in toBeCleanedSelectedFilterOption) {
		const toBeCleanedOptions = toBeCleanedSelectedFilterOption[field];

		for (const option in toBeCleanedOptions) {
			const selectedOption = (selectedFilters[field] || {})[option];
			// Remove the option from selectedFilters
			if (selectedOption) {
				let newSelectedFilter = { ...selectedFilters[field] };

				for (const newSelectedOption in newSelectedFilter) {
					if (newSelectedOption === option)
						newSelectedFilter[newSelectedOption] = false;
				}

				newSelectedFilters[field] = newSelectedFilter;
			}
			// If this is the only option, remove category
			if (toBeCleanedOptions[option].removeCategory) {
				delete newFilterData[field];
				continue;
			}
			// If not the only option, remove option from the category
			let newChildren = [...newFilterData[field].children];
			const index = newChildren.findIndex(
				({ label }) => label === option
			);
			newChildren.splice(index, 1);
			newFilterData[field] = {
				...newFilterData[field],
				children: newChildren
			};
		}
	}

	return {
		filterData: newFilterData,
		selectedFilters: newSelectedFilters
	};
};
