import { renderNowLabelValue } from './renderNowLabelValue';

export function renderFieldList(state, slot = 'content', fieldListProperty) {
	const items = getFieldList(state, fieldListProperty);

	if (!items) return null;

	const opts = { slot, className: 'field-list-container' };

	return renderNowLabelValue(items, opts);
}

export function getFieldList(state, fieldListProperty) {
	if (!fieldListProperty) return null;

	const { fieldListItems = {} } = state;
	if (!fieldListItems.hasOwnProperty(fieldListProperty)) return null;

	const { [fieldListProperty]: items = [] } = fieldListItems;

	return items.length ? items : null;
}
