import '@servicenow/now-template-message';

const templateMessageHeight = '535px';

const emptyState = ({
	title,
	text,
	level = 3,
	alignment = 'vertical-centered',
	illustration = 'add-attachment',
	actions = []
}) => (
	<now-template-message-empty-state
		alignment={alignment}
		heading={{ label: title, level }}
		content={text}
		illustration={illustration}
		actions={actions}
		style={
        	    alignment === 'horizontal' ? null :
				{
	                position: 'relative',
	                height: alignment === 'horizontal' ? 'auto' : templateMessageHeight,
	                top: alignment === 'horizontal' ? 0 : `calc(50% - 0.5 * ${templateMessageHeight})`,
	                overflow: 'visible'
				}
		}
	/>
);

export default emptyState;
