import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import { view } from './view';
import styles from './styles.scss';
import actionHandlers from './actions';
import { playbookCardBehavior } from '../../behaviors/playbookCardBehavior';

/**
 * ## Knowledge Card
 *
 * Using the knowledge table and knowledge record,
 * show a knowledge article in a card.
 *
 * @summary Playbook card component to render knowledge article excerpt.
 * @seismicElement now-playbook-experience-card-knowledge
 */
createCustomElement('now-playbook-experience-card-knowledge', {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {
		knowledge: {},
		tagline: { label: t('Article'), icon: 'document-outline' },
		heading: '',
		snippet: '',
		updated: '',
		views: ''
	},
	properties: {
		/** @type {String} **/
		knowledgeTable: {
			computed: (state) => {
				const { knowledge_table } = state.properties.activityProperties;
				return knowledge_table ? knowledge_table.value : '';
			}
		},
		/** @type {String} **/
		knowledgeRecord: {
			computed: (state) => {
				const {
					knowledge_record
				} = state.properties.activityProperties;
				return knowledge_record ? knowledge_record.value : '';
			}
		}
	},
	behaviors: [{ behavior: playbookCardBehavior }],
	actionHandlers,
	styles
});
