import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { preUserData } from 'sn-uxpage-presource';
import styles from './styles.scss';
import { view } from './view.js';

const { COMPONENT_BOOTSTRAPPED } = actionTypes;

/**
 * ## Playbook Experience
 *
 * @summary A single playbook execution for a given playbook context record.
 * @seismicElement now-playbook-experience
 */
createCustomElement('now-playbook-experience', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		/**
		 * sysId of the parent record associated with the playbooks
		 * @type {String}
		 */
		parentSysId: {
			reflect: true
		},
		/**
		 * table name of the parent record associated with the playbooks
		 * @type {String}
		 */
		parentTable: {
			reflect: true
		},
		/**
		 * playbook experience id to determine the configurations
		 * @type {String}
		 */
		playbookExperienceId: {},
		/**
		 * workspace current user id
		 * @type {String}
		 */
		workspaceUserId: { default: '' },
		/**
		 * optional boolean for Compact Mode layout
		 * @type {boolean}
		 */
		compactMode: { default: false }
	},
	styles,
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({ state, updateProperties }) => {
			const workspaceUserId = preUserData.getPreSource(state).sys_id; //preWsConfigSysId.getPreSource(state) only in sn-playbook-declarative-actions-data-provider

			updateProperties({ workspaceUserId });
		}
	},
	behaviors: [preUserData]
});
