import { actionTypes } from '@servicenow/ui-core';
import {
	ANIMATE_CARD_STATE_CHANGES,
	CARD_STATE_VALUES,
	PLAYBOOK_ACTIVITY_ANIMATION_END,
	PLAYBOOK_CARD_VIEW_MODE_CHANGE
} from '../../constants';

const { COMPONENT_PROPERTY_CHANGED } = actionTypes;

const actionHandlers = {
	[PLAYBOOK_ACTIVITY_ANIMATION_END]: ({ dispatch, updateState }) => {
		updateState({ animation: null });

		dispatch(PLAYBOOK_CARD_VIEW_MODE_CHANGE, { expanded: false });
	},
	[COMPONENT_PROPERTY_CHANGED]: ({ action, updateState }) => {
		if (
			action.payload.name === 'activityState' &&
			action.payload.previousValue.value ===
				CARD_STATE_VALUES.IN_PROGRESS.value &&
			Object.keys(ANIMATE_CARD_STATE_CHANGES).includes(
				action.payload.value.value
			)
		) {
			updateState({
				animation:
					ANIMATE_CARD_STATE_CHANGES[action.payload.value.value]
			});
		}
	}
};

export default actionHandlers;
