import {
	DECLARATIVE_ACTION,
	NOW_BUTTON_ICONIC_CLICKED,
	NOW_DROPDOWN_ITEM_CLICKED
} from '../../constants';

const headerClicked = {
	effect: ({ action: { payload }, dispatch }) => {
		const { action, item, model } = payload;

		if (item && !item.disabled) {
			dispatch(DECLARATIVE_ACTION, { action: item, model });
		} else if (action && !action.disabled) {
			dispatch(DECLARATIVE_ACTION, { action, model });
		}
	},
	stopPropagation: true
};

export const declarativeActionsHeaderClickedBehavior = {
	name: 'declarativeActionsHeaderClickedBehavior',
	actionHandlers: {
		[NOW_DROPDOWN_ITEM_CLICKED]: headerClicked,
		[NOW_BUTTON_ICONIC_CLICKED]: headerClicked
	}
};
