import rtlBehavior from '@servicenow/behavior-rtl';
import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import {
	snPanelNavigationBehavior,
	stageSelectorActionsDropdownBehavior
} from '../behaviors';
import {
	PANEL_NAVIGATION_SWITCH_FOCUS,
	PANEL_NAVIGATION_TAKE_FOCUS
} from '../behaviors/sn-panel-navigation/constants';
import {
	NOW_DROPDOWN_CUSTOM_TARGET_OPENED_SET,
	PLAYBOOK_STAGE_LIST_EXPANDED_SET,
	PLAYBOOK_STAGE_SELECTOR_SOURCE
} from '../constants';
import styles from './styles.scss';
import view from './view';

const { COMPONENT_PROPERTY_CHANGED, COMPONENT_BOOTSTRAPPED } = actionTypes;

createCustomElement('now-playbook-stage-selector', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	initialState: {
		expandedStageListsByContextId: {},
		playbookActionsDropdownOpened: false,
		openedActionsDropdownByStageName: null,
		onFocusDispatchId: { right: PANEL_NAVIGATION_SWITCH_FOCUS },
		dispatchSource: PLAYBOOK_STAGE_SELECTOR_SOURCE
	},
	properties: {
		/** @type {[]} **/
		playbookContextIdsInOrder: { default: [] },
		/** @type {} **/
		playbooksByContextId: { default: {} },
		/** @type {String} **/
		playbookExperienceId: { default: '' },
		/** @type {} */
		selectedStage: {
			default: {
				playbookContextId: '',
				stageName: ''
			}
		},
		/** @type {String} **/
		userId: {
			default: ''
		},
		/** @type {Boolean} **/
		compactMode: { default: false },
		/** @type {String} **/
		defaultExpandedPlaybook: {
			default: ''
		},
		/** @type {Number} **/
		parentRecordUpdateTime: { default: 0 }
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({
			properties: { compactMode },
			updateState
		}) => {
			if (compactMode) updateState({ onFocusDispatchId: { right: '' } });
		},
		[COMPONENT_PROPERTY_CHANGED]: ({
			action: {
				payload: { name, value }
			},
			updateState
		}) => {
			if (name === 'playbookContextIdsInOrder' && value && value[0])
				updateState({ focusNavId: `pb-heading-${value[0]}` });
			else if (
				name === 'selectedStage' &&
				value.playbookContextId &&
				value.stageName
			)
				updateState({
					focusNavId: `stage-item-${value.playbookContextId}.${value.stageName}`
				});
		},
		[PLAYBOOK_STAGE_LIST_EXPANDED_SET]: ({
			state,
			action: {
				payload: { playbookContextId, updatedExpandState }
			},
			updateState
		}) =>
			updateState({
				expandedStageListsByContextId: {
					...state.expandedStageListsByContextId,
					[playbookContextId]: updatedExpandState
				},
				playbookActionsDropdownOpened: false,
				openedActionsDropdownByStageName: null,
				focusNavId:
					updatedExpandState && state.focusNavId
						? state.focusNavId
						: `pb-heading-${playbookContextId}`
			}),
		[NOW_DROPDOWN_CUSTOM_TARGET_OPENED_SET]: ({
			action: {
				payload: { value }
			},
			dispatch
		}) => {
			if (!value) dispatch(PANEL_NAVIGATION_TAKE_FOCUS);
		}
	},
	behaviors: [
		stageSelectorActionsDropdownBehavior,
		snPanelNavigationBehavior,
		rtlBehavior
	]
});
