const clone = (dest, source) => {
	for (let prop in source) {
		if (Object.prototype.hasOwnProperty.call(source, prop))
			dest[prop] = source[prop];
	}
	return dest;
};

const wrapClient = (client, clientWindow) => {
	if (typeof client.getClientWindow !== 'undefined') {
		const context = client.getClientWindow();

		// we don't need to wrap the client
		if (context === clientWindow) {
			return client;
		}
	}

	const wrappedClient = clone({}, client);
	wrappedClient.getChannel = function (
		channelName,
		subscriptionConfig,
		overrideWindow
	) {
		return client.getChannel(
			channelName,
			subscriptionConfig,
			overrideWindow || clientWindow
		);
	};
	wrappedClient.getRecordWatcherChannel = function (
		table,
		query,
		actionPrefix,
		subscriptionConfig,
		overrideWindow
	) {
		return client.getRecordWatcherChannel(
			table,
			query,
			actionPrefix,
			subscriptionConfig,
			overrideWindow || clientWindow
		);
	};
	wrappedClient.subscribeToEvent = function (
		event,
		callback,
		overrideWindow
	) {
		return client.subscribeToEvent(
			event,
			callback,
			overrideWindow || clientWindow
		);
	};
	wrappedClient.unsubscribeFromEvent = function (id, overrideWindow) {
		return client.unsubscribeFromEvent(id, overrideWindow || clientWindow);
	};
	wrappedClient.getClientWindow = function () {
		return clientWindow;
	};
	return wrappedClient;
};

const getParentAmbClient = () => {
	let clientWindow = window;
	do {
		if (clientWindow.g_ambClient) return clientWindow.g_ambClient;
		clientWindow = clientWindow.parent;
	} while (clientWindow !== clientWindow.parent);

	// eslint-disable-next-line no-console
	console.error('no amb client found');
};

let ambClient;
const getAMBClient = function () {
	if (!ambClient) {
		let client = getParentAmbClient(window);
		ambClient = wrapClient(client, window);
	}
	return ambClient;
};

export default getAMBClient;
