import { CANCEL_PLAYBOOK_CONFIRMED, NOW_MODAL_OPENED_SET } from '../constants';

export default {
	[NOW_MODAL_OPENED_SET]: {
		effect: ({ state, updateState }) => {
			const { cancellationReason } = state;

			if (cancellationReason) {
				updateState({ cancellationReason: '' });
			}
		}
	},
	[CANCEL_PLAYBOOK_CONFIRMED]: {
		effect: ({ state, updateState }) => {
			const { cancellationReason } = state;

			if (cancellationReason) {
				updateState({ cancellationReason: '' });
			}
		}
	}
};
