import '@servicenow/now-record-list-connected';
import { renderFieldList } from '../../render-utils';

export const view = (state) => {
	const {
		properties: { playbookCardProperties },
		table = '',
		list_title = '',
		columns = '',
		list_query = '',
		row_count = 0,
		max_columns = '',
		ui_view = '',
		listResultsCount = 0,
		associatedRecordUpdated
	} = state;

	const hidePagination = !(
		listResultsCount &&
		row_count &&
		row_count < listResultsCount
	);
	const refreshRequested = associatedRecordUpdated
		? {
			timestamp: associatedRecordUpdated,
			background: false
		  }
		: null;

	return (
		<sn-playbook-card {...playbookCardProperties}>
			{renderFieldList(state, 'content', 'record_fields')}
			<now-record-list-connected
				slot="content"
				className="record-list-container"
				listType="RELATED"
				listTitle={list_title}
				variantHeader={{ size: 'sm', headingLevel: 3 }}
				table={table}
				columns={columns}
				query={list_query}
				limit={row_count}
				max-columns={max_columns}
				view={ui_view}
				hide-pagination={hidePagination}
				hide-limit-selector={true}
				hide-view-all={true}
				hide-cell-filter={true}
				hide-row-selector={true}
				hide-filter-panel={true}
				hide-last-refreshed-text={true}
				hide-menu-button={true}
				hide-declarative-actions={true}
				hide-panel={true}
				hide-quick-edit={true}
				hide-column-filtering={true}
				hide-column-grouping={true}
				{...(refreshRequested && { refreshRequested })}
			/>
		</sn-playbook-card>
	);
};
