import { CARD_STATE_VALUES } from '../../../constants';

const incrementSingleActivityStats = (
	laneStats = {},
	activityState = null,
	operation = '+'
) => {
	if (activityState === CARD_STATE_VALUES.SKIPPED.value)
		operation === '+' ? laneStats.skippedItems++ : laneStats.skippedItems--;
	if (activityState === CARD_STATE_VALUES.COMPLETE.value)
		operation === '+'
			? laneStats.completedItems++
			: laneStats.completedItems--;
};

export default incrementSingleActivityStats;
