import { uniq } from 'lodash';

export const uniqDifferenceString = (source = [], target = []) => {
	const targetSet = new Set(target);
	return source.filter((str) => !targetSet.has(str));
};

export const constructRWFilter = (filters = {}, logic = '&') => {
	const q = [];
	for (const field in filters) {
		// Deduplication
		const values = uniq(filters[field]);
		// For Record Watcher, when there are multiple values we have to use
		// the '^OR<field>=' format to seperate values rather than comma
		// seperated format
		q.push(`${field}=${values.join(`^OR${field}=`)}`);
	}
	// Filters are base64 encoded with the '=' sign replaced with '-'
	return btoa(q.join(logic)).replace(/=/g, '-');
};

/*
	This util function helps components generate which records, and in return
	help generate which channelIds, that the component needs to
	subscribe/unsubscribe given that they are saving their 'watchingRecords'
	in the state with the format below.

	The order of the collections of sysIds in the state's 'watchingRecords' is
	very important. The order in which the sysIds are saved will be used to
	generate the unique 'channelId' for Record Watcher. The collections of
	sysIds from 'watchingRecords' look like follows:

		{
			'incident': [
				'ed92e8d173d023002728660c4cf6a7bc',
				'd71b3b41c0a8016700a8ef040791e72a'
			],
			'problem': [
				'6632130c730123002728660c4cf6a734'
			]
		}

	Partially base64 encoded 'channelId' for the 'incident' table would be:

	'/rw/default/incident/c3lzX2lkPWVkOTJlOGQxNzNkMDIzMDAyNzI4NjYwYzRjZjZhN2JjX
	k9Sc3lzX2lkPWQ3MWIzYjQxYzBhODAxNjcwMGE4ZWYwNDA3OTFlNzJh'

	Let's say we want to remove the existing record with the sysId
	'd71b3b41c0a8016700a8ef040791e72a' from Record Watcher.

	First we need to unsubscribe to the previous 'channelId' above then generate
	a brand new 'channelId' to subscribe to. So what we send to Record Watcher
	would be two messages:

	1. calling unsubscribe on the Channel Listener of the above previous 'channelId'
	2. calling subscribe on a new Channel Listener with a new 'channelId'

	The previous 'channelId' can be reconstructed given that the collection of
	sysIds for the 'incident' table are the same as before and in the same order.

	So when we remove the existing sysId 'd71b3b41c0a8016700a8ef040791e72a'
	our unsubscribe message would look like:

		{
			channelId: "/rw/default/incident/c3lzX2lkPWVkOTJlOGQxNzNkMDIzMDAyNzI
				    4NjYwYzRjZjZhN2JjXk9Sc3lzX2lkPWQ3MWIzYjQxYzBhODAxNjcwMGE
				    4ZWYwNDA3OTFlNzJh",
			subscribe: false
		}

	And our subscribe message would look like

		{
			channelId: "/rw/default/incident/c3lzX2lkPWVkOTJlOGQxNzNkMDIzMDAyNzI
				    4NjYwYzRjZjZhN2Jj",
			subscribe: true
		}

	And after the subscriptions are dispatched 'watchingRecords' would be reduced to:

		{
			'incident': [
				'ed92e8d173d023002728660c4cf6a7bc'
			],
			'problem': [
				'6632130c730123002728660c4cf6a734'
			]
		}
*/
export const processWatchingRecords = (
	watchingRecords = {},
	toBeAddedRecords = {},
	toBeRemovedRecords = {}
) => {
	const nextWatchingRecords = { ...watchingRecords };
	const toBeSubscribedRecords = {};
	const toBeUnsubscribedRecords = {};

	Object.entries(toBeRemovedRecords).forEach(([table, newSysIds]) => {
		// Important that 'oldSysIds' is looked up from 'watchingRecords' and
		// NOT 'nextWatchingRecords' because it can change
		const oldSysIds = watchingRecords[table];
		// Only calculate if there are existing sysIds with the same table,
		// otherwise there is nothing to be removed because we are not watching
		// any records from that table
		if (oldSysIds) {
			// Setting it to all of the 'oldSysIds' not because all of the
			// records need to be UN-subscribed but because the 'oldSysIds' is
			// needed to recalculate the previous 'channelId', see comments at
			// the beginning of this method
			toBeUnsubscribedRecords[table] = oldSysIds;
			// The difference here is the 'sysIds' that we are NOT going to
			// UN-subscribe
			const d = uniqDifferenceString(oldSysIds, newSysIds);

			if (d.length) {
				// So if there are differences we want to re-subscribe to those
				// leftover 'sysIds'
				toBeSubscribedRecords[table] = d;
				nextWatchingRecords[table] = d;
			} else {
				// If there are no differences it means that in the current
				// state's 'watchingRecords', all of the 'sysIds' with this
				// table will be UN-subscribed, leaving nothing so it's safe
				// to delete the table key from the state's 'watchingRecords'
				delete nextWatchingRecords[table];
			}
		}
	});

	Object.entries(toBeAddedRecords).forEach(([table, newSysIds]) => {
		// Important that 'oldSysIds' is looked up from 'watchingRecords' and
		// NOT 'nextWatchingRecords' because it can change
		const oldSysIds = watchingRecords[table];

		if (oldSysIds) {
			let u;
			// Since this loop runs after above the above loop where we go
			// through 'toBeRemovedRecords', this table could have been processed
			// already and there could be a previous collection of 'sysIds' that
			// are already added to 'toBeSubscribedRecords', so in this case we
			// want to combine those 'sysIds' with the 'newSysIds'
			//
			// We don't need to combine with 'oldSysIds' because if the same
			// table is already processed in the above loop, then those
			// 'oldSysIds' would have already been captured at
			// 'toBeSubscribedRecords[table]'
			if (toBeSubscribedRecords.hasOwnProperty(table)) {
				u = uniq([...toBeSubscribedRecords[table], ...newSysIds]);
			} else {
				// If this table has not been processed then we don't have to
				// worry about a previous collection of 'sysIds' that may be
				// saved into 'toBeSubscribedRecords', we can simply combine
				// 'oldSysIds' with 'newSysIds'. We DO, however, have to
				// make sure that none of the 'oldSysIds' are ones that we are
				// actually trying to UN-subscribe ('toBeUnsubscribedRecords'),
				// so we don't accidentally subscribe to 'sysIds' that we won't
				// care about.
				let filteredOldSysIds = oldSysIds;

				if (toBeUnsubscribedRecords.hasOwnProperty(table))
					filteredOldSysIds = uniqDifferenceString(
						oldSysIds,
						toBeUnsubscribedRecords[table]
					);

				u = uniq([...filteredOldSysIds, ...newSysIds]);
			}

			if (!u.length) return;

			toBeSubscribedRecords[table] = u;
			// This is used to recalculate the previous 'channelId', see comments
			// at the beginning of this method
			toBeUnsubscribedRecords[table] = oldSysIds;
			nextWatchingRecords[table] = u;
		} else {
			if (!newSysIds.length) return;
			// If no 'oldSysIds' exist with the same table, then we simply
			// subscribe to all of 'newSysIds'
			const u = uniq(newSysIds);
			toBeSubscribedRecords[table] = u;
			nextWatchingRecords[table] = u;
		}
	});

	return {
		nextWatchingRecords,
		toBeSubscribedRecords,
		toBeUnsubscribedRecords
	};
};
