import rtlBehavior from '@servicenow/behavior-rtl';
import '@servicenow/now-loader';
import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import {
	PANEL_NAVIGATION_SET_FOCUS_ANCHOR,
	PANEL_NAVIGATION_SWITCH_FOCUS
} from '../behaviors/sn-panel-navigation/constants';
import EmptyState from '../components/empty-state';
import '../components/now-playbook-stage-panel';
import {
	LANE_STATE_VALUES,
	LOADING_TEXT_DEFAULT,
	PLAYBOOK_STAGE_CLICKED,
	PLAYBOOK_STAGE_DESELECT,
	PLAYBOOK_STAGE_PANEL_SOURCE,
	PLAYBOOK_STAGE_SELECTION_UPDATE,
	PLAYBOOK_STAGE_SELECTOR_SOURCE,
	PLAYBOOK_STATE_VALUES
} from '../constants';
import { NOTIFICATION_NO_PLAYBOOKS_AVAILABLE } from '../notification.constants';
import '../now-playbook-stage-selector';
import styles from './styles.scss';

const { COMPONENT_PROPERTY_CHANGED, COMPONENT_BOOTSTRAPPED } = actionTypes;

const updateSelectedStageCallback = ({
	state: { defaultExpandedPlaybook, selectedStage },
	properties: { compactMode, playbookContextIdsInOrder, playbooksByContextId }
}) => {
	const hasSufficientPlaybookData =
		Array.isArray(playbookContextIdsInOrder) &&
		playbookContextIdsInOrder.length > 0;
	if (!hasSufficientPlaybookData) return { shouldUpdate: false };

	const shouldResetSelectedStage =
		selectedStage.playbookContextId &&
		!playbookContextIdsInOrder.some(
			(id) => id === selectedStage.playbookContextId
		);

	const shouldSetSelectedStage =
		!compactMode &&
		(!selectedStage.playbookContextId ||
			!selectedStage.stageName ||
			shouldResetSelectedStage);
	const shouldSetDefaultExpandedPlaybook =
		!defaultExpandedPlaybook ||
		!playbookContextIdsInOrder.some((id) => id === defaultExpandedPlaybook);

	if (!shouldSetSelectedStage && !shouldSetDefaultExpandedPlaybook)
		return { shouldUpdate: false };

	const updateValue = {};

	const firstPlaybook = playbooksByContextId[playbookContextIdsInOrder[0]];
	const firstInProgressPlaybookId = playbookContextIdsInOrder.find(
		(id) =>
			playbooksByContextId[id].state.value ===
			PLAYBOOK_STATE_VALUES.IN_PROGRESS
	);
	const playbook = firstInProgressPlaybookId
		? playbooksByContextId[firstInProgressPlaybookId]
		: firstPlaybook;

	if (shouldSetDefaultExpandedPlaybook)
		updateValue.defaultExpandedPlaybook = playbook.sysId;

	if (shouldSetSelectedStage) {
		const firstStage = playbook.lanes[0];
		const stage =
			playbook.lanes.find(
				({ state }) =>
					state.value === LANE_STATE_VALUES.IN_PROGRESS.value
			) || firstStage;
		updateValue.selectedStage = {
			playbookContextId: playbook.sysId,
			stageName: stage.name
		};
	} else if (shouldResetSelectedStage)
		updateValue.selectedStage = {
			playbookContextId: '',
			stageName: ''
		};

	return {
		path: '',
		operation: 'assign',
		value: updateValue
	};
};

createCustomElement('now-playbook-panel-layout', {
	renderer: {
		type: snabbdom,
		view: (
			{
				defaultExpandedPlaybook,
				selectedStage,
				nextSelectedStage,
				properties: {
					compactMode,
					playbookContextIdsInOrder,
					playbooksByContextId,
					loading,
					loadingActivities,
					playbookExperienceId,
					userId,
					parentRecordUpdateTime
				},
				componentId
			},
			{ dispatch }
		) => {
			if (loading)
				return (
					<div className="playbook-loading">
						<now-loader size="lg" />
						<span>{LOADING_TEXT_DEFAULT}</span>
					</div>
				);

			if (playbookContextIdsInOrder.length === 0) {
				return (
					<EmptyState
						title={NOTIFICATION_NO_PLAYBOOKS_AVAILABLE.header}
						text={NOTIFICATION_NO_PLAYBOOKS_AVAILABLE.body}
						illustration={
							NOTIFICATION_NO_PLAYBOOKS_AVAILABLE.illustration
						}
					/>
				);
			}

			return (
				<div
					class={{ 'playbook-container': true, compact: compactMode }}
				>
					<nav class={{ compact: compactMode }}>
						<now-playbook-stage-selector
							playbook-context-ids-in-order={
								playbookContextIdsInOrder
							}
							playbooks-by-context-id={playbooksByContextId}
							playbook-experience-id={playbookExperienceId}
							selectedStage={
								nextSelectedStage
									? nextSelectedStage
									: selectedStage
							}
							user-id={userId}
							compact-mode={compactMode}
							defaultExpandedPlaybook={defaultExpandedPlaybook}
							parentRecordUpdateTime={parentRecordUpdateTime}
						/>
					</nav>
					<section
						class={{
							compact: compactMode,
							active:
								selectedStage.playbookContextId &&
								selectedStage.stageName,
							leaving:
								nextSelectedStage &&
								!nextSelectedStage.playbookContextId &&
								!nextSelectedStage.stageName
						}}
						on-animationend={(event) => {
							if (event.animationName === 'fade-out')
								dispatch(PLAYBOOK_STAGE_SELECTION_UPDATE);
						}}
					>
						<now-playbook-stage-panel
							playbook-context-ids-in-order={
								playbookContextIdsInOrder
							}
							compact-mode={compactMode}
							playbooks-by-context-id={playbooksByContextId}
							playbook-experience-id={playbookExperienceId}
							selected-stage={selectedStage}
							loading-activities={loadingActivities}
							user-id={userId}
							parentRecordUpdateTime={parentRecordUpdateTime}
						/>
					</section>
				</div>
			);
		}
	},
	styles,
	behaviors: [rtlBehavior],
	properties: {
		playbookContextIdsInOrder: {},
		playbooksByContextId: {},
		loading: {},
		loadingActivities: {},
		playbookExperienceId: {},
		userId: {},
		compactMode: { default: false },
		parentRecordUpdateTime: { default: 0 }
	},
	initialState: {
		defaultExpandedPlaybook: '',
		nextSelectedStage: null,
		selectedStage: {
			playbookContextId: '',
			stageName: ''
		},
		focusAnchorRefs: {}
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({ updateState }) =>
			updateState(updateSelectedStageCallback),
		[COMPONENT_PROPERTY_CHANGED]: ({ action: { payload }, updateState }) =>
			payload.name === 'playbooksByContextId' ||
			payload.name === 'playbookContextIdsInOrder' ||
			payload.name === 'compactMode'
				? updateState(updateSelectedStageCallback)
				: null,
		[PANEL_NAVIGATION_SET_FOCUS_ANCHOR]: ({
			action: {
				payload: { focusAnchorRef, source }
			},
			updateState
		}) =>
			source && focusAnchorRef
				? updateState({
					path: `focusAnchorRefs.${source}`,
					value: focusAnchorRef,
					operation: 'set'
				  })
				: null,
		[PANEL_NAVIGATION_SWITCH_FOCUS]: ({
			action: {
				payload: { source }
			},
			state: { focusAnchorRefs }
		}) => {
			switch (source) {
				case PLAYBOOK_STAGE_SELECTOR_SOURCE:
					const stagePanelRef =
						focusAnchorRefs[PLAYBOOK_STAGE_PANEL_SOURCE];
					stagePanelRef && stagePanelRef.focus();
					break;
				case PLAYBOOK_STAGE_PANEL_SOURCE:
					const stageSelectorRef =
						focusAnchorRefs[PLAYBOOK_STAGE_SELECTOR_SOURCE];
					stageSelectorRef && stageSelectorRef.focus();
			}
		},
		[PLAYBOOK_STAGE_CLICKED]: ({
			action: {
				payload: { playbookContextId, stageName }
			},
			state: { selectedStage },
			updateState,
			dispatch
		}) => {
			if (
				selectedStage &&
				selectedStage.stageName === stageName &&
				selectedStage.playbookContextId === playbookContextId
			)
				return;

			updateState({
				nextSelectedStage: { playbookContextId, stageName }
			});

			dispatch(PLAYBOOK_STAGE_SELECTION_UPDATE);
		},
		[PLAYBOOK_STAGE_DESELECT]: ({ updateState }) =>
			updateState({
				nextSelectedStage: {
					playbookContextId: '',
					stageName: ''
				}
			}),
		[PLAYBOOK_STAGE_SELECTION_UPDATE]: ({
			properties: { compactMode },
			state: { nextSelectedStage },
			updateState,
			dispatch
		}) => {
			updateState({
				nextSelectedStage: null,
				selectedStage: { ...nextSelectedStage }
			});

			if (compactMode)
				dispatch(PANEL_NAVIGATION_SWITCH_FOCUS, {
					source:
						nextSelectedStage &&
						nextSelectedStage.playbookContextId &&
						nextSelectedStage.stageName
							? PLAYBOOK_STAGE_SELECTOR_SOURCE
							: PLAYBOOK_STAGE_PANEL_SOURCE
				});
		}
	}
});
