import { get, mapValues, set } from 'lodash';
import { constructRWFilter } from '../effects/amb-batch-subscription';

export const pushRecord = (collection, table, record) => {
	if (!collection) {
		return {};
	}

	if (table && record) {
		const sysIds = get(collection, table, []);

		if (Array.isArray(sysIds) && !sysIds.includes(record))
			sysIds.push(record);

		set(collection, table, sysIds);
	}

	return collection;
};

// NOTE: collection is mutated, this function is intended to build an object
// from inside a loop
export const pushAssociatedRecord = (collection, activityOrCard = {}) => {
	const { associatedTable, associatedRecord } = activityOrCard;

	return pushRecord(collection, associatedTable, associatedRecord);
};

export const pushExperienceStatusRecord = (collection, activityOrCard = {}) => {
	const {
		activityProperties: {
			experience_status_table: { value: statusTable } = {},
			experience_status_record: { value: statusRecord } = {}
		} = {}
	} = activityOrCard;

	return pushRecord(collection, statusTable, statusRecord);
};

export const getChannelId = (table, filter) => `/rw/default/${table}/${filter}`;

export const constructRWQueryBatchFromRecordsMap = (records = {}) => {
	return mapValues(records, (sysIds) =>
		constructRWFilter({ sys_id: sysIds })
	);
};

const hasAnUnwatchedRecord = (state, table, record) => {
	const watchingRecords = (state || {}).watchingRecords;

	return (
		table &&
		record &&
		(!watchingRecords.hasOwnProperty(table) ||
			(watchingRecords.hasOwnProperty(table) &&
				!watchingRecords[table].includes(record)))
	);
};

// Returns Boolean, checks whether the Associated Record of a given Activity
// is already being watched by checking its associatedTable/associatedSysId
// against 'watchingRecords'
export const hasAnUnwatchedAssociatedRecord = (activity = {}, state) => {
	const { associatedTable, associatedRecord } = activity;

	return hasAnUnwatchedRecord(state, associatedTable, associatedRecord);
};

// Returns Boolean, checks whether the Experience Status Record of a given Activity
// is already being watched by checking its Table/SysId values
// against 'watchingRecords'
export const hasAnUnwatchedExperienceStatusRecord = (activity = {}, state) => {
	const {
		activityProperties: {
			experience_status_table: { value: statusTable } = {},
			experience_status_record: { value: statusRecord } = {}
		} = {}
	} = activity;

	return hasAnUnwatchedRecord(state, statusTable, statusRecord);
};
