import { actionTypes } from '@servicenow/ui-core';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { createUpdateStateEffect } from '@servicenow/ui-effect-update-state';
import { get } from 'lodash';
import {
	NOW_TEXT_LINK_CLICKED,
	PLAYBOOK_CARD_FORM_FIELDS_REQUIRED,
	PLAYBOOK_RECORD_FAILED,
	PLAYBOOK_RECORD_FETCH,
	PLAYBOOK_RECORD_FIELD_UPDATE,
	PLAYBOOK_RECORD_INIT,
	PLAYBOOK_RECORD_SET_FIELDS,
	PLAYBOOK_RECORD_STARTED,
	PLAYBOOK_RECORD_SUCCEEDED,
	PLAYBOOK_SET_ACTIVITY_LOADING,
	PREVIEW_RECORD
} from '../../constants';
import { recordQuery } from '../../gql.constants';
import {
	NOTIFICATION_NETWORK_ERROR,
	NOTIFICATION_PLAYBOOK_ERROR
} from '../../notification.constants';
import {
	convertFormFields,
	ensureFieldDictionaryType,
	formatColumns,
	formatFields,
	getElements,
	getLastJournalEntry,
	getUpdatedByValue,
	getValues,
	indexElements
} from '../utils';
import { LiveUpdateMessage } from '@devsnc/sn-form-controls';
import { FieldType } from '@devsnc/sn-glide-form-controls';

const { COMPONENT_PROPERTY_CHANGED, COMPONENT_BOOTSTRAPPED } = actionTypes;
// 'properties' needed for method 'recordInitEffect' for re-fetch the Associated Record
const allowedPropertyChanges = [
	'formFields',
	'formView',
	'associatedTable',
	'associatedSysId',
	'associatedRecordModCount'
];

const fetchRecordEffect = createGraphQLEffect(recordQuery, {
	templateVarList: ['associatedTable', 'fields'],
	variableList: ['sysId', 'table', 'view', 'columns'],
	startActionType: PLAYBOOK_RECORD_STARTED,
	successActionType: PLAYBOOK_RECORD_SUCCEEDED,
	errorActionType: PLAYBOOK_RECORD_FAILED
});

const setLoadingEffect = ({ action, dispatch }) => {
	dispatch(PLAYBOOK_SET_ACTIVITY_LOADING, {
		contextId: get(action, 'payload.contextId'),
		loading: true
	});
};

//TODO refactor this to be more easily testable
export const getConvertedFormFieldItems = (action, properties) => {
	const { formFields, associatedTable } = properties;
	const elements = getElements(action);
	const values = getValues(action);
	const elementsIndexed = indexElements(elements);
	const valuesIndexed = indexElements(values);
	return convertFormFields({
		elementsIndexed,
		valuesIndexed,
		formFields,
		associatedTable
	});
};

const recordSucceededEffect = ({
	action,
	dispatch,
	state,
	properties,
	updateState
}) => {
	const error = get(action, 'error', false);
	const errors = get(action, 'payload.errors', []);
	const hasErrors = !!(error || (errors && errors.length));
	const { formFieldItems = [] } = state;
	const { associatedTable } = properties;
	const formFieldItemsNew = getConvertedFormFieldItems(action, properties);

	let updatedformFieldItems = [];
	if (formFieldItems.length > 0) {
		formFieldItemsNew.forEach((newVersion) => {
			const oldVersion = formFieldItems.find(
				(field) => field.name === newVersion.name
			);

			//Check if new version is different than old version
			if (newVersion.value != oldVersion.value) {
				let msg = new LiveUpdateMessage(
					getUpdatedByValue(action, associatedTable)
				);

				//Check if it has been modified
				if (oldVersion.value != oldVersion.originalValue) {
					//if the new version is the same as original version,
					//user has updated it, don't display message
					if (newVersion.value != oldVersion.originalValue) {
						msg.setNewValue(newVersion.displayValue);
						oldVersion.messages = [
							{ type: 'liveUpdate', message: msg }
						];
					}

					//if this is the last saved journal entry, we want to clear it
					if (
						oldVersion.dictionary.type == FieldType.JOURNAL &&
						oldVersion.value ==
							getLastJournalEntry(action, associatedTable)
					) {
						updatedformFieldItems.push(newVersion);
					} else {
						updatedformFieldItems.push(oldVersion);
					}
				} else {
					newVersion.messages = [
						{ type: 'liveUpdate', message: msg }
					];
					updatedformFieldItems.push(newVersion);
				}
			} else {
				updatedformFieldItems.push(newVersion);
			}
		});
	} else {
		updatedformFieldItems = formFieldItemsNew;
	}

	dispatch(PLAYBOOK_SET_ACTIVITY_LOADING, {
		contextId: get(action, 'payload.contextId'),
		loading: false
	});

	updateState({
		formFieldItems: updatedformFieldItems,
		notification: hasErrors
			? NOTIFICATION_PLAYBOOK_ERROR
			: state.notification
	});
};

const recordFailedEffect = createUpdateStateEffect((state) => {
	return {
		...state,
		loading: false,
		notification: NOTIFICATION_NETWORK_ERROR
	};
});

const recordInitEffect = ({ properties, dispatch }) => {
	const {
		formFields: fields,
		formView,
		associatedTable,
		associatedSysId
	} = properties;

	if (!associatedTable || !associatedSysId || fields.length < 1) return;

	dispatch(PLAYBOOK_RECORD_FETCH, {
		table: associatedTable,
		associatedTable: associatedTable,
		sysId: associatedSysId,
		view: formView,
		fields: formatFields(fields),
		columns: formatColumns(fields)
	});
};

const propertyChangeEffect = ({ action, dispatch }) => {
	const {
		payload: { name }
	} = action;

	if (allowedPropertyChanges.includes(name)) dispatch(PLAYBOOK_RECORD_INIT);
};

const updateFieldEffect = ({ action, updateState, state }) => {
	const { formFieldItems = [] } = state;
	const { payload } = action;
	const formFieldItemsNew = formFieldItems.map((field) => {
		if (field.name === payload.name) return payload;

		return field;
	});

	updateState({ formFieldItems: formFieldItemsNew });
};

const setFieldsEffect = ({ action: { payload }, updateState }) => {
	// Sometimes field items returned have invalid type properties
	const formFieldItems = ensureFieldDictionaryType(payload);
	updateState({ formFieldItems });
};

const setFormFieldsRequired = ({ action, state, updateState }) => {
	const formFieldsRequired = get(action, 'payload.formFieldsRequired', null);

	typeof formFieldsRequired === 'boolean' &&
		state.formFieldsRequired !== formFieldsRequired &&
		updateState({ formFieldsRequired });
};

export const playbookRecordBehavior = {
	name: 'playbookRecordBehavior',
	initialState: {
		/**
		 * Copy of 'formFieldsRequired' state property exposed by sn-playbook-card
		 * through dispatched action 'PLAYBOOK_CARD_FORM_FIELDS_REQUIRED'.
		 * sn-playbook-card calculates this value in its Action Handler and here
		 * we simply save that value so that we don't have to do our own
		 * calculations. (Looping through the collection of Declarative Actions).
		 *
		 * Defaults to 'null' to signify no caculations have been done because
		 * no Declarative Actions are present.
		 *
		 * @type {Boolean}
		 */
		formFieldsRequired: null
	},
	properties: {
		/**
		 * Display the record fields with the values filled in
		 * @type {Array}
		 */
		recordFields: {
			computed({
				properties: {
					activityProperties: { record_fields }
				}
			}) {
				const value = record_fields ? record_fields.value : '';
				return value && value.length ? value.split(',') : [];
			}
		},
		/**
		 * The list of field names will display in a form
		 * with the current values filled in.
		 * @type {Array}
		 */
		formFields: {
			computed({
				properties: {
					activityProperties: { form_fields }
				}
			}) {
				const value = form_fields ? form_fields.value : '';
				return value && value.length ? value.split(',') : [];
			}
		},
		/**
		 * If form view activity property is provided,
		 * embedded quick form will display within the card for that
		 * form view, including that view's layout.
		 * @type {String}
		 */
		formView: {
			computed({
				properties: {
					activityProperties: { form_view }
				}
			}) {
				return form_view && form_view.value ? form_view.value : '';
			}
		}
	},
	actionHandlers: {
		[PLAYBOOK_RECORD_INIT]: recordInitEffect,
		[COMPONENT_BOOTSTRAPPED]: recordInitEffect,
		[COMPONENT_PROPERTY_CHANGED]: propertyChangeEffect,
		[PLAYBOOK_RECORD_FETCH]: fetchRecordEffect,
		[PLAYBOOK_RECORD_STARTED]: setLoadingEffect,
		[PLAYBOOK_RECORD_SUCCEEDED]: recordSucceededEffect,
		[PLAYBOOK_RECORD_FAILED]: recordFailedEffect,
		[PLAYBOOK_RECORD_FIELD_UPDATE]: updateFieldEffect,
		[PLAYBOOK_RECORD_SET_FIELDS]: setFieldsEffect,
		[PLAYBOOK_CARD_FORM_FIELDS_REQUIRED]: {
			effect: setFormFieldsRequired,
			stopPropagation: true
		},
		[NOW_TEXT_LINK_CLICKED]: {
			effect: ({
				dispatch,
				action: {
					payload: { table, sys_id, recordLink }
				}
			}) => {
				if (!table || !sys_id || !recordLink) return;
				dispatch(PREVIEW_RECORD, {
					table,
					sys_id
				});
			},
			stopPropagation: true
		}
	}
};
