import { actionTypes } from '@servicenow/ui-core';
import { createEnhancedElement } from '@servicenow/library-enhanced-element';
import '@servicenow/now-button';
import resizeBehavior from '@servicenow/behavior-resize';
import styles from './styles.scss';
import { CARD_ACTIONS_BREAK, CARD_ACTIONS_SIZES } from '../constants';

const { COMPONENT_RENDERED } = actionTypes;

const _renderButtons = (itemsObj, dispatch) => {
	if (!itemsObj || !itemsObj.length) return null;

	return itemsObj.map((item) => (
		<now-button
			className="sn-playbook-card-actions-button"
			size="sm"
			variant={item.variant}
			label={item.label}
			tooltipContent={item.label}
			icon={item.icon}
			disabled={item.disabled}
			on-click={() => {
				if (!item.disabled) {
					dispatch(
						item.clickActionType ||
							'SN_PLAYBOOK_CARD_ACTIONS#ACTION_CLICKED',
						{
							action: item
						}
					);
				}
			}}
		/>
	));
};

const _getElementWidths = (container, elements) => {
	let widths = 0;
	if (!container || !container.offsetWidth || !elements || !elements.length) {
		return 0;
	}
	for (let i = elements.length - 1; i >= 0; i--) {
		const style =
				elements[i].currentStyle ||
				window.getComputedStyle(elements[i]),
			width = elements[i].offsetWidth,
			margin =
				parseFloat(style.marginLeft) + parseFloat(style.marginRight),
			padding =
				parseFloat(style.paddingLeft) + parseFloat(style.paddingRight),
			border =
				parseFloat(style.borderLeftWidth) +
				parseFloat(style.borderRightWidth);
		if (width) widths += width + margin - padding + border;
	}
	return widths;
};

const _getContainerClassName = (
	buttonContainerRef,
	buttonRefs,
	buttonsWidth
) => {
	const cardActionsClassName = 'sn-playbook-card-actions';
	let cardSize = '';
	let largestButtonWidth = 0;

	// Buttons and container aren't rendered with a width yet, so render with no cardSize so they are hidden
	if (!buttonContainerRef || !buttonRefs || !buttonsWidth)
		return `${cardActionsClassName}`;

	for (let i = buttonRefs.length - 1; i >= 0; i--)
		if (largestButtonWidth < buttonRefs[i].offsetWidth)
			largestButtonWidth = buttonRefs[i].offsetWidth;
	const buttonsMinWidth = largestButtonWidth * buttonRefs.length;

	if (buttonContainerRef.offsetWidth > buttonsWidth)
		if (buttonContainerRef.offsetWidth > CARD_ACTIONS_BREAK)
			// if the buttons are smaller than the button container
			// if the button container is wider than a 320 card allows
			cardSize = CARD_ACTIONS_SIZES.lg;
		else if (buttonContainerRef.offsetWidth < buttonsMinWidth)
			// assume each button is as long as the longest, if the buttons are wider than the container
			cardSize = CARD_ACTIONS_SIZES.sm;
		else cardSize = CARD_ACTIONS_SIZES.md;
	else if (buttonContainerRef.offsetWidth) cardSize = CARD_ACTIONS_SIZES.sm;

	return `${cardActionsClassName} ${cardSize}`;
};

const view = (state, { dispatch }) => {
	const {
		buttonContainerRef,
		buttonRefs,
		buttonsWidth,
		properties: { items }
	} = state;

	const itemsObj = items || [];

	return (
		<div
			className={_getContainerClassName(
				buttonContainerRef,
				buttonRefs,
				buttonsWidth
			)}
		>
			{_renderButtons(itemsObj, dispatch)}
		</div>
	);
};

/**
 * Card subcomponent that enables the user to take action. Use card actions
 * to display one to MAX_NUM_DAS_BOTTOM_OF_CARD large action buttons for a card.
 *
 * ```jsx
 * const cardActions = [
 *   {label: 'Decline', variant: 'secondary-negative'},
 *   {label: 'Approve', variant: 'secondary-positive'}
 * ];
 * <sn-playbook-card-actions items={cardActions} />
 * ```
 *
 * @seismicElement sn-playbook-card-actions
 * @summary Card subcomponent that enables the user to take action.
 */
createEnhancedElement('sn-playbook-card-actions', {
	properties: {
		/**
		 * An array of objects that defines the buttons displayed.
		 * The keys for each object are properties for `now-button`. See
		 * `now-button` for more information on using these properties.
		 * - `label: string` required - Label describing the action.
		 * - `icon: string` - Icon displayed with the label.
		 * - `variant: string` - Button style variant.
		 * - `disabled: boolean` - Set to `true` to mute the button color and disable user click interactions.
		 * - `clickActionType: string = "SN_PLAYBOOK_CARD_ACTIONS#ACTION_CLICKED"` - Overrides the default action type dispatched when the user clicks the action
		 * @type {{ label: string, icon: string, variant: string, disabled: boolean, clickActionType: string}[]}
		 */
		items: {
			required: true,
			schema: {
				type: 'array',
				items: {
					type: 'object',
					properties: {
						label: { type: 'string' },
						icon: { type: 'string' },
						// TODO - re-use a typedef from now-button for this?
						variant: { type: 'string' },
						disabled: { type: 'boolean' },
						clickActionType: { type: 'string' }
					}
				}
			}
		}
	},
	view,
	styles,
	dispatches: {
		/**
		 * Dispatched when the user clicks a button. The payload will include a
		 * reference to the clicked item and action. The action type can be overridden
		 * by configuring `item.clickActionType`.
		 * @type {{action: Object}}
		 */
		'SN_PLAYBOOK_CARD_ACTIONS#ACTION_CLICKED': {}
	},
	behaviors: [
		{
			behavior: resizeBehavior,
			options: { timer: { type: 'throttle', wait: 100 } }
		}
	],
	actionHandlers: {
		[COMPONENT_RENDERED]: ({ host, state, updateState }) => {
			const buttonContainerRef = host.shadowRoot.querySelector(
				'.sn-playbook-card-actions'
			);
			const buttonRefs = host.shadowRoot.querySelectorAll(
				'.sn-playbook-card-actions-button'
			);

			if (!buttonContainerRef && !buttonRefs) return null;

			if (!state.buttonsWidth) {
				const buttonsWidth = _getElementWidths(
					buttonContainerRef,
					buttonRefs
				);
				if (buttonsWidth === 0) return null;

				updateState({
					buttonContainerRef, // pass ref as it changes as container changes
					buttonRefs,
					buttonsWidth // pass width at initial render as it doesn't change if playbook changes
				});
			}
		}
	}
});
