import { get } from 'lodash';
import { INVALID_URL_TYPE, BEHAVIOR_NAME } from './constants';

export const extractAnchorUrls = (html = '', opts) => {
	const { stagedUrls, invalidUrls, capturedUrls } = opts;
	const urls = new Set();
	const frag = document.createDocumentFragment();
	const div = document.createElement('div');
	frag.appendChild(div);
	div.innerHTML = html;

	frag.querySelectorAll('a').forEach((anchor) => {
		const url = get(anchor, 'attributes["href"].value', null);

		if (
			!url ||
			stagedUrls.hasOwnProperty(url) ||
			invalidUrls.has(url) ||
			capturedUrls.hasOwnProperty(url)
		)
			return;

		urls.add(url);
	});

	return Array.from(urls);
};

export const collectAnchorUrls = (htmlValues = [], opts) => {
	let collection = [];

	htmlValues.forEach((value) => {
		const urls = extractAnchorUrls(value, opts);
		collection = [...collection, ...urls];
	});

	return collection;
};

export const getCleanedUrl = (url = '') => {
	return url.length > 0 && url[0] === '/' ? url.substring(1) : url;
};

export const getFetchableUrls = (
	stagedUrls = {},
	invalidUrls = new Set(),
	maxRetry = 0
) => {
	const fetchableUrls = [];

	for (const url in stagedUrls) {
		const count = stagedUrls[url];
		if (!invalidUrls.has(url) && count <= maxRetry) fetchableUrls.push(url);
	}

	return fetchableUrls;
};

export const addToStagedUrls = (stagedUrls = {}, urls = []) => {
	const collection = Object.assign({}, stagedUrls);
	urls.forEach((url) => (collection[url] = 1));
	return collection;
};

export const addToInvalidUrls = (invalidUrls = [], newInvalidUrls = []) => {
	return new Set([...invalidUrls, ...newInvalidUrls]);
};

export const addToCapturedUrls = (capturedUrls = {}, newCapturedUrls = {}) => ({
	...capturedUrls,
	...newCapturedUrls
});

export const incrementStagedUrlCount = (stagedUrls = {}, urls = []) => {
	const collection = Object.assign({}, stagedUrls);

	for (const url in collection) collection[url] += 1;

	urls.forEach((url) => (collection[url] = 1));
	return collection;
};

export const processResult = (result = [], stagedUrls = {}) => {
	const processedStagedUrls = Object.assign({}, stagedUrls);
	const processedInvalidUrls = [];
	const processedCapturedUrls = {};

	result.forEach((entry) => {
		const { url } = entry;
		if (entry.type === INVALID_URL_TYPE) {
			processedInvalidUrls.push(url);
		} else {
			delete processedStagedUrls[url];
			processedCapturedUrls[url] = entry;
		}
	});

	return { processedStagedUrls, processedInvalidUrls, processedCapturedUrls };
};

export const killEvent = (event = {}) => {
	typeof event.stopPropagation === 'function' && event.stopPropagation();
	typeof event.preventDefault === 'function' && event.preventDefault();
	return true;
};

export const getPath = () => `behaviors.${BEHAVIOR_NAME}`;

export const getBehaviorState = (state) => get(state, getPath(), {});

export const setBehaviorState = (state, nextState) => {
	const currentState = getBehaviorState(state);

	return {
		path: getPath(),
		operation: 'set',
		value: {
			...currentState,
			...nextState
		}
	};
};
