import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { view } from './view';
import styles from './styles.scss';
import { PLAYBOOK_EMPTY_BODY_TITLE_DEFAULT } from '../constants';

/**
 * ## Empty Playbook
 *
 * When a playbook has no data or doesn't load show an empty state.
 *
 * @summary Empty state for playbook.
 * @seismicElement sn-playbook-empty
 */
createCustomElement('sn-playbook-empty', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		/**
		 * @type {Object}
		 */
		img: {
			default: {}
		},
		/**
		 * Empty state body title
		 * @type {String}
		 */
		bodyTitle: {
			default: PLAYBOOK_EMPTY_BODY_TITLE_DEFAULT
		},
		/**
		 * Empty state body text
		 * @type {String}
		 */
		bodyText: {
			default: ''
		}
	},
	styles
});
