import { get } from 'lodash';
import { OPEN_IN_WORKSPACE, INVALID_URL_TYPE } from './constants';
import { getBehaviorState, getCleanedUrl, killEvent } from './utils';

export default [
	{
		events: ['click'],
		effect({ action, dispatch, state }) {
			const event = get(action, 'payload.event', null);

			if (!event) return;

			const {
				stagedUrls,
				capturedUrls,
				tableExceptions
			} = getBehaviorState(state);

			const path = event.composedPath() || event.path || [];
			const anchor = path.find((el) => el && el.tagName === 'A');

			if (!anchor || !anchor.getAttribute('href')) return;

			const url = getCleanedUrl(anchor.getAttribute('href'));
			const captured = capturedUrls[url];
			const isOpenInNewTab =
				event.ctrlKey ||
				event.shiftKey ||
				event.metaKey ||
				(event.button && event.button === 1);

			if (
				isOpenInNewTab ||
				!captured ||
				stagedUrls.hasOwnProperty(url) ||
				(captured.type || '') === INVALID_URL_TYPE
			)
				return;

			const table = get(captured, 'type_metadata.table', '');
			const sysId = get(captured, 'type_metadata.sys_id', '');
			const sysKbId = get(captured, 'query_params.sys_kb_id', '');

			if (table && sysId && !tableExceptions.includes(table)) {
				killEvent(event);
				dispatch(OPEN_IN_WORKSPACE, { table, sysId });
			}

			if (
				sysKbId &&
				captured.type === 'UIPAGE' &&
				captured.url.includes('kb_view.do')
			) {
				killEvent(event);
				dispatch(OPEN_IN_WORKSPACE, {
					table: 'kb_knowledge',
					sysId: sysKbId
				});
			}
		}
	}
];
