import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { get } from 'lodash';
import {
	PLAYBOOK_ADD_OR_UPDATE_ACTIVITIES,
	SKIP_ACTIVITY,
	SKIP_ACTIVITY_REQUEST,
	SKIP_ACTIVITY_SUCCEEDED
} from '../../constants';
import { CARD_QUERY } from '../../gql.constants';

const skipActivityQuery = `
	query($contextId: String!) {
		GlidePlaybookContext_Query {
			skipActivityContext(contextId: $contextId, playbookExperienceId: "{{playbookExperienceId}}") {
				${CARD_QUERY}
			}
		}
	}
`;

// TODO: Create fail handler
const skipActivityEffect = createGraphQLEffect(skipActivityQuery, {
	variableList: ['contextId'],
	templateVarList: ['playbookExperienceId'],
	successActionType: SKIP_ACTIVITY_SUCCEEDED
});

const skipAcitivitySucceededEffect = ({ action, dispatch }) => {
	const activity = get(
		action,
		'payload.data.GlidePlaybookContext_Query.skipActivityContext',
		null
	);

	if (activity)
		dispatch(PLAYBOOK_ADD_OR_UPDATE_ACTIVITIES, {
			activities: [activity]
		});
};
const activitySkipActionHandlers = {
	[SKIP_ACTIVITY]: {
		effect: ({
			dispatch,
			action: {
				payload: { contextId }
			},
			properties: { playbookExperienceId }
		}) => {
			dispatch(SKIP_ACTIVITY_REQUEST, {
				contextId,
				playbookExperienceId
			});
		},
		stopPropagation: true
	},
	[SKIP_ACTIVITY_REQUEST]: skipActivityEffect,
	[SKIP_ACTIVITY_SUCCEEDED]: skipAcitivitySucceededEffect
};

export default activitySkipActionHandlers;
