import rtlBehavior from '@servicenow/behavior-rtl';
import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { snPanelNavigationBehavior } from '../../behaviors';
import {
	PANEL_NAVIGATION_SWITCH_FOCUS,
	PANEL_NAVIGATION_TAKE_FOCUS
} from '../../behaviors/sn-panel-navigation/constants';
import {
	PLAYBOOK_STAGE_PANEL_SOURCE,
	PLAYBOOK_STAGE_PANEL_UPDATE_FIRST_CARD_NAV_ID
} from '../../constants';
import { getFirstVisibleCardIdFromLane, setFocusNavId } from './helpers';
import styles from './styles.scss';
import view from './view';

const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

createCustomElement('now-playbook-stage-panel', {
	renderer: { type: snabbdom, view },
	properties: {
		/** @type {[]} **/
		playbookContextIdsInOrder: { default: [] },
		/** @type {{}} **/
		playbooksByContextId: { default: {} },
		/** @type {String} **/
		selectedStagePlaybookContextId: {},
		/** @type {String} **/
		playbookExperienceId: { default: '' },
		/** @type {} **/
		loadingActivities: { default: {} },
		/** @type {} **/
		selectedStage: {
			default: {
				playbookContextId: '',
				stageName: ''
			}
		},
		/** @type {String} **/
		userId: {
			default: ''
		},
		/** @type {String} **/
		firstVisibleCardNavId: {
			default: ''
		},
		/** @type {Boolean} **/
		compactMode: { default: false },

		parentRecordUpdateTime: { default: 0 }
	},
	initialState: {
		mountedStageMap: {},
		onFocusDispatchId: { left: PANEL_NAVIGATION_SWITCH_FOCUS },
		dispatchSource: PLAYBOOK_STAGE_PANEL_SOURCE
	},
	behaviors: [snPanelNavigationBehavior, rtlBehavior],
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({
			state: {
				properties: {
					selectedStage: { playbookContextId, stageName },
					playbooksByContextId
				}
			},
			updateState
		}) => {
			if (playbookContextId && stageName) {
				const firstCardId = getFirstVisibleCardIdFromLane(
					playbooksByContextId,
					playbookContextId,
					stageName
				);

				setFocusNavId(`card-header-${firstCardId}`, updateState);

				updateState({
					path: `mountedStageMap["${playbookContextId}"]["${stageName}"]`,
					value: true
				});
			}
		},
		[COMPONENT_PROPERTY_CHANGED]: ({
			action: {
				payload: { name, value }
			},
			properties: { playbooksByContextId },
			updateState
		}) => {
			if (
				name === 'selectedStage' &&
				value &&
				value.playbookContextId &&
				value.stageName
			) {
				const { playbookContextId, stageName } = value;

				const firstCardId = getFirstVisibleCardIdFromLane(
					playbooksByContextId,
					playbookContextId,
					stageName
				);

				setFocusNavId(`card-header-${firstCardId}`, updateState);

				updateState({
					path: `mountedStageMap["${playbookContextId}"]["${stageName}"]`,
					operation: 'set',
					value: true
				});
			} else if (name === 'firstVisibleCardNavId' && value)
				setFocusNavId(value, updateState);
		},
		[PLAYBOOK_STAGE_PANEL_UPDATE_FIRST_CARD_NAV_ID]: ({
			action: {
				payload: { cardNavId, takeFocus }
			},
			properties: { firstVisibleCardNavId },
			updateProperties,
			dispatch
		}) => {
			if (firstVisibleCardNavId !== cardNavId)
				updateProperties({ firstVisibleCardNavId: cardNavId });

			if (takeFocus) dispatch(PANEL_NAVIGATION_TAKE_FOCUS);
		}
	},
	styles
});
