import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { createNowTextAreaInputBehavior } from '../behaviors';
import { PLAYBOOK_CANCELLATION_REASON } from '../constants';
import actionHandlers from './actions';
import styles from './styles.scss';
import view from './view';

/**
 * ## Cancel Playbook Modal
 *
 * @seismicElement now-cancel-playbook-modal
 * @summary now-modal component that enables the user to confirm a Cancel Playbook action
 */
createCustomElement('now-cancel-playbook-modal', {
	renderer: { type: snabbdom, view },
	styles,
	properties: {
		/**
		 * the sysId and title of the playbook context to cancel
		 * @type {Object}
		 */
		cancelPlaybookContext: {
			default: {}
		},
		/**
		 * the reason given for cancelling the playbook context
		 * @type {String}
		 */
		cancellationReason: {
			default: ''
		},
		playbookExperienceId: {
			default: ''
		}
	},
	behaviors: [createNowTextAreaInputBehavior(PLAYBOOK_CANCELLATION_REASON)],
	actionHandlers
});
