import '../../sn-playbook-empty';
import {
	CARD_STATE_VALUES,
	IMG_EMPTY_STATE_CARD_PENDING,
	IMG_EMPTY_STATE_CARD_CANNCELLED
} from '../../constants';

export function getEmptyState(state) {
	const {
		properties: {
			activityState,
			pendingTitle,
			pendingDescription,
			cancelledTitle,
			cancelledDescription,
			notification,
			hasSufficientData
		}
	} = state;

	if (activityState.value === CARD_STATE_VALUES.PENDING.value)
		return renderEmptyState(pendingTitle, pendingDescription);

	if (activityState.value === CARD_STATE_VALUES.CANCELLED.value)
		return renderEmptyState(
			cancelledTitle,
			cancelledDescription,
			IMG_EMPTY_STATE_CARD_CANNCELLED
		);

	if (
		activityState.value !== CARD_STATE_VALUES.COMPLETE.value &&
		!hasSufficientData
	)
		return renderEmptyState(pendingTitle, pendingDescription);

	if (notification.header || notification.body)
		return renderEmptyState(notification.header, notification.body);

	return null;
}

export const renderEmptyState = (
	title,
	text,
	img = IMG_EMPTY_STATE_CARD_PENDING
) => <sn-playbook-empty bodyTitle={title} bodyText={text} img={img} />;
