export function renderAttachments(state, slotName) {
	const {
		properties: {
			activityProperties: {
				attachment_source = {},
				attachments_read_only
			},
			parentTable,
			parentSysId,
			associatedSysId,
			associatedTable
		},
		associatedRecordUpdated
	} = state;

	const refreshRequested = associatedRecordUpdated
		? associatedRecordUpdated
		: null;

	let table = '';
	let sysId = '';

	if (attachment_source.value == 'associated_record') {
		table = associatedTable;
		sysId = associatedSysId;
	} else if (attachment_source.value == 'parent_record') {
		table = parentTable;
		sysId = parentSysId;
	}

	if (!attachment_source || !table || !sysId) return;

	const readOnly = (attachments_read_only || {}).value === '1';

	return (
		<div slot={slotName}>
			<now-record-common-attachments-connected
				table={table}
				sys-id={sysId}
				is-attachment-upload-disabled={readOnly}
				is-read-only={readOnly}
				classic-form={false}
				mode="compact"
				refresh-requested={refreshRequested}
			/>
		</div>
	);
}
