import { BEHAVIOR_NAME } from './constants';
import actionHandlers from './actions';
import eventHandlers from './events';

/*
	Link Inteceptor Behavior:

	The host component and its child components will dispatch an action
	'LINK_INTERCEPTOR#SAVE_URLS' with stringified HTML values (i.e.
	'<div><a href="https://emotive.servicenow.com/">Emotive</a></div>').

	Upon receiving the HTML values, Link Interceptor will parse out anchor
	elements and send the collected urls to the 'link_parser' endpoint to be
	processed.

	In the HTTP response, any valid urls will be saved, and any invalid urls
	will also be saved to prevent future requests with the same url.

	When a link element inside the host component is clicked the 'href' url
	of that link will be compared with the collection of valid urls. If
	a match is found, Link Interceptor will dispatch a 'PREVIEW_RECORD' action
	to open up the corresponding record inside Agent Workspace using the valid url.
	The original click event will be killed.

	Implementation details:

	API requests to the 'link_parser' endpoint will only be made one at a time.
	While waiting for a response, any urls from newly dispatched
	'LINK_INTERCEPTOR#SAVE_URLS' actions will be saved to 'stagedUrls'.

	Successful or failed requests, in either case the Link Interceptor will check
	again at 'stagedUrls' and collect any urls that have not been tried the
	maximum number of allowed retries specified by the 'maxRetry' property
	(Default is 3). The collected urls will be used in a new API request.

	If request is successful, valid urls will be removed from 'stagedUrls' and
	added to 'capturedUrls'. Invalid urls will be added to 'invalidUrls'. Only
	'stagedUrls' are allowed to be included in API requests.

	This approach ensures no duplicate urls will be saved or included in future
	API requests. This approach also gives off a sort of 'debounce' behavior where
	we are trying to collect as many urls at one time and making fewer requests.
*/

export default {
	name: BEHAVIOR_NAME,
	initialState: {
		stagedUrls: {},
		invalidUrls: new Set(),
		capturedUrls: {},
		isFetching: false,
		tableExceptions: ['sys_attachment'],
		maxRetry: 3,
		delay: 500
	},
	actionHandlers,
	eventHandlers
};
