import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import EmptyState from '../components/empty-state';
import { PLAYBOOK_ERROR } from '../constants';
import {
	NOTIFICATION_PLAYBOOK_ERROR,
	NOTIFICATION_PLAYBOOK_INPUTS_MISSING
} from '../notification.constants';
import '../now-cancel-playbook-modal';
import '../now-playbook-data-broker';
import '../now-playbook-panel-layout';
import styles from './styles.scss';

const { COMPONENT_PROPERTY_CHANGED } = actionTypes;

/**
 * Playbook is a component that visually guides a user, typically a fulfiller,
 * through a process usually built with Process Automation Designer.
 * Playbook provides the fulfiller with an overview of the phases of the process,
 * highlights the current phase in the process, and displays the actions available to the fulfiller to perform.
 *
 * @seismicElement now-playbook-experience-connected
 * @summary Interactive visualization of an automated workflow.
 */
createCustomElement('now-playbook-experience-connected', {
	properties: {
		/**
		 * Sys Id of the parent record associated with the playbook.
		 * @type {String}
		 */
		parentSysId: {
			reflect: true
		},
		/**
		 * Name of the table of the parent record associated with the playbook.
		 * @type {String}
		 */
		parentTable: {
			reflect: true
		},
		/**
		 * Sys ID of the Playbook Experiences configuration record to use.
		 * @type {String}
		 */
		playbookExperienceId: {},
		/**
		 * (Optional) Sys ID of the User record to populate the Assigned to section.
		 * @type {String}
		 */
		userId: { reflect: true },
		/**
		 * (Optional) When true, the playbook displays in compact mode.
		 * @type {boolean}
		 */
		compactMode: { default: false }
	},
	renderer: {
		type: snabbdom,
		view: ({
			error,
			properties: {
				parentSysId,
				parentTable,
				playbookExperienceId,
				userId,
				compactMode
			},
			componentId
		}) => {
			const dataBrokerComponentId = `playbook-data-broker-${componentId}`;

			if (error) {
				return (
					<EmptyState
						title={NOTIFICATION_PLAYBOOK_ERROR.header}
						text={NOTIFICATION_PLAYBOOK_ERROR.body}
						illustration={NOTIFICATION_PLAYBOOK_ERROR.illustration}
					/>
				);
			}

			if (!(parentSysId && parentTable))
				return (
					<EmptyState
						title={NOTIFICATION_PLAYBOOK_INPUTS_MISSING.header}
						text={NOTIFICATION_PLAYBOOK_INPUTS_MISSING.body}
						illustration={
							NOTIFICATION_PLAYBOOK_INPUTS_MISSING.illustration
						}
					/>
				);

			return (
				<now-playbook-data-broker
					parent-sys-id={parentSysId}
					parent-table={parentTable}
					playbook-experience-id={playbookExperienceId}
					component-id={dataBrokerComponentId}
					current-user-id={userId}
				>
					<now-playbook-panel-layout
						playbook-context-ids-in-order={`@${dataBrokerComponentId}/playbookContextIdsInOrder`}
						playbooks-by-context-id={`@${dataBrokerComponentId}/playbooksByContextId`}
						loading={`@${dataBrokerComponentId}/loading`}
						loading-activities={`@${dataBrokerComponentId}/loadingActivities`}
						playbook-experience-id={playbookExperienceId}
						user-id={`@${dataBrokerComponentId}/currentUserId`}
						compact-mode={compactMode}
						parentRecordUpdateTime={`@${dataBrokerComponentId}/parentRecordUpdateTime`}
					/>
					<now-cancel-playbook-modal
						cancel-playbook-context={`@${dataBrokerComponentId}/cancelPlaybookContext`}
						playbook-experience-id={`@${dataBrokerComponentId}/playbookExperienceId`}
					/>
				</now-playbook-data-broker>
			);
		}
	},
	initialState: {
		error: false
	},
	actionHandlers: {
		[PLAYBOOK_ERROR]: {
			effect: ({ updateState }) => {
				updateState({ error: true });
			},
			stopPropagation: true
		},
		[COMPONENT_PROPERTY_CHANGED]: ({ updateState }) => {
			updateState({ error: false });
		}
	},
	styles
});
