/*
 * Depending on the value of 'shouldBeSelected', this method either adds the
 * newly selected filter option to the collection of 'selectedFilters' or
 * removes from it.
 */
const combineSelectedFilters = (
	selectedFilters = {},
	{ filterSection, label, shouldBeSelected }
) => {
	const combined = { ...selectedFilters };

	if (shouldBeSelected) {
		if (!combined[filterSection]) combined[filterSection] = {};

		combined[filterSection] = {
			...combined[filterSection],
			[label]: shouldBeSelected
		};
	} else if ((combined[filterSection] || {})[label]) {
		delete combined[filterSection][label];
	}

	return combined;
};

export default combineSelectedFilters;
