import { get } from 'lodash';
import { renderNowLabelValue } from './renderNowLabelValue';
import { showFormFieldList, convertFormFieldListItem } from '../utils';

export function renderFormFieldList(state, slot = 'form-field-stacked') {
	const { formFieldItems = [] } = state;

	if (formFieldItems.length < 1) return null;

	const formFieldsRequired = get(
		state,
		'behaviors.playbookRecordBehavior.formFieldsRequired'
	);

	if (!showFormFieldList(formFieldsRequired)) return null;

	const formFieldListItems = formFieldItems.map((item) =>
		convertFormFieldListItem(item)
	);
	const opts = {
		slot,
		className: 'form-fields-container',
		align: 'vertical'
	};

	return renderNowLabelValue(formFieldListItems, opts);
}
