import { actionTypes } from '@servicenow/ui-core';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import { get } from 'lodash';
import {
	SAVE_URLS,
	FETCH_BROKER,
	FETCH_BROKER_DELAYED,
	FETCH_URLS,
	FETCH_URLS_SUCCEEDED,
	FETCH_URLS_FAILED,
	OPEN_IN_WORKSPACE,
	PREVIEW_RECORD
} from './constants';
import {
	getBehaviorState,
	setBehaviorState,
	collectAnchorUrls,
	getFetchableUrls,
	addToStagedUrls,
	addToInvalidUrls,
	addToCapturedUrls,
	incrementStagedUrlCount,
	processResult
} from './utils';

const fetchBrokerEffect = ({ dispatch, state, updateState }) => {
	const { stagedUrls, invalidUrls, maxRetry, isFetching } = getBehaviorState(
		state
	);

	const fetchableUrls = getFetchableUrls(stagedUrls, invalidUrls, maxRetry);

	if (isFetching || fetchableUrls.length === 0) return;

	const newStagedUrls = incrementStagedUrlCount(stagedUrls);
	const nextState = setBehaviorState(state, {
		isFetching: true,
		stagedUrls: newStagedUrls
	});

	dispatch(FETCH_URLS, { sysparm_link: fetchableUrls });
	updateState(nextState);
};

const fetchBrokerDelayedEffect = ({ dispatch, state }) => {
	const { delay } = getBehaviorState(state);
	setTimeout(() => dispatch(FETCH_BROKER), delay);
};

const saveUrlsEffect = ({ action, dispatch, state, updateState }) => {
	const behaviorState = getBehaviorState(state);
	const { stagedUrls } = behaviorState;
	const htmlValues = get(action, 'payload.htmlValues', []);
	const newUrls = collectAnchorUrls(htmlValues, behaviorState);

	if (newUrls.length === 0) return;

	const newStagedUrls = addToStagedUrls(stagedUrls, newUrls);
	const nextState = setBehaviorState(state, { stagedUrls: newStagedUrls });

	dispatch(FETCH_BROKER_DELAYED);
	updateState(nextState);
};

const fetchUrlsSucceededEffect = ({ action, dispatch, state, updateState }) => {
	const { stagedUrls, invalidUrls, capturedUrls } = getBehaviorState(state);

	const result = get(action, 'payload.result', {});

	if (!result) return;

	const pr = processResult(result, stagedUrls);
	const newStagedUrls = pr.processedStagedUrls;
	const newInvalidUrls = addToInvalidUrls(
		invalidUrls,
		pr.processedInvalidUrls
	);
	const newCapturedUrls = addToCapturedUrls(
		capturedUrls,
		pr.processedCapturedUrls
	);
	const nextState = setBehaviorState(state, {
		stagedUrls: newStagedUrls,
		invalidUrls: newInvalidUrls,
		capturedUrls: newCapturedUrls,
		isFetching: false
	});

	dispatch(FETCH_BROKER_DELAYED);
	updateState(nextState);
};

const fetchUrlsFailedEffect = ({ dispatch, state, updateState }) => {
	const nextState = setBehaviorState(state, { isFetching: false });
	dispatch(FETCH_BROKER_DELAYED);
	updateState(nextState);
};

const openInWorkSpaceEffect = ({ action, dispatch }) => {
	const { table, sysId } = action.payload || {};

	if (table && sysId) dispatch(PREVIEW_RECORD, { table, sys_id: sysId }); // Agent workspace sys_id is snake_cased
};

const componentConnectedEffect = ({ action, state, updateState }) => {
	const options = get(action, 'payload.options', {});
	const { tableExceptions, maxRetry, delay } = options;
	const nextState = setBehaviorState(state, {
		...(Array.isArray(tableExceptions) && { tableExceptions }),
		...(typeof maxRetry === 'number' && { maxRetry }),
		...(typeof delay === 'number' && { delay })
	});

	updateState(nextState);
};

export default {
	[SAVE_URLS]: {
		effect: saveUrlsEffect,
		stopPropagation: true
	},
	[FETCH_BROKER]: fetchBrokerEffect,
	[FETCH_BROKER_DELAYED]: fetchBrokerDelayedEffect,
	[FETCH_URLS]: createHttpEffect('/api/now/ui/link_parser', {
		method: 'POST',
		queryParams: ['sysparm_link'],
		batch: true,
		successActionType: FETCH_URLS_SUCCEEDED,
		errorActionType: FETCH_URLS_FAILED
	}),
	[FETCH_URLS_SUCCEEDED]: fetchUrlsSucceededEffect,
	[FETCH_URLS_FAILED]: fetchUrlsFailedEffect,
	[OPEN_IN_WORKSPACE]: {
		effect: openInWorkSpaceEffect,
		stopPropagation: true
	},
	[actionTypes.COMPONENT_CONNECTED]: componentConnectedEffect
};
