import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { camelCase, isEmpty, isString, startsWith, reduce } from 'lodash';

/* Copied from sn-custom-create-element and converted to Snabbdom.  TODO: replace with shared method */

const ATTRIBUTES = [
	'component-id',
	'listen-to',
	'path',
	'context',
	'context-modifier',
	'key',
	'dangerouslySetInnerHTML',
	'suppressContentEditableWarning',
	'suppressHydrationWarning',
	'alt',
	'async',
	'autocomplete',
	'autofocus',
	'autoplay',
	'capture',
	'checked',
	'default',
	'defer',
	'disabled',
	'draggable',
	'dropzone',
	'hidden',
	'href',
	'hreflang',
	'http-equiv',
	'icon',
	'id',
	'is',
	'maxlength',
	'minlength',
	'media',
	'method',
	'multiple',
	'name',
	'placeholder',
	'readonly',
	'rel',
	'required',
	'reversed',
	'role',
	'rows',
	'rowspan',
	'selected',
	'slot',
	'src',
	'tabindex',
	'target',
	'title',
	'type',
	'value'
];

const MAPPED_TO_PROPS = ['value'];

export const customCreateElement = (name, props = {}, ...children) => {
	function isSeismicComponent(name) {
		return isString(name) && name.indexOf('-') !== -1;
	}

	function isAttribute(name) {
		return (
			ATTRIBUTES.indexOf(name) !== -1 ||
			startsWith(name, 'aria-') ||
			startsWith(name, 'data-')
		);
	}

	function isMappedToProps(name) {
		return MAPPED_TO_PROPS.indexOf(name) !== -1;
	}

	if (!isEmpty(props) && isSeismicComponent(name)) {
		const existingRef =
			props.ref && typeof props.ref === 'function' && props.ref;

		const { attributes, properties } = reduce(
			props,
			(acc, value, name) => {
				if (isAttribute(name)) acc['attributes'][name] = value;
				else acc['properties'][camelCase(name)] = value;

				if (isMappedToProps(name)) acc['properties'][name] = value;

				return acc;
			},
			{
				attributes: {},
				properties: {}
			}
		);

		if (!isEmpty(properties)) {
			return snabbdom.createElement(
				name,
				Object.assign({}, attributes, {
					ref: (node) => {
						if (!node) return;
						existingRef && existingRef(node);
						Object.assign(node, properties);
					}
				}),
				...children
			);
		}
	}
	return snabbdom.createElement(name, props, ...children);
};
