import { getComponentsByTagNames } from '@devsnc/library-uxf';
import { FieldType } from '@devsnc/sn-glide-form-controls';
import { get } from 'lodash';
import { t } from 'sn-translate';
import {
	CARD_STATE_VALUES,
	CARD_STATUS_FILTER_SECTION,
	LINK_INTERCEPTOR_SAVE_URLS,
	PLAYBOOK_RENDERERS,
	PLAYBOOK_RW_ADD_AND_REMOVE_WATCHING_RECORDS
} from '../constants';
import {
	getDefaultConfigProperties,
	incrementSingleActivityStats
} from './actions/utils';
import {
	pushAssociatedRecord,
	pushExperienceStatusRecord,
	pushRecord
} from './ambUtils';
import {
	evaluateFilterOnCard,
	pushToFilter,
	reduceSelectedFilters
} from './filterUtils';

export const processCard = (playbook, card, currentUserId) => {
	let newCard = { ...card };

	if (newCard.filterData) {
		newCard.filterData = newCard.filterData.reduce((acc, prop) => {
			const { name, value, displayValue: dv } = prop;
			const displayValue =
				value && value === currentUserId ? t('Me') : dv;
			if (name == CARD_STATUS_FILTER_SECTION) {
				acc[name] =
					CARD_STATE_VALUES[value].displayValue || displayValue;
				return acc;
			}
			acc[name] = displayValue;
			return acc;
		}, {});
	} else newCard.filterData = {};

	const { selectedFilters = {} } = playbook;
	if (selectedFilters) {
		const { reducedSelectedFilter } = reduceSelectedFilters(
			selectedFilters
		);
		newCard = evaluateFilterOnCard(reducedSelectedFilter, newCard);
	}

	newCard.parentTable = playbook.parentTable;
	newCard.parentRecord = playbook.parentRecord;

	let htmlValues = [];

	if (!Array.isArray(newCard.activityProperties))
		newCard.activityProperties = [newCard.activityProperties];

	newCard.activityProperties = newCard.activityProperties.reduce(
		(acc, prop) => {
			const { name, ...activityProperty } = prop;
			acc[name] = { ...activityProperty };
			const {
				value,
				elementData: { type = 'string' } = {}
			} = activityProperty;
			if (
				(type === FieldType.HTML ||
					type === FieldType.TRANSLATED_HTML) &&
				value
			)
				htmlValues.push(value);
			return acc;
		},
		{}
	);

	const recordFieldList = get(
		newCard,
		'activityProperties.record_fields.value.length',
		0
	)
		? newCard.activityProperties.record_fields.value.split(',')
		: [];

	newCard.recordFieldHasComments =
		recordFieldList.includes('comments') ||
		recordFieldList.includes('work_notes');

	return {
		card: { ...newCard, configProperties: playbook.configProperties },
		htmlValues
	};
};

export const processLanes = (playbook, state) => {
	const currentUserId = state.properties.currentUserId;
	const registeredRenderers = get(state, 'playbook.registeredRenderers', []);
	let newRenderers = [];

	return playbook.lanes.reduce(
		(pbResult, lane) => {
			const { cards, stats } = lane.cards.reduce(
				(laneResult, card) => {
					if (card.filterData)
						pbResult.filterData = pushToFilter(
							state,
							card,
							pbResult.filterData,
							currentUserId
						);

					const {
						card: newCard,
						htmlValues: newHtmlValues
					} = processCard(playbook, card, currentUserId);

					pbResult.htmlValues = pbResult.htmlValues.concat(
						newHtmlValues
					);

					if (
						newCard.cardRendererTag &&
						!PLAYBOOK_RENDERERS.includes(newCard.cardRendererTag) &&
						!registeredRenderers.includes(newCard.cardRendererTag)
					)
						newRenderers.push(newCard.cardRendererTag);

					pushAssociatedRecord(pbResult.recordWatchMap, newCard);
					pushExperienceStatusRecord(
						pbResult.recordWatchMap,
						newCard
					);

					laneResult.cards.push(newCard);

					const newCardState = get(newCard, 'state.value');
					const show = newCard.show;

					if (show) {
						incrementSingleActivityStats(
							laneResult.stats,
							newCardState,
							'+'
						);

						if (newCard.prioritized) laneResult.stats.readyItems++;

						laneResult.stats.totalItems++;
					}

					return laneResult;
				},
				{
					cards: [],
					stats: {
						totalItems: 0,
						completedItems: 0,
						skippedItems: 0,
						readyItems: 0
					}
				}
			);

			pbResult.lanes.push({
				...lane,
				stats,
				cards,
				expanded: cards.some((card) => card.prioritized)
			});

			pbResult.registeredRenderers = newRenderers;
			pbResult.stats.completedItems += stats.completedItems;
			pbResult.stats.skippedItems += stats.skippedItems;
			pbResult.stats.totalItems += stats.totalItems;

			return pbResult;
		},
		{
			lanes: [],
			filterData: {},
			registeredRenderers: [],
			htmlValues: [],
			recordWatchMap: {},
			stats: {
				totalItems: 0,
				completedItems: 0,
				skippedItems: 0
			}
		}
	);
};

export const processPlaybook = (state, playbook, dispatch) => {
	if (!playbook || !playbook.lanes) {
		return {};
	}

	const configProperties = getDefaultConfigProperties(playbook) || {};
	const currentRegisteredRenderers = get(
		state,
		'playbook.registeredRenderers',
		[]
	);

	const {
		lanes,
		filterData,
		htmlValues,
		recordWatchMap,
		registeredRenderers = [],
		stats
	} = processLanes({ ...playbook, configProperties }, state);

	if (registeredRenderers.length > 0)
		getComponentsByTagNames(registeredRenderers);

	pushRecord(recordWatchMap, playbook.parentTable, playbook.parentRecord);
	dispatch(PLAYBOOK_RW_ADD_AND_REMOVE_WATCHING_RECORDS, {
		toBeAdded: recordWatchMap
	});

	if (htmlValues.length) dispatch(LINK_INTERCEPTOR_SAVE_URLS, { htmlValues });

	return {
		...playbook,
		registeredRenderers: [
			...currentRegisteredRenderers,
			...registeredRenderers
		],
		stats,
		configProperties,
		filterData,
		lanes
	};
};
