import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { get } from 'lodash';
import {
	NOW_ACCORDION_EXPANDED_SET,
	PLAYBOOK_ERROR,
	PLAYBOOK_FETCH_ALL_PLAYBOOK_CONTEXTS,
	PLAYBOOK_FETCH_ALL_PLAYBOOK_CONTEXTS_GQL,
	PLAYBOOK_FETCH_ALL_PLAYBOOK_CONTEXTS_STARTED,
	PLAYBOOK_FETCH_ALL_PLAYBOOK_CONTEXTS_SUCCEEDED,
	PLAYBOOK_FETCH_ALL_PLAYBOOK_ERROR,
	PLAYBOOK_FETCH_PLAYBOOKS,
	PLAYBOOK_FETCH_PLAYBOOKS_DEBOUNCED,
	PLAYBOOK_FETCH_PLAYBOOK_SUCCEEDED,
	PLAYBOOK_REQUEST_FETCH_PLAYBOOKS,
	PLAYBOOK_SUBSCRIBE
} from '../../constants';
import { debounce } from '../../effects';
import { PLAYBOOK_QUERY } from '../../gql.constants';
import { setCoeffectsPayload } from './utils';

const FetchPlaybookContextsByParentRecordQuery = `
	query($parentTable: String!, $parentSysId: String!, $playbookExperienceId: String) {
		GlidePlaybookContext_Query {
			getPlaybookContextsByParentRecord (parentTable: $parentTable, parentSysId: $parentSysId, playbookExperienceId: $playbookExperienceId) {
				provider
				ambChannel
				playbookContexts {
					${PLAYBOOK_QUERY}
				}
			}
		}
	}
`;

const FetchPlaybookContextsByIdQuery = `
query($sysIds: [String!], $provider: String, $playbookExperienceId: String) {
	GlidePlaybookContext_Query {
		getPlaybookContexts (sysIds: $sysIds, provider: $provider, playbookExperienceId: $playbookExperienceId) {
			${PLAYBOOK_QUERY}
		}
	}
}
`;

export const fetchPlaybooksDebouncedEffect = ({
	action,
	dispatch,
	state,
	properties
}) => {
	const sysIds = get(action, 'payload.sysIds', []);
	const provider = state.provider;
	const playbookExperienceId = properties.playbookExperienceId;

	if (!sysIds.length || !provider) return;

	dispatch(PLAYBOOK_FETCH_PLAYBOOKS, {
		sysIds,
		provider,
		playbookExperienceId
	});
};

export const createGraphQLActionHandlers = (getAMBChannel, processPlaybook) => {
	const fetchAllPlaybookContextsEffect = createGraphQLEffect(
		FetchPlaybookContextsByParentRecordQuery,
		{
			variableList: [
				'parentTable',
				'parentSysId',
				'playbookExperienceId'
			],
			startActionType: PLAYBOOK_FETCH_ALL_PLAYBOOK_CONTEXTS_STARTED,
			successActionType: PLAYBOOK_FETCH_ALL_PLAYBOOK_CONTEXTS_SUCCEEDED,
			errorActionType: PLAYBOOK_FETCH_ALL_PLAYBOOK_ERROR
		}
	);

	const fetchAllPlaybookContextStartedEffect = ({ updateProperties }) => {
		updateProperties({
			loading: true,
			playbookContextIdsInOrder: [],
			playbooksByContextId: [],
			notification: null
		});
	};

	const fetchAllPlaybooksFailedEffect = ({ updateProperties, dispatch }) => {
		updateProperties({
			loading: false
		});
		dispatch(PLAYBOOK_ERROR);
	};

	const fetchAllPlaybookContextsSucceededEffect = ({
		state,
		action,
		dispatch,
		updateState,
		updateProperties
	}) => {
		const errors = get(action, 'payload.errors', []);

		const playbookContexts = get(
			action,
			'payload.data.GlidePlaybookContext_Query.getPlaybookContextsByParentRecord.playbookContexts',
			[]
		);

		const ambChannel = get(
			action,
			'payload.data.GlidePlaybookContext_Query.getPlaybookContextsByParentRecord.ambChannel',
			''
		);

		const provider = get(
			action,
			'payload.data.GlidePlaybookContext_Query.getPlaybookContextsByParentRecord.provider',
			''
		);

		if (errors.length > 0 || !ambChannel || !provider) {
			updateProperties({ loading: false });
			dispatch(PLAYBOOK_ERROR);
			return;
		}

		const playbookContextIdsInOrder = playbookContexts.map(
			({ sysId }) => sysId
		);
		const playbooksByContextId = {};

		for (let playbookContext of playbookContexts) {
			const { sysId } = playbookContext;
			const playbook = processPlaybook(state, playbookContext, dispatch);

			if (!playbook) return;

			playbooksByContextId[sysId] = playbook;
		}

		updateState({
			ambChannel,
			provider
		});

		updateProperties({
			loading: false,
			playbookContextIdsInOrder,
			playbooksByContextId
		});

		dispatch(PLAYBOOK_SUBSCRIBE, {
			channelId: getAMBChannel(ambChannel, state.properties.parentSysId),
			subscribe: true
		});
	};

	const fetchPlaybooksEffect = createGraphQLEffect(
		FetchPlaybookContextsByIdQuery,
		{
			variableList: ['sysIds', 'provider', 'playbookExperienceId'],
			successActionType: PLAYBOOK_FETCH_PLAYBOOK_SUCCEEDED,
			errorActionType: PLAYBOOK_FETCH_ALL_PLAYBOOK_ERROR
		}
	);

	const fetchPlaybooksSucceededEffect = ({
		state,
		action,
		dispatch,
		updateProperties
	}) => {
		const playbookContexts = get(
			action,
			'payload.data.GlidePlaybookContext_Query.getPlaybookContexts',
			[]
		);

		const {
			playbookContextIdsInOrder,
			playbooksByContextId
		} = state.properties;

		const newSysIds = [];
		const newPlaybooks = {};
		playbookContexts.forEach((playbookContext) => {
			const playbook = processPlaybook(state, playbookContext, dispatch);
			const { sysId } = playbook;
			newPlaybooks[sysId] = playbook;
			if (!playbooksByContextId.hasOwnProperty(sysId))
				newSysIds.push(sysId);
		});

		updateProperties({
			loading: false,
			playbookContextIdsInOrder: [
				...playbookContextIdsInOrder,
				...newSysIds
			],
			playbooksByContextId: { ...playbooksByContextId, ...newPlaybooks }
		});
	};

	const requestFetchPlaybooksEffect = ({ action, state, dispatch }) => {
		const sysIds = get(action, 'payload.sysIds', []);
		const provider = state.provider;

		if (!sysIds.length || !provider) return;

		dispatch(PLAYBOOK_FETCH_PLAYBOOKS_DEBOUNCED, {
			sysIds
		});
	};

	return {
		[PLAYBOOK_FETCH_PLAYBOOKS]: fetchPlaybooksEffect,
		[PLAYBOOK_REQUEST_FETCH_PLAYBOOKS]: requestFetchPlaybooksEffect,
		[PLAYBOOK_FETCH_PLAYBOOKS_DEBOUNCED]: debounce(
			fetchPlaybooksDebouncedEffect,
			{
				delay: 1000,
				maxWait: 2000,
				aggregate: (args = [], nextArgs = []) => {
					const idx = nextArgs.length - 1 || 0; // 'coeffects' is the last argument passed to Action Handler
					const oldContextIds = get(
						args[idx],
						'action.payload.sysIds',
						[]
					);
					const newContextIds = get(
						nextArgs[idx],
						'action.payload.sysIds',
						[]
					);
					const newArgs = [...nextArgs];

					newArgs[idx] = setCoeffectsPayload(nextArgs[idx], {
						sysIds: [...oldContextIds, ...newContextIds]
					});
					return newArgs;
				}
			}
		),
		[PLAYBOOK_FETCH_ALL_PLAYBOOK_CONTEXTS]: {
			effect: ({ dispatch, action: { payload } }) => {
				dispatch(PLAYBOOK_FETCH_ALL_PLAYBOOK_CONTEXTS_GQL, payload);
			},
			modifier: { name: 'debounce', delay: 10 }
		},
		[PLAYBOOK_FETCH_ALL_PLAYBOOK_CONTEXTS_GQL]: fetchAllPlaybookContextsEffect,
		[PLAYBOOK_FETCH_ALL_PLAYBOOK_CONTEXTS_STARTED]: fetchAllPlaybookContextStartedEffect,
		[PLAYBOOK_FETCH_ALL_PLAYBOOK_CONTEXTS_SUCCEEDED]: fetchAllPlaybookContextsSucceededEffect,
		[PLAYBOOK_FETCH_ALL_PLAYBOOK_ERROR]: fetchAllPlaybooksFailedEffect,
		[PLAYBOOK_FETCH_PLAYBOOK_SUCCEEDED]: fetchPlaybooksSucceededEffect,
		[NOW_ACCORDION_EXPANDED_SET]: ({
			action,
			state: {
				properties: { playbooksByContextId }
			},
			updateProperties
		}) => {
			const {
				playbookContextId,
				laneOrder = null,
				cardOrder = null,
				value: expanded
			} = (action || {}).payload || {};
			// Do nothing if no lane is specified
			if (laneOrder === null) return;

			const playbook = playbooksByContextId[playbookContextId];

			let newLanes = [...playbook.lanes];
			const laneIndex = newLanes.findIndex(
				({ order }) => order === laneOrder
			);
			let newCards = [...newLanes[laneIndex].cards];

			// Lane expand/collapse
			if (cardOrder === null) {
				// No card is specified. A lane has been expanded.
				// Remove defer from any cards that are expanded and deferred
				if (expanded) {
					newCards = newCards.map((card) => {
						if (!card.prioritized || !card.defer) return card;
						return { ...card, defer: false };
					});
				}

				// Set lane expanded property as it likely changed
				newLanes[laneIndex].expanded = expanded;
				// Card expand/collapse
			} else {
				// reflect the new expanded/collapsed card state if there is a change
				newCards[cardOrder] = {
					...newCards[cardOrder],
					prioritized: expanded,
					defer: false
				};
				newLanes[laneIndex] = {
					...newLanes[laneIndex],
					cards: newCards
				};
			}

			updateProperties(() => ({
				path: `playbooksByContextId.${playbookContextId}.lanes`,
				operation: 'set',
				value: newLanes
			}));
		}
	};
};
