import { get } from 'lodash';
import {
	PANEL_NAVIGATION_UPDATE_MAP,
	PANEL_NAVIGATION_TAKE_FOCUS
} from './constants';

export default {
	[PANEL_NAVIGATION_UPDATE_MAP]: ({
		state: { navMap },
		action: {
			payload: { mapKey, mapEntry, operation = 'assign' }
		},
		updateState
	}) => {
		const { [mapKey]: currentMapEntry } = navMap || {};
		for (let entryKey in mapEntry)
			if (
				!navMap ||
				!currentMapEntry ||
				!currentMapEntry[entryKey] ||
				currentMapEntry[entryKey] !== mapEntry[entryKey]
			)
				return updateState({
					path: `navMap.${mapKey}`,
					value: mapEntry,
					operation
				});
	},
	[PANEL_NAVIGATION_TAKE_FOCUS]: ({ state: { navMap, focusNavId } }) => {
		const navData = get(navMap, focusNavId);

		if (navData && navData.domRef) navData.domRef.focus();
	}
};
