import { actionTypes } from '@servicenow/ui-core';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { get } from 'lodash';
import { preWsConfigSysId } from 'sn-uxpage-presource';
import {
	GLOBAL_TABLE,
	PLAYBOOK_BOOTSTRAP_DECLARATIVE_ACTIONS,
	PLAYBOOK_ERROR,
	PLAYBOOK_FETCH_DECLARATIVE_ACTIONS,
	PLAYBOOK_FETCH_DECLARATIVE_ACTIONS_SUCCEEDED
} from '../../../constants';
import {
	declarativeActionQuery,
	declarativeActionResponse
} from './declarative-action-query';

const { COMPONENT_PROPERTY_CHANGED, COMPONENT_BOOTSTRAPPED } = actionTypes;

const excludedPropertyChanges = [
	// When we receive and save new declarativeActions we don't want to trigger
	// another refetch
	'declarativeActions'
];

const fetchPlaybookDeclarativeActionsSucceededEffect = ({
	action,
	updateProperties
}) => {
	if (action.payload.errors && action.payload.errors.length) return null;

	const declarativeActions = get(
		action,
		`payload.${declarativeActionResponse}`,
		[]
	);
	updateProperties({ declarativeActions });
};

const fetchPlaybookDeclarativeActions = createGraphQLEffect(
	declarativeActionQuery,
	{
		variableList: [
			'table',
			'sysId',
			'view',
			'source',
			'workspaceConfigId',
			'parentTable',
			'parentSysId',
			'playbookExperienceId',
			'activityDefinitionId'
		],
		successActionType: PLAYBOOK_FETCH_DECLARATIVE_ACTIONS_SUCCEEDED,
		errorActionType: PLAYBOOK_ERROR
	}
);

export const daActionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: ({ state, dispatch, updateState }) => {
		updateState({
			workspaceConfigId: preWsConfigSysId.getPreSource(state)
		});

		dispatch(PLAYBOOK_BOOTSTRAP_DECLARATIVE_ACTIONS);
	},
	[COMPONENT_PROPERTY_CHANGED]: {
		effect: ({ action, dispatch }) => {
			const {
				payload: { name }
			} = action;

			if (!excludedPropertyChanges.includes(name))
				dispatch(PLAYBOOK_BOOTSTRAP_DECLARATIVE_ACTIONS);
		},
		modifier: {
			name: 'debounce',
			delay: 20
		}
	},
	[PLAYBOOK_BOOTSTRAP_DECLARATIVE_ACTIONS]: ({
		state,
		dispatch,
		properties
	}) =>
		dispatch(PLAYBOOK_FETCH_DECLARATIVE_ACTIONS, {
			table: properties.associatedTable || GLOBAL_TABLE,
			sysId: properties.associatedSysId,
			view: properties.view,
			source: properties.modelId,
			workspaceConfigId: state.workspaceConfigId,
			parentTable: properties.parentTable,
			parentSysId: properties.parentSysId,
			playbookExperienceId: properties.playbookExperienceId,
			activityDefinitionId: properties.activityDefinitionId
		}),
	[PLAYBOOK_FETCH_DECLARATIVE_ACTIONS]: fetchPlaybookDeclarativeActions,
	[PLAYBOOK_FETCH_DECLARATIVE_ACTIONS_SUCCEEDED]: fetchPlaybookDeclarativeActionsSucceededEffect
};
