const findActivityIndexAndOperation = (newActivity, cards) => {
	const noResult = { operation: null, activityIdx: -1 };

	if (!Array.isArray(cards)) return noResult;

	for (let activityIdx = 0; activityIdx < cards.length; activityIdx++) {
		const activity = cards[activityIdx];
		// We're not in the right lane. Abandon.
		if (newActivity.laneName !== activity.laneName) return noResult;

		// We've found the existing activity. Update.
		if (activity.contextId === newActivity.contextId)
			return { operation: 'update', activityIdx };

		// We've passed where the activity is expected to have been without finding it. Insert at the current index.
		if (
			newActivity.order != '-1' &&
			parseInt(activity.order) > parseInt(newActivity.order)
		)
			return { operation: 'insert', activityIdx };
	}

	// We never found the activity nor passed its order within the lane. Insert at the end.
	return { operation: 'insert', activityIdx: cards.length };
};

export default findActivityIndexAndOperation;
