import { get } from 'lodash';
import { EVENT_KEY } from '../../constants';
import {
	PANEL_NAVIGATION_FOCUS_ANCHOR_NAV_ID,
	PANEL_NAVIGATION_TAKE_FOCUS
} from './constants';

const handleArrowKey = (
	navMap,
	navId,
	direction,
	dispatch,
	onFocusDispatchId,
	dispatchSource
) => {
	const navData = get(navMap, navId);
	const navIdOrData = navData && navData[direction];
	const data =
		navIdOrData &&
		(typeof navIdOrData === 'object'
			? navIdOrData
			: get(navMap, navIdOrData));

	if (data && data.domRef) data.domRef.focus();
	else if (data && data.dispatchId)
		dispatch(data.dispatchId, { source: data.source });
	else if (onFocusDispatchId && onFocusDispatchId.hasOwnProperty(direction))
		dispatch(onFocusDispatchId[direction], {
			source: dispatchSource
		});

	return navIdOrData && typeof navIdOrData === 'string' ? navIdOrData : navId;
};

export default [
	{
		events: ['click'],
		effect({
			action: {
				payload: { event: e }
			},
			state: { navMap, focusNavId },
			updateState
		}) {
			const ePath = e.composedPath();
			const navElement =
				ePath &&
				ePath.find((elm) => elm && elm.dataset && elm.dataset.nav);
			const navId = navElement && get(navElement, 'dataset.nav');

			if (navId && navId !== focusNavId && get(navMap, navId))
				updateState({ focusNavId: navId });
		}
	},
	{
		events: ['focus'],
		effect({
			action: {
				payload: { event: e }
			},
			dispatch
		}) {
			const ePath = e.composedPath();
			let navId = get(ePath, '[0].dataset.nav');

			if (navId === PANEL_NAVIGATION_FOCUS_ANCHOR_NAV_ID)
				dispatch(PANEL_NAVIGATION_TAKE_FOCUS);
		}
	},
	{
		events: ['keydown'],
		effect({
			action: {
				payload: { event: e }
			},
			properties: { dir },
			state: { navMap, focusNavId, onFocusDispatchId, dispatchSource },
			dispatch,
			updateState
		}) {
			const ePath = e.composedPath();
			let navId = get(ePath, '[0].dataset.nav');

			if (!navId && ePath && ePath[0].tagName === 'BUTTON') {
				const elm = ePath.find(
					(elm) => elm && elm.dataset && elm.dataset.nav
				);
				navId = elm && elm.dataset && elm.dataset.nav;
			}

			const navData = get(navMap, navId);

			switch (e.key) {
				case EVENT_KEY.HOME:
					let upNavId = navData && navData.up;
					let upData = null;

					while (upNavId) {
						upData = get(navMap, upNavId);
						upNavId = upData && upData.up;
					}

					if (upData && upData.domRef) {
						upData.domRef.focus();
						navId = upNavId;
					}

					break;
				case EVENT_KEY.ARROW.UP:
					navId = handleArrowKey(
						navMap,
						navId,
						'up',
						dispatch,
						onFocusDispatchId,
						dispatchSource
					);
					break;
				case EVENT_KEY.ARROW.DOWN:
					navId = handleArrowKey(
						navMap,
						navId,
						'down',
						dispatch,
						onFocusDispatchId,
						dispatchSource
					);
					break;
				case EVENT_KEY.END:
					let downNavId = navData && navData.down;
					let downData = null;

					while (downNavId) {
						downData = get(navMap, downNavId);
						downNavId = downData && downData.down;
					}

					if (downData && downData.domRef) {
						downData.domRef.focus();
						navId = downNavId;
					}

					break;
				case EVENT_KEY.ARROW.LEFT:
					navId = handleArrowKey(
						navMap,
						navId,
						dir === 'rtl' ? 'right' : 'left',
						dispatch,
						onFocusDispatchId,
						dispatchSource
					);
					break;
				case EVENT_KEY.ARROW.RIGHT:
					navId = handleArrowKey(
						navMap,
						navId,
						dir === 'rtl' ? 'left' : 'right',
						dispatch,
						onFocusDispatchId,
						dispatchSource
					);
					break;
				case EVENT_KEY.TAB:
				case EVENT_KEY.PAGE.UP:
				case EVENT_KEY.PAGE.DOWN:
				default:
					return;
			}

			const newNavData = navId && get(navMap, navId);

			if (newNavData && newNavData.domRef && navId !== focusNavId)
				updateState({ focusNavId: navId });
		}
	}
];
