import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import styles from './styles.scss';

/**
 * ## Playbook Cancelled Popover Content
 *
 * @seismicElement now-playbook-cancelled-popover
 * @summary Component to display as the content slot of now-playbook-cancelled-popover
 */
createCustomElement('now-playbook-cancelled-popover-content', {
	renderer: {
		type: snabbdom,
		view: ({ properties: { cancellationReason, canceledBy } }) => (
			<div className="popover-content-container">
				<b>{t('Canceled by {0}', canceledBy)}</b>
				<br />
				{cancellationReason}
			</div>
		)
	},
	styles,
	properties: {
		/**
		 * the text for the reason the playbook was canceled - displayed in popover
		 * @type {String}
		 */
		cancellationReason: {
			default: ''
		},

		/**
		 * the text for the who canceled the playbook - displayed in popover
		 * @type {String}
		 */
		canceledBy: {
			default: ''
		}
	}
});
