import { GFORM_ACTIONS } from '@devsnc/sn-scripting-environment';
import { actionTypes, createCustomElement } from '@servicenow/ui-core';
import { snabbdom } from '@servicenow/ui-renderer-snabbdom';
import {
	DECLARATIVE_ACTION,
	GFORM_VALIDATE_SUCCESS,
	PLAYBOOK_RECORD_SET_FIELDS
} from '../../constants';
import { ensureFieldDictionaryType } from '../utils';
const { COMPONENT_PROPERTY_CHANGED, COMPONENT_BOOTSTRAPPED } = actionTypes;

createCustomElement('sn-playbook-form-section-footer', {
	renderer: {
		type: snabbdom,
		view: () => null
	},
	properties: {
		associatedRecordModCount: {
			default: 0
		},
		actionSelected: {
			default: null
		},
		dispatchAction: {
			default: null
		},
		fields: {
			default: null
		}
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({
			state: {
				properties: { fields }
			},
			dispatch
		}) => {
			if (fields) {
				dispatch(PLAYBOOK_RECORD_SET_FIELDS, fields);
			}
		},
		[COMPONENT_PROPERTY_CHANGED]: ({
			action,
			state,
			updateState,
			dispatch
		}) => {
			const {
				properties: { fields },
				actionSelectedValue = null
			} = state;
			const {
				payload: { name, value }
			} = action;

			//If we get a fields update from the form section, we want to update the
			//declarative model with the update fields
			if (name === 'fields') {
				dispatch(PLAYBOOK_RECORD_SET_FIELDS, value);

				//If we get actionSelected, it means a DA was clicked that requries form fields
				//We perform a GFORM VALIDATE to validate the fields
				//We save the value of the DA so we can dispatch after the form is validated and updated
			} else if (name === 'actionSelected') {
				updateState({ actionSelectedValue: value });

				if (value !== null)
					dispatch(GFORM_ACTIONS.GFORM_VALIDATE, {
						successActionName: GFORM_VALIDATE_SUCCESS
					});

				//If we get dispatchAction, dispatch the currently saved action with the updated fields
			} else if (name === 'dispatchAction') {
				if (value != null && actionSelectedValue != null) {
					const {
						action: selectedAction,
						model
					} = actionSelectedValue;
					const fieldArray = ensureFieldDictionaryType(fields);

					dispatch(DECLARATIVE_ACTION, {
						action: selectedAction,
						model: {
							...model,
							fields: fieldArray
						}
					});

					//reset actionSelectedValue to null to show that action has been peformed
					updateState({ actionSelectedValue: null });
				}
			}
		}
	}
});
