import { get, isEmpty } from 'lodash';
import {
	NOW_DROPDOWN_CUSTOM_TARGET_ITEM_CLICKED,
	NOW_DROPDOWN_CUSTOM_TARGET_OPENED_SET,
	PLAYBOOK_CLEAN_SELECTED_FILTERS,
	PLAYBOOK_NOTIFICATION,
	PLAYBOOK_UPDATE_FILTER
} from '../../constants';
import { NOTIFICATION_ZERO_FILTER_RESULTS } from '../../notification.constants';
const filterActionHandlers = (
	cleanUpFilter,
	combineSelectedFilters,
	evaluateFilterOnCard,
	reduceSelectedFilters
) => {
	return {
		[NOW_DROPDOWN_CUSTOM_TARGET_OPENED_SET]: ({
			action,
			updateProperties
		}) => {
			const { playbookContextId, value } = get(action, 'payload', null);
			const filterOpenedPath = `playbooksByContextId.${playbookContextId}.filterOpened`;

			updateProperties(() => ({
				path: filterOpenedPath,
				operation: 'set',
				value
			}));
		},
		[NOW_DROPDOWN_CUSTOM_TARGET_ITEM_CLICKED]: ({
			action,
			dispatch,
			state,
			updateProperties
		}) => {
			const { label, filterSection } = get(action, 'payload.item', {});
			const { playbookContextId } = get(action, 'payload', null);

			if (!playbookContextId) return;

			const selectedFiltersPath = `playbooksByContextId.${playbookContextId}.selectedFilters`;

			const selectedFilters = get(
				state.properties,
				selectedFiltersPath,
				{}
			);

			if (!filterSection) return;

			const hasItem = (selectedFilters[filterSection] || {})[label];
			// If panel item is already selected, meaning 'hasItem' has a truthy value,
			// we want to unselect this panel item
			const shouldBeSelected = !hasItem;

			const value = combineSelectedFilters(selectedFilters, {
				filterSection,
				label,
				shouldBeSelected
			});

			updateProperties(() => ({
				path: selectedFiltersPath,
				operation: 'set',
				value
			}));

			dispatch(PLAYBOOK_CLEAN_SELECTED_FILTERS, { playbookContextId });
			dispatch(PLAYBOOK_UPDATE_FILTER, { playbookContextId });
		},
		[PLAYBOOK_CLEAN_SELECTED_FILTERS]: ({
			state: { properties },
			action: { payload: { playbookContextId } = {} },
			updateProperties
		}) => {
			if (!playbookContextId) return;
			const playbookPath = `playbooksByContextId.${playbookContextId}`;
			const playbook = get(properties, playbookPath, {});
			const {
				selectedFilters = {},
				toBeCleanedSelectedFilterOption = {},
				filterData = {}
			} = playbook;

			if (isEmpty(toBeCleanedSelectedFilterOption)) return;

			const cleanedFilters = cleanUpFilter(
				filterData,
				selectedFilters,
				toBeCleanedSelectedFilterOption
			);

			const {
				filterData: newFilterData,
				selectedFilters: newSelectedFilters
			} = cleanedFilters;

			updateProperties(() => ({
				path: playbookPath,
				operation: 'set',
				value: {
					...playbook,
					filterData: newFilterData,
					selectedFilters: newSelectedFilters,
					toBeCleanedSelectedFilterOption: {}
				}
			}));
		},
		[PLAYBOOK_UPDATE_FILTER]: ({
			state: { properties },
			action: { payload: { playbookContextId } = {} },
			updateProperties,
			dispatch
		}) => {
			if (!playbookContextId) return;
			const playbookPath = `playbooksByContextId.${playbookContextId}`;
			const playbook = get(properties, playbookPath, {});
			const { lanes = [], selectedFilters = {} } = playbook;
			let hasResults = false;

			const {
				reducedSelectedFilter,
				selectedFilterCount
			} = reduceSelectedFilters(selectedFilters);

			const newLanes = lanes.map((lane) => {
				let shouldExpand = false;

				const newCards = lane.cards.map((card) => {
					if (isEmpty(reducedSelectedFilter)) {
						shouldExpand = undefined;

						return {
							...card,
							shouldExpand: false,
							hidden: false
						};
					}

					const newCard = evaluateFilterOnCard(
						reducedSelectedFilter,
						card
					);
					shouldExpand = !newCard.hidden || shouldExpand;
					return newCard;
				});

				hasResults = shouldExpand || hasResults;
				return {
					...lane,
					shouldExpand,
					cards: newCards
				};
			});

			if (!hasResults && selectedFilterCount)
				dispatch(PLAYBOOK_NOTIFICATION, {
					notification: NOTIFICATION_ZERO_FILTER_RESULTS
				});
			else dispatch(PLAYBOOK_NOTIFICATION, { notification: null });

			updateProperties(() => ({
				path: playbookPath,
				operation: 'set',
				value: {
					...playbook,
					lanes: newLanes,
					selectedFilterCount
				}
			}));
		}
	};
};
export default filterActionHandlers;
