import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import get from 'lodash/get';
import { t } from 'sn-translate';
import {
	ADD_NOTIFICATIONS,
	CANCEL_PLAYBOOK_STAGE,
	PLAYBOOK_CANCEL_PLAYBOOK_CONTEXT_MUTATION_GQL,
	PLAYBOOK_CANCEL_PLAYBOOK_CONTEXT_SUCCEEDED,
	PLAYBOOK_ERROR,
	UPDATE_PLAYBOOK_CONTEXT,
	UPDATE_PLAYBOOK_CONTEXT_WHITELIST
} from '../../constants';

export const CancelPlaybookContextMutation = `
	mutation ($playbookContextId: String!, $playbookExperienceId: String!, $cancellationReason: String!) {
		GlidePlaybookContext_Mutation {
			cancelPlaybookContext(playbookContextId: $playbookContextId, playbookExperienceId: $playbookExperienceId, cancellationReason: $cancellationReason) {
				cancellationReason
				canceledBy
				sysId
				title
				state {
					value
					displayValue
				}
			}
		}
	}
`;

const playbookActionsHandlers = {
	[UPDATE_PLAYBOOK_CONTEXT]: {
		effect: ({ action, updateProperties }) => {
			const {
				payload: { playbookContextId = '', fieldsToUpdate = {} }
			} = action;

			const verifiedFieldsToUpdate = Object.entries(
				fieldsToUpdate
			).reduce((verifiedFields, [key, value]) => {
				if (UPDATE_PLAYBOOK_CONTEXT_WHITELIST.includes(key)) {
					verifiedFields[key] = value;
				}

				return verifiedFields;
			}, {});

			if (
				Object.keys(verifiedFieldsToUpdate).length &&
				playbookContextId
			) {
				updateProperties(() => ({
					path: `playbooksByContextId.${playbookContextId}`,
					operation: 'assign',
					value: verifiedFieldsToUpdate
				}));
			}
		},
		stopPropagation: true
	},
	[PLAYBOOK_CANCEL_PLAYBOOK_CONTEXT_MUTATION_GQL]: createGraphQLEffect(
		CancelPlaybookContextMutation,
		{
			variableList: [
				'playbookContextId',
				'playbookExperienceId',
				'cancellationReason'
			],
			successActionType: PLAYBOOK_CANCEL_PLAYBOOK_CONTEXT_SUCCEEDED,
			errorActionType: PLAYBOOK_ERROR
		}
	),
	[PLAYBOOK_CANCEL_PLAYBOOK_CONTEXT_SUCCEEDED]: ({ action, dispatch }) => {
		const playbookContext = get(
			action,
			'payload.data.GlidePlaybookContext_Mutation.cancelPlaybookContext',
			{}
		);

		const {
			sysId: playbookContextId = null,
			cancellationReason = null,
			canceledBy = null,
			title = null,
			state: updatedState
		} = playbookContext;

		if (playbookContextId && cancellationReason) {
			dispatch(UPDATE_PLAYBOOK_CONTEXT, {
				playbookContextId,
				fieldsToUpdate: {
					cancellationReason,
					canceledBy,
					state: updatedState
				}
			});

			dispatch(ADD_NOTIFICATIONS, {
				notifications: [
					{
						type: 'info',
						message: `<div>${t(
							'{0} was canceled sucessfully',
							title
						)}</div>`
					}
				]
			});
		}
	},
	[CANCEL_PLAYBOOK_STAGE]: {
		effect: ({ dispatch, action: { payload } }) => {
			console.log(payload);
		},
		stopPropagation: true
	}
};

export default playbookActionsHandlers;
