import { get, mapValues } from 'lodash';

export const getParsedDoc = (html) => {
	if (typeof html !== 'string') return null;
	return new DOMParser().parseFromString(html, 'text/html');
};

export const stripHTML = (html) => {
	return get(getParsedDoc(html), 'body.textContent', '');
};

// Be careful when using to parse non admin user inputs, XSS vulnerability
export const decodeHTML = (html) => {
	if (/&amp;|&quot;|&#39;|'&lt;|&gt;/.test(html))
		return get(getParsedDoc(html), 'documentElement.textContent', html);

	return html;
};

export const convertNullToEmptyString = (value) =>
	value === null ? '' : value;

export const setIfAbsent = (obj = {}, key, value) => {
	if (!obj.hasOwnProperty(key)) obj[key] = value;
	return obj[key];
};

export const withStopPropagation = (handlers = {}) => {
	return mapValues(handlers, (effect) => {
		return {
			// For Seismic create effect type methods (i.e.
			// createAmbSubscriptionEffect, createGraphQLEffect) where it
			// already returns an object like { effect: <func>, args: [...] };
			...(effect.hasOwnProperty('effect') ? effect : { effect }),
			stopPropagation: true
		};
	});
};

export const getBehaviorPath = (name) => `behaviors.${name}`;

export const getBehaviorState = (name, state) =>
	get(state, getBehaviorPath(name), {});

export const setBehaviorState = (name, state, nextState) => {
	const currentState = getBehaviorState(name, state);

	return {
		path: getBehaviorPath(name),
		operation: 'set',
		value: {
			...currentState,
			...nextState
		}
	};
};
