import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import {
	cancelPlaybookBehavior,
	snLinkInterceptorBehavior
} from '../behaviors';
import actionHandlers from './actions';

createCustomElement('now-playbook-data-broker', {
	renderer: {
		type: snabbdom,
		view: () => <slot />
	},
	properties: {
		/**
		 * sysId of the parent record associated with the playbooks
		 * @type {String}
		 */
		parentSysId: {
			reflect: true
		},
		/**
		 * table name of the parent record associated with the playbooks
		 * @type {String}
		 */
		parentTable: {
			reflect: true
		},
		/**
		 * playbook experience id to determine the configurations
		 * @type {String}
		 */
		playbookExperienceId: { selectable: true, default: '' },

		loading: { selectable: true, default: true },
		loadingActivities: { selectable: true, default: {} },
		playbookContextIdsInOrder: { selectable: true, default: [] },
		playbooksByContextId: { selectable: true, default: {} },
		currentUserId: {
			selectable: true,
			default: ''
		},
		parentRecordUpdateTime: {
			selectable: true,
			default: 0
		}
	},
	actionHandlers,
	behaviors: [snLinkInterceptorBehavior, cancelPlaybookBehavior]
});
