import { NOW_TEXTAREA_INPUT } from '../../constants';

export const createNowTextAreaInputBehavior = (textAreaInputField) => ({
	name: `nowTextAreaInputBehavior-${textAreaInputField}`,
	actionHandlers: {
		[NOW_TEXTAREA_INPUT]: {
			effect: ({ action: { payload }, updateState }) => {
				if (payload.textAreaField === textAreaInputField) {
					updateState({
						[textAreaInputField]: payload.fieldValue
					});
				}
			},
			stopPropagation: true
		}
	}
});
