import '@servicenow/now-heading';
import { createRef, Fragment } from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate'; //'@servicenow/library-translate';
import { PANEL_NAVIGATION_UPDATE_MAP } from '../../behaviors/sn-panel-navigation/constants';
import {
	EVENT_KEY,
	FILTERED_ICON,
	FILTER_CLASS,
	FILTER_ICON,
	FILTER_TOOLTIP,
	PLAYBOOK_COLLAPSE_TOOLTIP,
	PLAYBOOK_EXPAND_TOOLTIP,
	PLAYBOOK_MODEL_ID,
	PLAYBOOK_STANDARD_TOOLTIP_DELAY
} from '../../constants';
import '../now-playbook-cancelled-popover';
import '../sn-playbook-actions';

const PlaybookHeading = ({
	title,
	parentTable,
	parentRecord,
	playbookExperienceId,
	playbookContextId,
	state,
	filterOpened,
	filterData,
	selectedFilters,
	selectedFilterCount,
	expandStageList,
	cancellationReason,
	canceledBy,
	actionsDropdownOpened,
	onExpandOrCollapseClicked,
	navKey,
	previousNavKey,
	dispatch,
	parentRecordUpdateTime
}) => {
	const selectedItems = selectedFilters
		? Object.keys(selectedFilters).flatMap((selectedFilterSection) =>
			Object.keys(selectedFilters[selectedFilterSection]).map(
				(selectedItemLabel) =>
					`${selectedFilterSection}|${selectedItemLabel}`
			)
		  )
		: undefined;

	let filterArray = [];
	let collapseTriggerRef = createRef();

	if (filterData) filterArray = Object.values(filterData);

	return (
		<Fragment>
			<now-collapse-trigger
				ref={collapseTriggerRef}
				className="playbook-heading"
				controls={`collapse-playbook-stage-list-${playbookContextId}`}
				on-click={(e) => {
					e.stopPropagation();
					onExpandOrCollapseClicked();
				}}
				on-keydown={(e) => {
					switch (e.key) {
						case EVENT_KEY.ENTER:
						case EVENT_KEY.SPACE:
							if (
								collapseTriggerRef &&
								collapseTriggerRef.current &&
								!e.ctrlKey &&
								!e.altKey
							) {
								e.preventDefault();
								collapseTriggerRef.current.click();
							}
							break;
						default:
							return;
					}
				}}
				tabindex={0}
				append-to-payload={{
					playbookContextId,
					expandStageList: !expandStageList
				}}
				data-nav={navKey}
				hook-insert={({ elm }) => {
					if (dispatch && elm)
						dispatch(PANEL_NAVIGATION_UPDATE_MAP, {
							mapKey: navKey,
							mapEntry: {
								up: previousNavKey ? previousNavKey : '',
								domRef: elm,
								right: expandStageList
									? `pb-filter-${playbookContextId}`
									: `pb-actions-${playbookContextId}`
							}
						});

					if (previousNavKey)
						dispatch(PANEL_NAVIGATION_UPDATE_MAP, {
							mapKey: previousNavKey,
							mapEntry: { down: navKey },
							operation: 'merge'
						});
				}}
				config-aria={{
					role: 'listitem',
					'aria-labelledby': `playbook-heading-${playbookContextId}`,
					'aria-controls': `collapse-playbook-stage-list-${playbookContextId}`,
					'aria-expanded': expandStageList
				}}
				key={`playbook-collapse-trigger-${playbookContextId}`}
			>
				<div className="playbook-header-container">
					<now-heading
						label={title}
						level="2"
						variant="header-tertiary"
						wontWrap={true}
						hasNoMargin={true}
						id={`playbook-heading-${playbookContextId}`}
					/>

					{expandStageList &&
					title &&
					filterArray &&
					filterArray.length ? (
							<div
								class={{
									[FILTER_CLASS]: true,
									'filter-container': true,
									show: expandStageList
								}}
								on-click={(e) => e.stopPropagation()}
								on-keydown={(e) => {
									switch (e.key) {
										case EVENT_KEY.ENTER:
										case EVENT_KEY.SPACE:
											e.stopPropagation();
											break;
										default:
											return;
									}
								}}
							>
								<now-dropdown-custom-target
									items={filterArray}
									select="multi"
									selected-items={selectedItems}
									panel-fit-props={{
										position: ['bottom-right top-right']
									}}
									append-to-payload={{ playbookContextId }}
									id={`filter-dropdown-panel-${playbookContextId}`}
									on-mouseover={(e) => e.stopPropagation()}
									config-aria={{
										role: 'listbox',
										'aria-label': t(
											`${FILTER_TOOLTIP} Selection Items`
										)
									}}
								>
									<div slot="trigger">
										<now-button-iconic
											className={'filter-button-dropdown'}
											icon={
												selectedFilterCount
													? FILTERED_ICON
													: FILTER_ICON
											}
											size="md"
											variant="tertiary"
											label="Toggle panel"
											bare={true}
											tooltip-content={FILTER_TOOLTIP}
											data-nav={`pb-filter-${playbookContextId}`}
											hook-insert={({ elm }) =>
												dispatch &&
											typeof dispatch === 'function' &&
											elm
													? dispatch(
														PANEL_NAVIGATION_UPDATE_MAP,
														{
															mapKey: `pb-filter-${playbookContextId}`,
															mapEntry: {
																left: navKey,
																domRef: elm,
																right: `pb-actions-${playbookContextId}`
															}
														}
												  )
													: null
											}
											config-aria={{
												role: 'button',
												'aria-label': t(
													`Playbook ${FILTER_TOOLTIP}`
												)
											}}
										/>
										{selectedFilterCount ? (
											<now-badge
												size="sm"
												round={true}
												status="info"
												value={selectedFilterCount}
												variant="secondary"
												className="playbook-filter-badge"
												config-aria={{
													'aria-label': t(
														'Filter Count: {0}',
														selectedFilterCount
													)
												}}
											/>
										) : null}
									</div>
								</now-dropdown-custom-target>
							</div>
						) : null}

					{parentTable && parentRecord && playbookExperienceId ? (
						<sn-playbook-declarative-actions-data-provider
							component-id={`playbook-actions-data-provider-${playbookContextId}`}
							associatedTable={parentTable}
							associatedSysId={parentRecord}
							playbookExperienceId={playbookExperienceId}
							modelId={PLAYBOOK_MODEL_ID}
							parentRecordUpdateTime={parentRecordUpdateTime}
						>
							<sn-declarative-mediator>
								<sn-playbook-actions
									class={{
										'sn-playbook-actions': true,
										show:
											expandStageList ||
											actionsDropdownOpened
									}}
									declarative-actions={`@playbook-actions-data-provider-${playbookContextId}/declarativeActions`}
									table={parentTable}
									sys-id={parentRecord}
									playbook-context-id={playbookContextId}
									playbook-status={state.value}
									opened={actionsDropdownOpened}
									leftNavKey={
										expandStageList
											? `pb-filter-${playbookContextId}`
											: navKey
									}
									on-mouseover={(e) => e.stopPropagation()}
									config-aria={{
										'aria-label': t('Playbook Actions')
									}}
								/>
							</sn-declarative-mediator>
						</sn-playbook-declarative-actions-data-provider>
					) : null}
				</div>

				<now-icon
					class={{
						'chevron-icon': true,
						'is-expanded': expandStageList
					}}
					icon="chevron-down-outline"
					size="md"
					tabindex={-1}
				/>
			</now-collapse-trigger>
			<now-tooltip
				content={
					expandStageList
						? PLAYBOOK_COLLAPSE_TOOLTIP
						: PLAYBOOK_EXPAND_TOOLTIP
				}
				delay={PLAYBOOK_STANDARD_TOOLTIP_DELAY}
				hook-insert={({ elm }) =>
					(elm.targetRef = elm.previousElementSibling)
				}
			/>
			{cancellationReason && (
				<now-playbook-cancelled-popover
					cancellationReason={cancellationReason}
					canceledBy={canceledBy}
				/>
			)}
		</Fragment>
	);
};

export default PlaybookHeading;
