import '@servicenow/now-heading';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { get } from 'lodash';
import { t } from 'sn-translate';
import '../../activity-renderers/cards/';
import {
	PANEL_NAVIGATION_FOCUS_ANCHOR_NAV_ID,
	PANEL_NAVIGATION_SET_FOCUS_ANCHOR,
	PANEL_NAVIGATION_UPDATE_MAP
} from '../../behaviors/sn-panel-navigation/constants';
import {
	BACK_TO_STAGE_SELECTOR_NAV_ID,
	CARD_STATE_VALUES,
	EVENT_KEY,
	PLAYBOOK_CARD_MODEL_ID,
	PLAYBOOK_STAGE_DESELECT,
	PLAYBOOK_STAGE_PANEL_SOURCE,
	PLAYBOOK_STAGE_PANEL_UPDATE_FIRST_CARD_NAV_ID
} from '../../constants';
import {
	NOTIFICATION_PLAYBOOK_EMPTY,
	NOTIFICATION_PLAYBOOK_ERROR,
	NOTIFICATION_PLAYBOOK_STAGE_EMPTY,
	NOTIFICATION_PLAYBOOK_STAGE_ERROR,
	NOTIFICATION_ZERO_STAGE_FILTER_RESULTS
} from '../../notification.constants';
import '../../sn-playbook-card-actions';
import EmptyState from '../empty-state';
import PlaybookCard from '../playbook-card';

const view = (
	{
		properties: {
			playbookContextIdsInOrder,
			playbooksByContextId,
			playbookExperienceId,
			loadingActivities,
			selectedStage,
			userId,
			compactMode,
			parentRecordUpdateTime
		},
		mountedStageMap,
		componentId
	},
	{ dispatch }
) => (
	<Fragment>
		<div
			contenteditable={true}
			className="focus-anchor"
			tabindex={-1}
			hook-insert={({ elm }) =>
				dispatch(PANEL_NAVIGATION_SET_FOCUS_ANCHOR, {
					focusAnchorRef: elm,
					source: PLAYBOOK_STAGE_PANEL_SOURCE
				})
			}
			data-nav={PANEL_NAVIGATION_FOCUS_ANCHOR_NAV_ID}
		/>

		{playbookContextIdsInOrder.map((playbookContextId) => {
			const playbook = playbooksByContextId[playbookContextId];
			const {
				state: { value: playbookState } = {},
				configProperties: { expandFirstPrioritizedCard = true } = {}
			} = playbook;

			if (
				selectedStage.playbookContextId === playbookContextId &&
				(!playbook.lanes || playbook.lanes.length < 1)
			)
				return (
					<EmptyState
						title={NOTIFICATION_PLAYBOOK_EMPTY.header}
						text={NOTIFICATION_PLAYBOOK_EMPTY.body}
						illustration={NOTIFICATION_PLAYBOOK_EMPTY.illustration}
					/>
				);

			return playbook.lanes.map((stage) => {
				let previousContextId = '';
				let oneCardExpanded = false;
				let firstVisibleCardNotReached = true;
				const stageId = `${playbookContextId}.${stage.name}`;
				const hide =
					selectedStage.playbookContextId !== playbookContextId ||
					selectedStage.stageName !== stage.name;
				const {
					state: { value: stageState }
				} = stage;
				const showEmptyStagePanel = stage.cards.every(
					(card) => card.hidden || !card.show
				);
				const someCardsHaveError = stage.cards.some(
					(card) => card.state.value === CARD_STATE_VALUES.ERROR.value
				);

				const emptyStateMessage =
					playbookState === CARD_STATE_VALUES.ERROR.value &&
					stage.cards.length === 0
						? NOTIFICATION_PLAYBOOK_ERROR
						: stage.cards.length < 1
							? NOTIFICATION_PLAYBOOK_STAGE_EMPTY
							: stageState === CARD_STATE_VALUES.ERROR.value &&
						  !someCardsHaveError
								? NOTIFICATION_PLAYBOOK_STAGE_ERROR
								: null;

				if (emptyStateMessage && !hide)
					return (
						<EmptyState
							title={emptyStateMessage.header}
							text={emptyStateMessage.body}
							illustration={emptyStateMessage.illustration}
						/>
					);

				const stagePath = `["${playbook.sysId}"]["${stage.name}"]`;

				if (!get(mountedStageMap, stagePath)) return null;

				return (
					<div
						class={{
							'now-stage-section': true,
							hide
						}}
						role="list"
						aria-labelledby={`stage-heading-${stageId}`}
						aria-hidden={hide}
						key={`stage-section-key-${stageId}`}
					>
						{compactMode ? (
							<div
								className="stage-deselect"
								on-click={() =>
									dispatch(PLAYBOOK_STAGE_DESELECT)
								}
								on-keydown={(e) => {
									if (
										e.key === EVENT_KEY.ENTER ||
										e.key === EVENT_KEY.SPACE
									)
										dispatch(PLAYBOOK_STAGE_DESELECT);
								}}
								tabindex={0}
								data-nav={`${BACK_TO_STAGE_SELECTOR_NAV_ID}-${stageId}`}
								hook-insert={({ elm }) =>
									elm
										? dispatch(
											PANEL_NAVIGATION_UPDATE_MAP,
											{
												mapKey: `${BACK_TO_STAGE_SELECTOR_NAV_ID}-${stageId}`,
												mapEntry: {
													domRef: elm
												}
											}
										  )
										: null
								}
							>
								<now-icon
									icon="chevron-left-outline"
									size="md"
								/>
								<span>{t('Back to {0}', playbook.title)}</span>
							</div>
						) : null}
						{showEmptyStagePanel ? (
							<EmptyState
								title={
									NOTIFICATION_ZERO_STAGE_FILTER_RESULTS.header
								}
								text={
									NOTIFICATION_ZERO_STAGE_FILTER_RESULTS.body
								}
								illustration={
									NOTIFICATION_ZERO_STAGE_FILTER_RESULTS.illustration
								}
							/>
						) : (
							<now-heading
								label={stage.title}
								level="3"
								variant="header-tertiary"
								hasNoMargin={true}
								wontWrap={true}
								class={{
									title: true,
									'is-compact': compactMode
								}}
								now-mode={hide ? 'suspend' : 'active'}
								id={`stage-heading-${stageId}`}
								config-aria={{
									role: 'heading',
									'aria-label': t(
										'{0} Playbook Stage Activity Cards',
										stage.title
									)
								}}
							/>
						)}

						{stage.cards.map((card) => {
							const showCard = card.show && !card.hidden;
							const expandCard =
								expandFirstPrioritizedCard &&
								!oneCardExpanded &&
								card.prioritized;
							if (!hide && showCard) {
								if (firstVisibleCardNotReached) {
									const firstCardNavId = `card-header-${card.contextId}`;
									firstVisibleCardNotReached = false;
									dispatch(
										PLAYBOOK_STAGE_PANEL_UPDATE_FIRST_CARD_NAV_ID,
										{
											cardNavId: firstCardNavId,
											takeFocus: compactMode
										}
									);
								}

								dispatch(PANEL_NAVIGATION_UPDATE_MAP, {
									mapKey: `card-header-${card.contextId}`,
									mapEntry: {
										up:
											!firstVisibleCardNotReached &&
											previousContextId
												? `card-header-${previousContextId}`
												: null
									}
								});

								if (previousContextId)
									dispatch(PANEL_NAVIGATION_UPDATE_MAP, {
										mapKey: `card-header-${previousContextId}`,
										mapEntry: {
											down: `card-header-${card.contextId}`
										}
									});

								if (expandCard) oneCardExpanded = true;
								previousContextId = card.contextId;
							}

							card.playbookExperienceId = playbookExperienceId;
							card.modelId = PLAYBOOK_CARD_MODEL_ID; //card.state = {value: 'SKIPPED', displayValue: 'Skipped'};

							return (
								<div
									class={{
										'hide-card': !showCard,
										'max-card-width': true,
										'now-m-inline--xl': compactMode,
										'now-m-inline--3xl': !compactMode,
										'now-m-block-end--md': true
									}}
									key={`card-outer-div-key-${card.contextId}`}
								>
									<PlaybookCard
										item={card}
										userId={userId}
										loadingActivities={loadingActivities}
										expandOnMountOrChange={expandCard}
										nowMode={hide ? 'suspend' : 'active'}
										playbookState={playbookState}
										playbookContextId={playbookContextId}
										dataProviderComponentId={`data-provider-${card.contextId}-${componentId}`}
										parentRecordUpdateTime={
											parentRecordUpdateTime
										}
									/>
								</div>
							);
						})}
					</div>
				);
			});
		})}
	</Fragment>
);

export default view;
