import { getComponentsByTagNames } from '@devsnc/library-uxf';
import { get } from 'lodash';
import {
	PLAYBOOK_ADD_OR_UPDATE_ACTIVITIES,
	PLAYBOOK_RENDERERS,
	PLAYBOOK_RW_ADD_AND_REMOVE_WATCHING_RECORDS
} from '../../constants';
import {
	hasAnUnwatchedAssociatedRecord,
	hasAnUnwatchedExperienceStatusRecord,
	pushAssociatedRecord,
	pushExperienceStatusRecord
} from '../ambUtils';
import { pushToFilter, removeFromFilter } from '../filterUtils';
import { processCard } from '../processingUtils';
import { findActivityIndexAndOperation, getUpdatedLaneStats } from './utils';

export default {
	[PLAYBOOK_ADD_OR_UPDATE_ACTIVITIES]: ({
		action: { payload = {} },
		dispatch,
		updateProperties
	}) => {
		const { activities = [] } = payload;

		if (!activities) return;

		// Using declarative 'updateProperties' here we ensure that the new 'playbook'
		// object created will use the most up to date 'state' object.
		// This avoids a scenario where if 'PLAYBOOK_ADD_OR_UPDATE_ACTIVITIES'
		// is triggered multiple times back to back and we end up updating the
		// state with the last new 'playbook object and overwriting all of the
		// new 'playbook' objects prior to the last one, instead of combining
		// all of them.
		//
		// This happens when we use the 'state' object from 'coeffects' and tries
		// to create a new 'playbook' immediate and then call 'updateProperties'.
		// Because 'updateProperties' is invoked asynchronous, each new 'playbook'
		// object would have relied on the old 'state' object because 'updateProperties'
		// hasn't updated the actual 'state' object yet.
		//
		// We avoid this by putting all of the processing code inside a callback
		// to the declarative 'updateProperties'. The callbacks will be invoked
		// synchronously, meaning in order, even though the 'updateProperties'
		// invocation is asynchronous.
		//
		// https://developer.servicenow.com/dev.do#!/guide/paris/now-experience/ui-framework/now-experience-ui-framework-101/helpers
		// May have to log in with your AD credentials before the documentation
		// appears
		updateProperties(({ state, properties }) => {
			const toBeAdded = {},
				toBeRemoved = {}; // For Record Watcher

			//const { playbook } = properties;
			//const nextPlaybook = { ...playbook };
			//const nextLanes = [...nextPlaybook.lanes];

			const updatedPlaybooksByContextId = {};
			const currentUserId = properties.currentUserId;
			const newRenderers = [];

			activities.forEach((activity) => {
				const { laneName, playbookContextId } = activity;
				if (!laneName) return { shouldUpdate: false };

				const playbook = get(
					properties,
					`playbooksByContextId.${playbookContextId}`,
					null
				);
				if (!playbook) return { shouldUpdate: false };

				if (!updatedPlaybooksByContextId[playbookContextId]) {
					updatedPlaybooksByContextId[playbookContextId] = {
						...playbook,
						lanes: [...(playbook.lanes || [])],
						registeredRenderers: [
							...(playbook.registeredRenderers || [])
						]
					};
				}
				const nextPlaybook =
					updatedPlaybooksByContextId[playbookContextId];

				const lane = nextPlaybook.lanes.find(
					({ name }) => name === laneName
				);
				if (!lane) return { shouldUpdate: false };

				const { cards = [] } = lane;
				const {
					operation,
					activityIdx
				} = findActivityIndexAndOperation(activity, cards);

				if (!operation) {
					return { shouldUpdate: false };
				}

				const existingActivity =
					operation === 'update' ? lane.cards[activityIdx] : null;

				if (hasAnUnwatchedAssociatedRecord(activity, state)) {
					// If the Associated Record has not been watched AND we found an
					// existing matching Activity, it means the Associated Record has
					// changed, so we must unsubscribe to the previous Associated Record
					// while re-subscribing the new one
					if (existingActivity)
						pushAssociatedRecord(toBeRemoved, existingActivity);

					pushAssociatedRecord(toBeAdded, activity); //
				}

				if (hasAnUnwatchedExperienceStatusRecord(activity, state)) {
					if (existingActivity)
						pushExperienceStatusRecord(
							toBeRemoved,
							existingActivity
						);

					pushExperienceStatusRecord(toBeAdded, activity);
				}

				// Always first remove the Activity 's filter data from state to so we
				// can build a fresh state immediately afterwards. Sometimes an Activity's
				// fields change (i.e. Activity' state, associated record's assigned_to),
				// we want to remove those old values from the state
				const fo = removeFromFilter(
					{ ...nextPlaybook.filterData },
					{ ...nextPlaybook.selectedFilters },
					{ ...nextPlaybook.toBeCleanedSelectedFilterOption },
					existingActivity ? existingActivity.filterData : {}
				);

				const filterData = pushToFilter(
					{ playbook, nextPlaybook },
					activity,
					fo.filter,
					currentUserId
				);
				const selectedFilters = fo.selectedFilters;
				const toBeCleanedSelectedFilterOption =
					fo.toBeCleanedSelectedFilterOption;

				const { card: newCard } = processCard(
					nextPlaybook,
					activity,
					currentUserId
				);
				const nextCards = [...cards];
				// Update existing activity
				if (existingActivity) nextCards[activityIdx] = newCard;
				// insert new activity
				else nextCards.splice(activityIdx, 0, newCard);

				const nextStats = getUpdatedLaneStats(
					activity,
					existingActivity,
					lane.stats
				);
				// Update the next lane.
				// NOTE: we only set the lane's shouldExpand property if the activity
				// has that property set. If not set, this usually means it's a new activity
				// or only its associated record changed.
				lane.cards = nextCards;
				lane.stats = nextStats;
				lane.state = activity.laneContext.state.value;

				if (newCard.hasOwnProperty('shouldExpand'))
					lane.shouldExpand = newCard.shouldExpand && !newCard.hidden;

				nextPlaybook.filterData = filterData;

				if (nextPlaybook.selectedFilterCount) {
					nextPlaybook.toBeCleanedSelectedFilterOption = toBeCleanedSelectedFilterOption;
					nextPlaybook.selectedFilters = selectedFilters;
				}

				if (
					newCard.cardRendererTag &&
					!PLAYBOOK_RENDERERS.includes(newCard.cardRendererTag) &&
					!nextPlaybook.registeredRenderers.includes(
						newCard.cardRendererTag
					)
				) {
					nextPlaybook.registeredRenderers.push(
						newCard.cardRendererTag
					);
					newRenderers.push(newCard.cardRendererTag);
				}
			});

			if (newRenderers.length > 0) getComponentsByTagNames(newRenderers);

			dispatch(PLAYBOOK_RW_ADD_AND_REMOVE_WATCHING_RECORDS, {
				toBeAdded,
				toBeRemoved
			});

			return {
				path: 'playbooksByContextId',
				operation: 'assign',
				value: updatedPlaybooksByContextId
			};
		});
	}
};
