import { actionTypes } from '@servicenow/ui-core';
import {
	PLAYBOOK_ERROR,
	PLAYBOOK_FETCH_ALL_PLAYBOOK_CONTEXTS,
	PLAYBOOK_RW_ADD_AND_REMOVE_WATCHING_RECORDS,
	PLAYBOOK_SUBSCRIBE
} from '../../constants';

const {
	COMPONENT_BOOTSTRAPPED,
	COMPONENT_PROPERTY_CHANGED,
	COMPONENT_DISCONNECTED
} = actionTypes;

const contextReloadProperties = [
	'parentTable',
	'parentSysId',
	'playbookExperienceId'
];

const createLifecycleActionHandlers = (getAMBChannel) => ({
	[COMPONENT_BOOTSTRAPPED]: ({
		properties: { parentSysId, parentTable, playbookExperienceId },
		dispatch
	}) => {
		if (!parentSysId || !parentTable) {
			dispatch(PLAYBOOK_ERROR);
			return;
		}
		dispatch(PLAYBOOK_FETCH_ALL_PLAYBOOK_CONTEXTS, {
			parentTable,
			parentSysId,
			playbookExperienceId
		});
	},
	[COMPONENT_PROPERTY_CHANGED]: ({
		properties: { parentTable, parentSysId, playbookExperienceId },
		action,
		dispatch
	}) => {
		if (!parentSysId || !parentTable) {
			dispatch(PLAYBOOK_ERROR);
			return;
		}

		if (contextReloadProperties.includes(action.payload.name))
			dispatch(PLAYBOOK_FETCH_ALL_PLAYBOOK_CONTEXTS, {
				parentTable,
				parentSysId,
				playbookExperienceId
			});
	},
	[COMPONENT_DISCONNECTED]: ({ state, dispatch }) => {
		const {
			ambChannel,
			properties: { playbookContextIdsInOrder }
		} = state;
		for (let playbookContextId of playbookContextIdsInOrder) {
			const fullChannel = getAMBChannel(ambChannel, playbookContextId);
			dispatch(PLAYBOOK_SUBSCRIBE, {
				channelId: fullChannel,
				subscribe: false
			});
		}

		dispatch(PLAYBOOK_RW_ADD_AND_REMOVE_WATCHING_RECORDS, {
			unsubscribeAll: true
		});
	}
});

export default createLifecycleActionHandlers;
