const reduceSelectedFilters = (selectedFilters = {}) => {
	let selectedFilterCount = 0;
	const reducedSelectedFilter = {};

	Object.keys(selectedFilters).forEach((filter) => {
		const filterSection = selectedFilters[filter];
		const newFilter = [];

		Object.keys(filterSection).forEach((label) => {
			if (filterSection[label] !== true) return;

			selectedFilterCount++;
			newFilter.push(label);
		});

		if (newFilter.length)
			reducedSelectedFilter[filter] = newFilter.join('|');
	});

	return {
		reducedSelectedFilter,
		selectedFilterCount
	};
};

export default reduceSelectedFilters;
