import { isEmpty } from 'lodash';

const evaluateFilterOnCard = (reducedSelectedFilter = {}, card = {}) => {
	let match = null,
		shouldExpand = null,
		hidden = false;
	const cardFilterData = card.filterData;

	for (const [fieldName, filterValues] of Object.entries(
		reducedSelectedFilter
	)) {
		if (!filterValues) {
			shouldExpand = true;
			hidden = false;
			match = null;
			continue;
		}

		if (isEmpty(cardFilterData)) {
			hidden = true;
			match = null;
			break;
		}

		if (!cardFilterData[fieldName]) {
			hidden = true;
			match = null;
			break;
		}

		const exp = new RegExp(filterValues);
		match = cardFilterData[fieldName].match(exp);
		if (match == null) {
			hidden = true;
			break;
		}
	}

	if (match != null) {
		shouldExpand = true;
		hidden = false;
	}

	return {
		...card,
		...(shouldExpand !== null && { shouldExpand }),
		hidden,
		defer: !shouldExpand && !card.prioritized
	};
};

export default evaluateFilterOnCard;
